<template>
  <div class="FeeBillDetail">
    <componentHeader  headerText= '' :IfFanhuiShow='true'  @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class="date-c">{{ monthDate }}</div>
      <div class="customer-c">
        <p><span>{{ customerInfo.CUSTOMER_CODE }}</span></p>
        <p><span>{{ customerInfo.CUSTOMER_NAME }}</span></p>
        <p><span>{{ $store.state.businessInfo.businessCode }}</span></p>
        <p><span>{{ $store.state.businessInfo.name }}</span></p>
      </div>
      <div class="detail-list">
        <div><span>{{ fee.AppBusinessFee.INITIAL_BALANCE }}</span></div>
        <div class="help-fee" @click='PackUp("help-fee","v-f-help")'><span>{{ fee.AppBusinessFee.SUM_FEE }}</span><i class="iconfont icon-xiangxia"></i></div>
        <div class="list v-f-help" v-for='(item, index) in fee.partySumFeeList' :id="index" :key="'info1-' + index">
          <p>{{ item.FEE_NAME }}</p>
          <p>{{ item.FEE_DATE }}</p>
          <p>{{ item.FEE_AMOUNT }}</p>
        </div>
        <div class="tran-fee" @click='PackUp("tran-fee","v-f-tran")'><span>{{ fee.AppBusinessFee.COST_TRANSFER }}</span></span><i class="iconfont icon-xiangxia"></i></div>
        <div class="list v-f-tran" v-for='(item, index) in fee.partyCostTransferList' :id="index" :key="'info2-' + index">
          <p>{{ item.FEE_DATE }}</p>
          <p>{{ item.FEE_NAME }}</p>
          <p>{{ item.FEE_AMOUNT }}</p>
        </div>
        <div class="bank-fee" @click='PackUp("bank-fee","v-f-bank")'><span>{{ fee.AppBusinessFee.BANK_TRANSFER }}</span></span><i class="iconfont icon-xiangxia"></i></div>
        <div class="list v-f-bank" v-for='(item, index) in fee.partyBankTransferList' :id="index" :key="'info3-' + index">
          <p>{{ item.FEE_DATE }}</p>
          <p>{{ item.FEE_AMOUNT }}</p>
        </div>
        <div class="sale-fee" @click='PackUp("sale-fee","v-f-sale")'><span>{{ fee.AppBusinessFee.SUM_PRICE }}</span></span><i class="iconfont icon-xiangxia"></i></div>
        <div class="list v-f-sale" v-for='(item, index) in fee.partySumPriceList' :id="index" :key="'info4-' + index">
          <p>{{ item.FEE_NAME }}</p>
          <p>{{ item.FEE_DATE }}</p>
          <p>{{ item.FEE_AMOUNT }}</p>
        </div>
        <div><span>{{ fee.AppBusinessFee.BALANCE_TRANSFER }}</span></div>
        <div class="prompt">
          <div>
            <p>: </p>
            <p>110</p>
            <p>2</p>
            <p>3</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    name:"FeeBillDetail",
    data(){
      return{
        customerInfo:{}, //
        monthDate:"", //
        fee:{"AppBusinessFee":{"INITIAL_BALANCE":"0"}},//
      }
    },
    created(){
      var that = this;

      if(this.$route.query.customerInfo){
        this.customerInfo = this.$route.query.customerInfo
      }
      if(this.$route.query.monthDate){
        this.monthDate = this.$route.query.monthDate
      }
      
      var postData = {
        "businessId":that.$store.state.businessInfo.businessId,//id
        "customerId":that.customerInfo.CUSTOMER_ID,//id
        "monthDate":that.monthDate,//
      }

      this.httpRequest("getAppBusinessFeeList.do",postData,function(res){

        that.$store.state.FeeBillDetail_feeBill = res.data
        that.fee = res.data
      })
    },
    components:{
      componentHeader,
    },
    methods:{
      f_Fanhui(){
        this.$router.push({
          name:"FeeBill",
          query:{
            customerInfo:this.customerInfo,
          }
        })
      },
      //  
      PackUp(head_class,body_class){
        if($('.' + body_class).css("display") == "block"){
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(-90deg)"})
          $('.' + body_class).hide(200);
        }else{
          $("." + head_class + " i.icon-xiangxia").css({"transform":"translateY(-50%) rotate(0deg)"})
          $('.' + body_class).show(200);
        }
      },
    }
  }
</script>
<style lang="less" scoped>
  .FeeBillDetail{
    overflow: hidden;
    background-color: #eee;
    height: 100%;
    .container{
      overflow: hidden;
      margin-left: 15/50rem;
      margin-right: 15/50rem;
      height: 100%;
      .date-c{
        margin-top: 30/50rem;
        height: 70/50rem;
        line-height: 70/50rem;
        text-align: center;
        background-color: white;
      }
      .customer-c{
        margin-top: 30/50rem;
        background-color: white;
        p{
          padding: 12/50rem;
        }
      }
      .detail-list{
        margin-top: 30/50rem;
        overflow: scroll;
        height: calc(100% - 247px);
        &>div{
          background-color: white;
          margin-top: 3/50rem;
          line-height: 70/50rem;
          padding: 12/50rem;
          position: relative;
          i{
            position: absolute;
            right: 20/50rem;
            font-size: 40/50rem;
            top: 50%;
            transform: translateY(-50%) rotate(-90deg);
            transition:all 0.4s;
          }
        }
        .list{
          display: none;
          width: 100%;
        }
        .prompt{
          margin-top: 30/50rem;
          background-color: white;
          margin-bottom: 30/50rem;
          color: #686868;
          font-size: 26/50rem;
        }
      }
    }
  }
</style>


