<template>
  <div class="ChooseAddress">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='IfTextShow' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>

    <div class="container" id="container"></div>

    <div id="pickerBox">
      <input id="pickerInput" placeholder="" />
      <div id="poiInfo"></div>
    </div>

    <div class="titleView">
      <div class='title'></div>
      <div class="line">
        <div class='c'>:</div>
        <div id='lnglat' class='d'></div>
      </div>
      <div class="line">
        <div class='c'>:</div>
        <div id='address' class='d'></div>
      </div>
<!--       <div class="line">
        <div class='c'>:</div>
        <div id='nearestJunction' class='d'></div>
      </div>
      <div class="line">
        <div class='c'>:</div>
        <div id='nearestRoad' class='d'></div>
      </div>
      <div class="line">
        <div class='c'>POI:</div>
        <div id='nearestPOI' class='d'></div>
      </div> -->
    </div>

  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
        headerText:"", //
        TextContent:'', //
        IfTextShow:false,  //
        lng:0, //
        lat:0, //
        address:"", //
        whoPush:"",
      }
    },
    components:{
      componentHeader,
    },
    created(){
      // 
      if(this.$route.query.whoPush){
        this.whoPush = this.$route.query.whoPush;
      }
    },
    mounted(){

      var that = this

      var map = new AMap.Map('container', {
        resizeEnable: true,
      })

      // 
      AMap.plugin('AMap.Geolocation', function() {
        var geolocation = new AMap.Geolocation({
          enableHighAccuracy: true,//:true
          timeout: 10000,          //105s
          buttonPosition:'RB',    //
          buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
          zoomToAccuracy: true,   //
        })
        map.addControl(geolocation)
        geolocation.getCurrentPosition(function(status,result){
          if(status=='complete'){
            that.onComplete(result, map)
          }else{
            that.onError(result, map)
          }
        })
      })
    },
    methods:{
      // 
      f_Fanhui(){
        this.$router.go(-1);
      },
      f_Text(){

        this.$store.state.StoreInfo.longitude = this.lng;//
        this.$store.state.StoreInfo.latitude = this.lat;//
        this.$store.state.StoreInfo.lnglat = this.lng + "," + this.lat + "" + this.address + "";//
        if(this.whoPush == "CreateNewCustomer"){
          this.$router.push({
            name:'CreateNewCustomer',
          })
        }else if(this.whoPush == "CreateCustomerWeChat"){
          this.$router.push({
            name:'CreateCustomerWeChat',
          })
        }
      },
      onComplete(data, map){

        var that = this;
        console.log("", data)
        that.loadMap(map, data.position)
      },
      onError(data, map){

        var that = this
        console.log("", data)
        var p = {lng:114.039706, lat:22.622864}
        that.loadMap(map, p)
      },
      loadMap(map, p){

        console.log(p)
        var that = this

        AMapUI.loadUI(['misc/PositionPicker', 'misc/PoiPicker'], function(PositionPicker, PoiPicker) {

          var positionPicker = new PositionPicker({
            mode: 'dragMap',
            map: map,
          })
          positionPicker.on('success', function(positionResult) {
            document.getElementById('lnglat').innerHTML = positionResult.position;
            document.getElementById('address').innerHTML = positionResult.address;
            // document.getElementById('nearestJunction').innerHTML = positionResult.nearestJunction;
            // document.getElementById('nearestRoad').innerHTML = positionResult.nearestRoad;
            // document.getElementById('nearestPOI').innerHTML = positionResult.nearestPOI;
            that.lng = positionResult.position.lng
            that.lat = positionResult.position.lat
            that.address = positionResult.address
            that.IfTextShow = true
          })
          positionPicker.on('fail', function(positionResult) {
            document.getElementById('lnglat').innerHTML = ' '
            document.getElementById('address').innerHTML = ' '
            // document.getElementById('nearestJunction').innerHTML = ' '
            // document.getElementById('nearestRoad').innerHTML = ' '
            // document.getElementById('nearestPOI').innerHTML = ' '
            that.lng = 0
            that.lat = 0
            that.address = ""
            that.IfTextShow = false
          })
          positionPicker.start([p.lng, p.lat])

          var poiPicker = new PoiPicker({
            //city:'',
            input: 'pickerInput'
          })
          //poiPicker
          poiPickerReady(poiPicker)
        })

        function poiPickerReady(poiPicker) {

          window.poiPicker = poiPicker;

          var marker = new AMap.Marker();

          var infoWindow = new AMap.InfoWindow({
            offset: new AMap.Pixel(0, -20)
          });

          //POI
          poiPicker.on('poiPicked', function(poiResult) {

            var source = poiResult.source,
              poi = poiResult.item,
              info = {
                source: source,
                id: poi.id,
                name: poi.name,
                location: poi.location.toString(),
                address: poi.address
            };
            marker.setMap(map);
            infoWindow.setMap(map);
            marker.setPosition(poi.location);
            infoWindow.setPosition(poi.location);
            infoWindow.open(map, marker.getPosition());
            map.setCenter(marker.getPosition());
          });

          // poiPicker.onCityReady(function() {
          //   poiPicker.suggest('');
          // });
        }
      },
      poiPickerReady(poiPicker){
        console.log(432)
      },
    }
  }
</script>
<style lang="less" scoped>
  .ChooseAddress{
    width: 100%; 
    height: 100%;
    .container{
      width: 100%; 
      height: 60%;
    }
    .titleView{
      font-size: 27/50rem;
    }
    .title{
      width: 100%;
      background-color: #dadada
    }
    #pickerBox {
      position: absolute;
      z-index: 9999;
      top: 50px;
      right: 30px;
      width: 300px;
    }
    
    #pickerInput {
      width: 200px;
      padding: 5px 5px;
    }
    
    #poiInfo {
      background: #fff;
    }
    .line{
        height: 50/50rem;
        margin-top: 12/50rem;
      .c{
        font-weight: 600;
        margin-left: 15/50rem;
        float: left;
      }
      .d{
        margin-left: 8/50rem;
        float: left;
      }
    }
  }

</style>


