<template>
  <div class="QuerySalesOrder">
    <componentHeader v-if='$store.state.G_AppOrWX != "WX" && !$store.state.CurrentRoleData.QueryPurchaseOrder' headerText=""></componentHeader>
    <componentHeader v-if='$store.state.G_AppOrWX == "WX"' headerText="" :IfFanhuiShow='false'  @f_Fanhui='f_Fanhui'></componentHeader>
	  <header class="SalesOrderHeader" v-if='$store.state.G_AppOrWX != "WX" && $store.state.CurrentRoleData.QueryPurchaseOrder'>
      <div class="activeBtn"></div>
      <div @click="SelectPurchase"></div>
    </header>
	  <div class="container haveHeadcontainer">
      <div class="orderState">
        <div class="all" @click='choseAll'></div>
        <div class="toPay" @click='choseToPay'></div>
        <div class="toGet" @click='choseToGet'></div>
        <div class="finish" @click='choseFinish'></div>
        <div class="cancel" @click='choseCancel'></div>
      </div>
      <div class="fixedHead">
        <div class="CustomerSearch">
          <div>
            <input v-model='searchValue' placeholder=" // ">
            <i class="iconfont icon-sousuo"></i>
            <span v-show='!MergeCancelBtnShow && $store.state.CurrentRole == "osp"' class="MergeChoose" @click='MergeChoose'></span>
            <span v-show='MergeCancelBtnShow' class="MergeCancel" @click='MergeCancel'></span>
          </div>
        </div>
        <div class="selectCondition">
          <div @click='selectBoxShow("")'>
            <span></span>
            <i class="iconfont icon-xiangyou"></i>
            <span>{{SupplierName}}</span>
          </div>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <componentOutboundOrder :IFQuerySalesOrder='true' :MergeChooseBtnShow='MergeCancelBtnShow' :OutboundOrderList='SaleArr' @push_stack_click="push_stack_click"></componentOutboundOrder>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span></span>
      </div>
	  </div>
    <div class="MergeOrder" v-show='MergeCancelBtnShow' @click='MergeOrder'></div>
    <componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
    <componentFooter v-if='$store.state.G_AppOrWX != "WX"'></componentFooter>
  </div>
</template>
<script type="text/javascript">
  import componentFooter from '@/components/componentFooter'
  import componentHeader from '@/components/componentHeader'
  import componentOutboundOrder from '@/components/Widgets/componentOutboundOrder'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import $ from 'jquery'
  export default{
    data(){
      return{
        SaleArr:[],//
        noDataShow:false,//
        SupplierName:'',//
        suppliersId:'',//id
        supplierList:[],//
        searchValue:"",//
        SelectBoxFlag:false,// 
        numberPerPage:15,//
        loadPage: 1 ,//
        allLoaded:true,//
        autoFill:false,//
        MergeCancelBtnShow:false,// 
        orderState:'',//
        push_stack:false,//
      }
    },
    components:{
	    componentFooter,
      componentHeader,
      componentOutboundOrder,
      componentSelectBox
	  },
    watch:{

      // input
      searchValue(val, oldVal){

        this.loadPage = 1;
        this.allLoaded = false;
        this.reqOrderListInfo();
        this.$store.state.QuerySalesOrder_searchValue = val
      }
    },
    destroyed: function () {
      if(this.push_stack == false){
        this.$store.state.QuerySalesOrder_searchValue = ""
      }
    },
    created(){
      if(this.$route.query.orderState){ 
        this.orderState = this.$route.query.orderState;
      }
     
      var that = this;
      var SupplierData = {
        "appUsersId":that.$store.state.userInfo.user_id,//id
        "businessId":that.$store.state.businessInfo.businessId,//id
      }

      if(that.$store.state.G_AppOrWX == 'WX'){
        var InterfaceName = 'wxSearchCustomerList.do';
         
        this.$nextTick(() => {
          $('.fixedHead').hide()
          $('.ListBox').css('padding-top','100px')
          if(this.orderState == ""){
            this.toAll();
          }else if(this.orderState == "NEW"){
            this.toPay();
          }else if(this.orderState == "PAID"){
            this.toGet();
          }else if(this.orderState == "FINISH"){
            this.toFinish();
          }else if(this.orderState == "CANCEL"){
            this.toCancel();
          }
        })
      }else{
        var InterfaceName = 'searchCustomerList.do'; 
        this.$nextTick(() => {
          $('.orderState').hide()
        })
      }
      // 
      this.httpRequest(InterfaceName,SupplierData,function(res){

        if(res.data.length){
          
          for(var k=0;k<res.data.length;k++){
            res.data[k].name = res.data[k].CUSTOMER_NAME;
          }
          var idx = that.$store.state.QuerySalesOrder_supplierListIdx;
          that.supplierList = res.data;
          that.SupplierName = res.data[idx].CUSTOMER_NAME;//
          that.suppliersId = res.data[idx].CUSTOMER_ID;//id
          if(that.$store.state.QuerySalesOrder_searchValue){
            // 
            that.searchValue = that.$store.state.QuerySalesOrder_searchValue
          }else{
            that.reqOrderListInfo();
          }

        }else{
          that.$alert('', '', {
            confirmButtonText: '',
          })
        }
      })
    },
    methods:{
      f_Fanhui(){
        this.$router.push({
          name:"Index"
        })
      },
      // pushvuex
      push_stack_click(val){
        this.push_stack = val
      },
      // 
      SelectPurchase(){
        this.$router.push({
          name:"QueryPurchaseOrder"
        })
      },

      //
      choseAll(){
        this.toAll();
        this.orderState = "";
        this.reqOrderListInfo();
      }, 
      //
      choseToPay(){
        this.toPay();
        this.orderState = "NEW";
        this.reqOrderListInfo();
      },
      //
      choseToGet(){
        this.toGet();
        this.orderState = "PAID";
        this.reqOrderListInfo();
      },
      //
      choseFinish(){
        this.toFinish();
        this.orderState = "FINISH";
        this.reqOrderListInfo();
      },
      //
      choseCancel(){
        this.toCancel();
        this.orderState = "CANCEL";
        this.reqOrderListInfo();
      },
       //
      toAll(){
        $(".all").css({'border-bottom':'2px solid #1e8cf8','color':'#1e8cf8','font-size':'17px'})
        $(".toPay").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toGet").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".finish").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".cancel").css({'border-bottom':'none','font-size':'14px','color':'#000'})
      }, 
      //
      toPay(){
        $(".all").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toPay").css({'border-bottom':'2px solid#1e8cf8','color':'#1e8cf8','font-size':'17px'})
        $(".toGet").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".finish").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".cancel").css({'border-bottom':'none','font-size':'14px','color':'#000'})
      },
      //
       toGet(){
        console.log(222)
        $(".all").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toPay").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toGet").css({'border-bottom':'2px solid#1e8cf8','color':'#1e8cf8','font-size':'17px'})
        $(".finish").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".cancel").css({'border-bottom':'none','font-size':'14px','color':'#000'})
      },
      //
       toFinish(){
        $(".all").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toGet").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toPay").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".finish").css({'border-bottom':'2px solid#1e8cf8','color':'#1e8cf8','font-size':'17px'})
        $(".cancel").css({'border-bottom':'none','font-size':'14px','color':'#000'})
      },
      //
       toCancel(){
        $(".all").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toGet").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".toPay").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".finish").css({'border-bottom':'none','font-size':'14px','color':'#000'})
        $(".cancel").css({'border-bottom':'2px solid#1e8cf8','color':'#1e8cf8','font-size':'17px'})
      },


      // 
      reqOrderListInfo(load){
        // if(this.$route.query.orderState){ 
        //   this.orderState = this.$route.query.orderState;
        // }
        var that = this;
        var transferType = that.$store.state.G_AppOrWX == "WX"?'STORE_PURCHASE':"SALE_OUT";
        var postData = {
          "storeName":that.searchValue,//
          "appUsersId":that.$store.state.userInfo.user_id,//id
          "businessId":that.$store.state.businessInfo.businessId,//id
          "suppliersId":that.suppliersId,//ospID
          'transferType':transferType,// 
          "currentPage":that.loadPage,//
          "numberPerPage":that.numberPerPage,//
          "userType":that.$store.state.G_AppOrWX,//app
          "orderState":that.orderState,//
        }
        this.httpRequest( "searchTransferOut.do",postData,function(SupRes){

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          if(that.$store.state.G_AppOrWX == 'WX'){
            for(var i =0;i<SupRes.data.length;i++){

              if(SupRes.data[i].STATUS == 'NEW'){

                SupRes.data[i].STATUS_color = 'red';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'PAID'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'FINISH' && SupRes.data[i].HAVE_RETURN != 'T'){

                SupRes.data[i].STATUS_color = 'green';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'CANCEL'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].HAVE_RETURN == 'T'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';
              }else{
                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = SupRes.data[i].STATUS;
              }
            }
          }
          else{
            for(var i =0;i<SupRes.data.length;i++){

              if(SupRes.data[i].STATUS == 'NEW'){

                SupRes.data[i].STATUS_color = 'red';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'PAID'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'FINISH' && SupRes.data[i].HAVE_RETURN != 'T'){

                SupRes.data[i].STATUS_color = 'green';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].STATUS == 'CANCEL'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';

              }else if(SupRes.data[i].HAVE_RETURN == 'T'){

                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = '';
              }else{
                SupRes.data[i].STATUS_color = '#777';
                SupRes.data[i].STATUS_text = SupRes.data[i].STATUS;
              }
            }
          }

          // 
          if (that.loadPage > 1) {
            if(SupRes.data.length){
              that.SaleArr = that.SaleArr.concat(SupRes.data);
            }
          } else {
            if(SupRes.data.length){

              that.SaleArr = SupRes.data;
            }else{
              that.SaleArr = [];
            }
          }

          // 
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.SaleArr.length){
            that.noDataShow = false;

          }else{
            // 
            that.noDataShow = true;
          }
        })
      },
      //  
      selectBoxShow(text){
        this.BouncedName = text;
        this.SelectBoxFlag=true;

        this.SelectBoxList = this.supplierList;
      },
      // 
      selectedItem(index){

        var that = this;

        this.SupplierName = this.supplierList[index].name;
        this.suppliersId = this.supplierList[index].CUSTOMER_ID;
        this.$store.state.QuerySalesOrder_supplierListIdx = index;

        this.SelectBoxFlag = false;
        // 
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqOrderListInfo();
      },
      //  
      selectCancel(){
        this.SelectBoxFlag = false;
      },
      // 
      MergeChoose(){

        this.MergeCancelBtnShow = true;
      },
      //  
      MergeOrder(){
        var that = this;
        var SumMergeOrderList = [];

        for(var i = 0 ; i < that.SaleArr.length;i++){

          if($(".putWarehouseList").eq(i).children(".iconfont").hasClass("icon-1")){

            SumMergeOrderList[SumMergeOrderList.length] = that.SaleArr[i].TRANSFER_OUT_ID;
          }
        }

        if(!SumMergeOrderList.length){

          that.$alert("", '', {
            confirmButtonText: ''
          })

        }else{

          that.$store.state.SumMergeOrderList = SumMergeOrderList;//

          console.log(that.$store.state.SumMergeOrderList)

          that.$router.push({
            name:"ChooseGoods",
            query:{
              ReceiptInfo:that.supplierList[that.$store.state.QuerySalesOrder_supplierListIdx],
              whoPush:"PurchaseOrder",//
              orderType:'PURCHASE_IN',
              CombinedOrder:'CombinedOrder',//
            }
          })
        }
      },
      // 
      MergeCancel(){
        this.MergeCancelBtnShow = false;
      },
      // 
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqOrderListInfo("TopLoad");
      },
      // 
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqOrderListInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .QuerySalesOrder{
    overflow:hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .orderState{
        background-color:#fff;
        width: 100%;
        z-index: 50;
        position:fixed;
        top: 89/50rem;
        left: 0;
        text-align: center;
        // border-bottom: 3/50rem solid #999;
        div{
          display: block;
          width: 20%;
          line-height: 90/50rem;
          float: left;
        }
         // .all{
         //    border-bottom: 4/50rem solid @PublicColor;
         //    color: @PublicColor;
         //    font-size: 32/50rem;
         //  }

      }
      .fixedHead{
        background-color:#fff;
        z-index: 50;
        width: 100%;
        position:fixed;
        top: 89/50rem;
        left: 0;
        .CustomerSearch{
          .TopSearchBox;
            &>div{
              height: 60/50rem;
              display: flex;
              input{
                flex-grow: 1;
              }
              span{
                min-width: 120/50rem;
                flex-shrink: 0;
                margin-left: 20/50rem;
                line-height: 60/50rem;
                color:#fff;
                font-size: 28/50rem;
                background-color:@PublicColor;
                text-align: center;
                border-radius: 20/50rem;
                box-sizing:border-box;
                border:1/50rem solid @PublicColor;
              }
              span.MergeChoose{
                color:@PublicColor;
                background-color:#fff;
              }
              @media screen and (max-width: 374px) {
                span{
                  border:3/50rem solid @PublicColor;
                }
              }
            }
        }
        .selectCondition{
          div>span:nth-child(3){
            text-align: right;
            width: 430/50rem;
            overflow: hidden;
            text-overflow: ellipsis;/**/
            white-space: nowrap;/**/
          }
        }
      }
      .ListBox{
        height: 100%;
        overflow: scroll;
        padding-top: 275/50rem;
        box-sizing: border-box;
      }
      .kong{
        height: 200/50rem;
      }
    }
    .MergeOrder{
      position:fixed;
      bottom:140/50rem;
      left: 50%;
      transform: translate(-50%);
      width: 280/50rem;
      text-align: center;
      line-height: 70/50rem;
      height: 70/50rem;
      font-size: 28/50rem;
      border-radius:35/50rem;
      color: #fff;
      background-color:@PublicColor;
    }
  }
</style>


