<template>
  <div class='LogisticsInfoDetail'>
  	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
  	<div class="container haveHeadcontainer">
  	  <div class='orderInfo'>
        <div class='infoHead'></div>
        <div class="infoContainer">
          <div><span>{{data.OMS_NO}}</span></div>
          <div><span>{{data.SHIPMENT_NO}}</span></div>
          <div><span>{{data.SHIP_TIME}}</span></div>
          <div><span>{{data.ACTUAL_DEPT_TIME}}</span></div>
          <div><span>{{data.trans_party_name}}</span></div>
          <div><span>{{data.vehicle_Code}}</span></div>
          <div><span>{{data.driver_Name}}</span>&nbsp;&nbsp;&nbsp;<span>{{data.mobile_Number}}&nbsp;&nbsp;&nbsp;<i v-if='data.mobile_Number' @click="callPhone(data.mobile_Number)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div  class='bottom'><div><span>{{data.ORDER_QTY}} </span></div><div><span>{{data.NODE_STATUS}}</span></div></div>
          <div  class='bottom'><div><span>{{data.ORDER_WT}} </span></div><div><span>{{data.DRIVER_STATUS}}</span></div></div>
          <div  class='bottom'><div><span>{{data.ORDER_VOL}} m</span></div><div><span>{{ }}</span></div></div>
        </div>
      </div>
      <div class='orderInfo LogisticsGoodsInfo'>
        <div class='infoHead'></div>
        <div>
          <div class='infoContainer' v-for='(item,idx) in data.PRODUCT'  :id="idx"  :key='idx'>
  		    <div class="PRODUCT_NAME">{{item.PRODUCT_NAME}}</div>
  		    <div><span>{{item.PRODUCT_NO}}</span></div>
          <div><span>{{item.ISSUE_QTY}} {{item.SECONDARY_UNIT_STR}}</span></div>
          </div>  
        </div>
      </div>
  	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
  	data(){
  	  return{
  	  	headerText:'',
        data:{},
  	  	ORD_NO: '', //
        TMS_SHIPMENT_NO:'', //
        TMS_DATE_LOAD: '',//
        TMS_DATE_ISSUE: '', // 
        TMS_FLEET_NAME: '', //
        TMS_DRIVER_NAME: '',// 
        TMS_DRIVER_TEL:'',// 
        ORD_ISSUE_QTY: 0,// 
        ORD_ISSUE_WEIGHT: 0,// 
        ORD_ISSUE_VOLUME: 0,// 
        TMS_PLATE_NUMBER: '',// 
        ORD_WORKFLOW: '',// 
        ORD_STATE: '',// 
        DRIVER_PAY:'',// 
        OrderDetails: [], //
  	  }
  	},
  	components:{
	  componentHeader,
	},
  	created(){

      var that = this;

      if(this.$route.query.orderId){
        this.orderId = this.$route.query.orderId;
      }

      var postData = {
        "orderId":this.orderId,
      }

      this.httpRequest("GetTMSOrderInfo.do",postData,function(res){

        that.data = res.data;
      })
  	},
  	methods:{
  	  //
  	  f_Fanhui(){
  	  	this.$router.go(-1);
  	  },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .LogisticsInfoDetail{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    .LogisticsGoodsInfo{
      div:not(.infoHead){
        color: #666;
        .PRODUCT_NAME{
          color: #000;
          font-weight: 900;
        }
      }
    }
    .bottom{
      &>div:first-child{
        float: left;
        width: 50%;
      }
      &>div:nth-child(2){
        float: right;
        width: 50%;
      }
    }
  }
</style>


