<template>
  <div class="CreateNewCustomer">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="SupplierInfo">
        <div></div>
        <div>
          <span></span>
          <span>{{SupplierName}}</span>
        </div>
      </div>
      <div class="CustomerInfo">
        <div></div>
        <div>
          <span></span>
          <input placeholder="" v-model='CustomerName'></input>
        </div>
        <!-- <div>
          <span></span>
          <span>{{CustomerCode}}</span>
        </div> -->
        <div class="channel" @click='selectBoxShow()'>
          <span></span>
          <span>{{Channel}}</span>
          <i class="iconfont icon-xiangyou"></i>
        </div>
        <div class="VisitingRoutes" @click='selectBoxShowRoutes()'>
          <span></span>
          <span>{{VisitRouteName}}</span>
          <i class="iconfont icon-xiangyou"></i>
        </div>
        <div class="CustomerRemark">
          <span></span>
          <input v-model='CustomerRemark'></input>
        </div>
      </div>
      <div class="AddressInfo">
        <div></div>
        <div>
           <span></span>
           <input placeholder="" v-model='Consignee'></input>
        </div>
        <div>
           <span></span>
          <input placeholder="" v-model='ContactTel'></input>
        </div>
        <div class="lianxiren">
          <div>
            <i class="iconfont icon-lianxiren" @click='toChooseContact'></i>
            <span></span>
          </div>
        </div>
        <div class="ChooseAddress" @click='toChooseAddress' >
          <span></span>
          <span>{{Location}}</span>
          <span><i class="iconfont icon-xiangyou"></i></span>
        </div>
        <div>
           <span></span>
           <input placeholder="" v-model='AddressDetail'></input>
        </div>
        <div  class="lnglat"  @click='toLnglat'>
          <span></span>
          <span>{{ lnglat }}</span>
          <span><i class="iconfont icon-xiangyou"></i></span>
        </div>
      </div>

      <div v-if="priceStrategys.length > 0" class="StrategyInfo">
        <div></div>
        <div  class="priceStrategys"  v-for='(dataItem,index) in priceStrategys'  :id="index"  :key='index' @click='price_strategy_click(index)'>
         <i class="iconfont icon-weixuanzhong"></i><span>&nbsp;&nbsp;{{dataItem.POLICY_NAME}}</span>
        </div>
      </div>

      <div class="uploadImg">           
        <div class="LoadImg_head"></div>
        <div class="Img">
          <el-upload  :action= "HOST + 'uploadStorePicture.do'"  :limit="1" :multiple="true" :data="uploadParam"    ref='upload_ref' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"      
          :on-error= "uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>

      <div class="StrategyInfo">
        <div></div>
        <div  class="StrategyList"  v-for='(dataItem,index) in StrategyList'  :id="index"  :key='index' @click='fds(index)'>
         <i class="iconfont icon-1"></i><span>&nbsp;&nbsp;{{dataItem.STRATEGY_NAME}}</span>
        </div>
      </div>
      

      <div class="BtnComponent bottomBtn">
        <div @click='CustomerSave'></div>
      </div>
      <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>
    <componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="cruuentSeleText" @selectCancel="selectCancel"></componentSelectBox>
    <div class="SelectMeng" v-if='RoutLineFlag'>
      <div class="SelectWarehouses SelectMengContent">
        <div class="SelectTitle">{{cruuentSeleText}}</div>
        <div class="SelectItem">
          <div class="RoutLineInfo"  v-for='(dataItem,idx) in VisitLineList'  :id="idx"  :key='idx'>
            <span>{{dataItem.name}}</span>
            <input  class="border_w" type="checkbox" :value="idx" :checked="dataItem.checked"></input>
          </div>
        </div>
        <div class="selectBtn">
          <div @click="RoutLineConfirm"></div>
          <div @click="RoutLineCancel"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
    data(){
      return{
      	headerText:"",//
        cruuentSeleText:"",//
        SelectBoxList:[],//
        SelectBoxFlag:false,//
        RoutLineFlag:false,//
        supplierList:[],//
        SupplierName:'',//
        suppliersId:'',//id
        CustomerName:'',//
        // CustomerCode:'',//
        ChannelList:[],//
        Channel:'',//
        VisitLineList:[],//
        VisitRouteName:'',//
        VisitRouteNameInsert:'',//
        VisitRouteId:"",//id
        CustomerRemark:'',//
        Consignee:'',//
        ContactTel:'',//
        Location:'',//
        AddressDetail:'',//
        longitude:'',//
        latitude:'',//
        lnglat:'',//
        province:'',//
        cityChinese:'',//
        districict:'',//
        street:'',//
        ifTips:false,//
        tips_Msg:"",//
        selectedLine:[],
        selectedValue:[],
        StrategyList:[],//
        SumStrategyList:[],//
        suppliersId:[],
        priceStrategys:[],//
        uploadParam:{},// 
        uploadFailNum:0,//
        uploadSuccessNum:0,//
        uploadTotalNum:0,//
        dialogImageUrl: '',//
        dialogVisible: false,//
        storeId:'',//ID
      } 
    },
    components:{
	    componentHeader,
      componentSelectBox
	  },
    mounted(){
      window.SetContactPeople = this.SetContactPeople;
      window.SetSendLocation = this.SetSendLocation;
    },
    created(){

      var that = this;
      that.SupplierName = that.$store.state.CustomerInfoOspInfo.name; //
      that.CustomerName = that.$store.state.StoreInfo.CustomerName;//
      if(that.$store.state.StoreInfo.ChannelList){
        that.Channel = that.$store.state.StoreInfo.ChannelList.name;//
      }
      that.VisitRouteId = that.$store.state.StoreInfo.VisitRouteId;//id
      that.VisitRouteName = that.$store.state.StoreInfo.VisitRouteName;//
      that.CustomerRemark = that.$store.state.StoreInfo.CustomerRemark;//
      that.Consignee = that.$store.state.StoreInfo.Consignee;//
      that.ContactTel = that.$store.state.StoreInfo.ContactTel;//
      that.Location = that.$store.state.StoreInfo.Location;//
      that.AddressDetail = that.$store.state.StoreInfo.AddressDetail;//
      that.longitude = that.$store.state.StoreInfo.longitude;//
      that.latitude = that.$store.state.StoreInfo.latitude;//
      that.lnglat = that.$store.state.StoreInfo.lnglat;//

      // var CodeData = {
      //   "businessId":that.$store.state.businessInfo.businessId,//id
      //   "businessCode":that.$store.state.businessInfo.businessCode,//
      // }
      // // 
      // that.httpRequest("GetStoresCode.do",CodeData,function(CodeRes){
         
      //   if(CodeRes.data.length){
      //     that.CustomerCode = CodeRes.data;
      //     that.$store.state.StoreInfo.CustomerCode = that.CustomerCode;
      //   }else{
      //     that.$alert('', '', {
      //       confirmButtonText: '',
      //     })
      //   }
      // })
      if(that.$store.state.userInfo.business == 5){

        that.suppliersId = that.$store.state.CustomerInfoOspInfo.suppliersId
      }else{

        that.suppliersId = that.$store.state.CustomerInfoOspInfo.CUSTOMER_ID
      }

      var LineData = {
        "suppliersId":that.suppliersId,//id
      }
      // 
      that.httpRequest("GetVisitLine.do",LineData,function(LineRes){
         
       if(LineRes.data.length){
        that.VisitLineList = LineRes.data;
        for(var i = 0; i < that.VisitLineList.length; i++){
          if(LineRes.data[i].name == ""){
            that.VisitLineList.splice(i, 1)
          }
        }

       }else{
        that.$alert('', '', {
          confirmButtonText: '',
        })
       }
      })

      // 
      that.httpRequest("findChannel.do", "",function(ChannelRes){
         
       if(ChannelRes.data.length){
        that.ChannelList = ChannelRes.data;
       }else{
        that.$alert('', '', {
          confirmButtonText: '',
        })
       }
      })

       var StrategyData = {
        "suppliersId":that.suppliersId,//id
      }
      // 
      that.httpRequest("GetStrategyByOSP.do",StrategyData,function(StrategyRes){
         
        if(StrategyRes.data.length){
          that.StrategyList = StrategyRes.data;
        }
      })
      that.getVisitLineInfo();

      var postData = {
        "businessId":that.$store.state.businessInfo.businessId,//id
      }
      // 
      that.httpRequest("GetStrategyPriceByCustomer.do",postData,function(res){
         
        if(res.data.length){
          that.priceStrategys = res.data
        }
      })
    },
    methods:{
    	// 
    	f_Fanhui(){
       this.$store.state.StoreInfo = {};
         this.$router.push({
          name:"CustomerList",
        })
      },
      price_strategy_click(index){

        let iconElement = $('.priceStrategys:eq('+index+')>i.iconfont')

        var selected = false
        if(iconElement.hasClass("icon-1")){
         selected = true
        }
        
        // 
        for(var i = 0 ; i < this.priceStrategys.length;i++){
          if($(".priceStrategys").eq(i).children(".iconfont").hasClass("icon-1")){
            var item = $('.priceStrategys:eq(' + i + ')>i.iconfont')
            item.removeClass("icon-1").addClass("icon-weixuanzhong")
          }
        }
        // 
        if(selected){
          return
        }
        // 
        if(iconElement.hasClass("icon-weixuanzhong")){
          iconElement.removeClass("icon-weixuanzhong").addClass("icon-1")
        }else if(iconElement.hasClass("icon-1")){
          iconElement.removeClass("icon-1").addClass("icon-weixuanzhong")
        }
        return;
      },
      fds(index){
        let iconElement = $('.StrategyList:eq('+index+')>i.iconfont');

        if(iconElement.hasClass("icon-1")){

          iconElement.removeClass("icon-1").addClass("icon-weixuanzhong")

        }else if(iconElement.hasClass("icon-weixuanzhong")){

          iconElement.removeClass("icon-weixuanzhong").addClass("icon-1")
        }
        return;

      },
      toLnglat(){
        this.saveInfo();
        if(this.$store.state.Device == "iOS"){
          // 
          try { CallAndroidOrIOS("", ""); }
          catch(error) { }
        }else{
          this.$router.push({
            name:"SendLnglat",
            query:{
              whoPush:"CreateNewCustomer",
            }
          })
        }
      },
       //push
      saveInfo(){
        var that = this;
        that.$store.state.StoreInfo.CustomerName = that.CustomerName;//
        that.$store.state.StoreInfo.Consignee = that.Consignee;//
        that.$store.state.StoreInfo.ContactTel = that.ContactTel;//
        that.$store.state.StoreInfo.CustomerRemark = that.CustomerRemark;//
        that.$store.state.StoreInfo.Location = that.Location;//
        that.$store.state.StoreInfo.AddressDetail = that.AddressDetail;//
        that.$store.state.StoreInfo.longitude = that.longitude;//
        that.$store.state.StoreInfo.latitude = that.latitude;//
        that.$store.state.StoreInfo.lnglat = that.lnglat;//
      },
      SetSendLocation(address, lng, lat){
        this.lnglat = address
        this.longitude = lng
        this.latitude = lat
      },
      SetContactPeople(name, tel){
        this.Consignee = name
        this.ContactTel = tel
      },
      toChooseContact(){
        // 
        try { CallAndroidOrIOS.callAndroid("", ""); } 
        catch(error) { }
        // 
        try { CallAndroidOrIOS("", ""); }
        catch(error) { }
      },
      // 
      sortNumbers(array){
        return array.sort(
          function LM(a, b){
            return a - b;
        });
      },
      // key
       sortByKeyDesc(array, key){

        return array.sort(function(a,b){
            var x = a[key];
            var y = b[key];
            return((x<y)?-1:((x>y)?1:0));
        })
      },
      //  
      selectBoxShow(){
        this.SelectBoxFlag=true;
        this.SelectBoxList = this.ChannelList;
        this.cruuentSeleText = '';
      },
      // 
      selectBoxShowRoutes(){

        var that = this;
        this.selectedLine = this.$store.state.StoreInfo.VisitLineList;
        for(var m=0;m<that.VisitLineList.length;m++){
            that.VisitLineList[m].checked = '';//checked
        }
        if(that.VisitRouteName){
          for(var j = 0; j < that.selectedLine.length; j++){
           for(var k = 0; k < that.VisitLineList.length; k++){

            if(that.selectedLine[j].visitId == that.VisitLineList[k].visitId){

              that.VisitLineList[k].checked = 'checked';
            }
           }
          }
        }else{
          //
          that.Date= new Date();
          var m = that.Date.getDay();
          var data;
          that.data = "" + "".charAt(m);
          for(var i = 0; i < that.VisitLineList.length; i++){
            if(that.data == that.VisitLineList[i].name){
              that.VisitLineList[i].checked = 'checked';
            }
          }
        }
        that.RoutLineFlag=true;
        that.SelectBoxList = that.VisitLineList;
        that.cruuentSeleText = '';
      },
      // 
      selectedItem(index){
        var that = this;
        that.Channel = that.SelectBoxList[index].name;
        that.$store.state.StoreInfo.ChannelList = that.ChannelList[index];
        this.SelectBoxFlag = false;
      },
      //id 
      getVisitLineInfo(){
        var that = this;
        that.VisitRouteName = "";
        that.VisitRouteNameInsert = "";
        that.VisitRouteId = "";
        if(that.$store.state.StoreInfo.VisitLineList){
          that.selectedLine = that.$store.state.StoreInfo.VisitLineList;
        }
        for (var i = 0; i < that.selectedLine.length; i++) {
          that.VisitRouteName += that.selectedLine[i].name.substring(3,2)+" ";
          that.VisitRouteNameInsert += that.selectedLine[i].name+",";
          that.VisitRouteId += that.selectedLine[i].visitId+",";
        }
        //
        that.VisitRouteName = that.VisitRouteName.substring(0, that.VisitRouteName.lastIndexOf(' '));
        that.VisitRouteNameInsert = that.VisitRouteNameInsert.substring(0, that.VisitRouteNameInsert.lastIndexOf(','));
        that.VisitRouteId = that.VisitRouteId.substring(0, that.VisitRouteId.lastIndexOf(','));
      },
      //  
      RoutLineConfirm(){
        var that = this;
        var checkedLine = [];
        for(var i =0;i<that.VisitLineList.length;i++){

          if($('.RoutLineInfo:eq('+i+') input.border_w').prop('checked')){
            checkedLine[checkedLine.length] = that.VisitLineList[i];
          }
        }
        that.$store.state.StoreInfo.VisitLineList = checkedLine;
        this.RoutLineFlag = false;
        this.getVisitLineInfo();
      },
      // 
      selectCancel(){
        this.SelectBoxFlag = false;
      },
      // 
      RoutLineCancel(){
        this.selectedLine = [];
        this.RoutLineFlag = false;
      },
      //   
      toChooseAddress(){
        this.saveInfo();
        this.$router.push({
          name:"ChooseAddress",
        })
      },
       // 
      uploadSuccess  (response, file, fileList){

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 
          console.log(response)
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 
          that.$alert("", '', {
            confirmButtonText: ''
          })
        }
      },
      // 
      uploadError  (err, file, fileList){
        var that = this;

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 

          that.uploadSuccessNum = 0;
          that.uploadFailNum = 0;
          that.uploadTotalNum = 0;
          that.$emit('isLoading', false);

          that.$alert("", '', {
            confirmButtonText: ''
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 
          that.$alert("", '', {
            confirmButtonText: ''
          })
        }
      },
      // 
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 
      CustomerSave:function(){
        var that = this;  
        var strategyIds = '';
        var policyPriceId = ""

        for(var i = 0 ; i < that.priceStrategys.length;i++){
          if($(".priceStrategys").eq(i).children(".iconfont").hasClass("icon-1")){
            policyPriceId = that.priceStrategys[i].POLICY_PRICE_ID + ",";
          }
        }

        for(var i = 0 ; i < that.StrategyList.length;i++){
          if($(".StrategyList").eq(i).children(".iconfont").hasClass("icon-1")){
            strategyIds += that.StrategyList[i].STRATEGY_DETAIL_ID + ",";
          }
        }
        if(strategyIds){
          strategyIds = strategyIds.substring(0, strategyIds.lastIndexOf(','));
        }

        if(!this.CustomerName){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
        if(!$("div.uploadImg ul>li").length){

          that.$alert('', '', {
            confirmButtonText: ''
          })
          return;
        }
         if(!this.Consignee){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
         if(!this.ContactTel){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
        if(!this.AddressDetail){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
        if(!that.Channel){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }

        if(!(that.$store.state.StoreInfo.province || that.$store.state.StoreInfo.cityChinese || that.$store.state.StoreInfo.districict || that.$store.state.StoreInfo.street)){
          that.$alert("", '', {
            confirmButtonText: '',
          })
          return;
        }

        if(!that.latitude){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }

        that.getVisitLineInfo();

        if(!that.VisitRouteId){
          that.$alert("", '', {
            confirmButtonText: '',
          })
          return;
        }

        var SubmitData = {
          "cellphone":that.$store.state.userInfo.cellphone,//
          "userId":that.$store.state.userInfo.user_id,
          "businessId":that.$store.state.businessInfo.businessId,//id
          "suppliersId":that.suppliersId,//id
          "storesCode":"",//
          "storesName":that.CustomerName,//
          "storesAdress":that.AddressDetail,//
          "province":that.$store.state.StoreInfo.province?that.$store.state.StoreInfo.province:'',//
          "cityChinese":that.$store.state.StoreInfo.cityChinese?that.$store.state.StoreInfo.cityChinese:'',//
          "districict":that.$store.state.StoreInfo.districict?that.$store.state.StoreInfo.districict:'',//
          "street":that.$store.state.StoreInfo.street?that.$store.state.StoreInfo.street:'',//
          "storesContactName":that.Consignee,//
          "storesTel":that.ContactTel,//
          "storesNotes":that.CustomerRemark,//
          "visitRouteId":that.VisitRouteId?that.VisitRouteId:'',//id
          "visitRoute":that.VisitRouteNameInsert?that.VisitRouteNameInsert:'',//
          "channel":that.$store.state.StoreInfo.ChannelList.code?that.$store.state.StoreInfo.ChannelList.code:'',//
          "longitude":that.longitude?that.longitude:'',//114.22,//
          "latitude":that.latitude?that.latitude:'',//22.44,//
          "strategyIds":strategyIds,
          "policyPriceId":policyPriceId,//id
        }
        
        // 
        that.httpRequest("insterOrgn.do",SubmitData,function(Res){
          

          if(Res.status == 1){
            
            that.storeId =Res.data;
            
            //
            that.uploadParam = {
              businessCode: that.$store.state.businessInfo.businessCode,//
              storeId: that.storeId,//id
              storeName: that.$store.state.StoreInfo.CustomerName,//
              userId : that.$store.state.userInfo.user_id,//id
              cellphone : that.$store.state.userInfo.cellphone,//
            };
         
            // 
            that.$refs.upload_ref.submit();
            that.ifTips = true;
            that.tips_Msg = "";

            setTimeout(function(){
              //
              that.$router.push({
                  name:"CustomerList",//
                })
              },2000)
            that.$store.state.StoreInfo = {};
          }else{
            that.$alert('', '', {
              confirmButtonText: '',
            })
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateNewCustomer{
    overflow-x: hidden;
    padding-bottom:140/50rem;
    header{
      background-color:@PublicColor;
      color:#fff;
      border-bottom: none;
    }
    .SelectMeng{
      .SelectItem{
        max-height: 570/50rem;
      }
    }
    .RoutLineInfo{
      span{
        float: left;
        width: 50%;
      }
      input{
        background-color: pink;
        width: 50%;
      }
    }
    .container{
      font-size: 30/50rem;
      i.icon-xiangyou{
        float: right;
        margin-right: 15/50rem;
      }
      .SupplierInfo,.CustomerInfo,.StrategyInfo{
        &>div{
          line-height: 90/50rem;
          padding-left: 10/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &:nth-child(1){
            background-color: #E6E6E6;
            font-size: 26/50rem;
            line-height: 50/50rem;
            border-bottom:none;
          }
        }
      }
      .StrategyInfo{
        .StrategyList,.priceStrategys{
          &>i.iconfont{
            color:#1e8cf8;
            font-size: 3s0/50rem;
            &.icon-weixuanzhong{
              color: #888;
            }
          }
        }
      }
      .CustomerInfo{
        .channel{ 
          background-color: #E6E6E6;
          float: left;
          width: 48%;
          height: 92/50rem;
          margin-right: 10/50rem;
          span:first-child{float: left;}
          span:nth-child(2){
            width: 215/50rem;
            line-height: 35/50rem;
            font-size: 24/50rem;
            display: block;
            float: left;
            padding-top: 25/50rem;
          }
          i{float: right;}
        }
        .VisitingRoutes{ 
          background-color: #E6E6E6;
          width: 48%;
          height: 92/50rem;
          float: right;
          span:first-child{float: left;}
          span:nth-child(2){
            width: 150/50rem;
            line-height: 35/50rem;
            font-size: 25/50rem;
            display: block;
            float: left;
            padding-top: 25/50rem;
          }
          i{float: right;}
        } 
        .CustomerRemark{
          span{float: left;}
        }
      }
      .AddressInfo{
        overflow: hidden;
        position: relative;
        .ChooseAddress,.lnglat{
          flex-grow: 1;
          display: flex;
          span:first-child{
            float: left;
            width: 160/50rem;
          }
          span:nth-child(2){
            width: 550/50rem;
            line-height: 35/50rem;
            padding-top: 28/50rem;
            padding-bottom: 10/50rem;
          }
          span:nth-child(3){
            float: right;
          }
        }
        .coordinate{
          display:flex; 
          span:nth-child(1){
            flex:none;
          }
          span:nth-child(2){
            overflow:hidden;
            width: auto;
            line-height: 40/50rem;
            flex:1;
            padding-top: 28/50rem;
          }
          span:nth-child(3){
            width: 50/50rem;
            flex:none;
          }
        }
        &>div:not(.lianxiren){
          line-height: 90/50rem;
          padding-left: 10/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &:nth-child(1){
            border-bottom:none;
            line-height: 50/50rem;
            background-color: #E6E6E6;
            font-size: 26/50rem;
          }
          &:nth-child(2),&:nth-child(3){
            width: 75%;
          }
        }
        &>div.lianxiren{
          height: 180/50rem;
          width: 180/50rem;
          position: absolute;
          display: table;
          right: 0;
          top: 50/50rem;
          text-align: center;
          border-left:1/50rem solid @PublicBorderColor;
          border-bottom:1/50rem solid @PublicBorderColor;
          div{
            display: table-cell;
            vertical-align: middle;
          }
          i{
            font-size: 80/50rem;
            color: @PublicColor;
           }
          span{
            display: block; 
          }
        }
      }
      .uploadImg{
        padding: 20/50rem 20/50rem 20/50rem 10/50rem;
        .LoadImg_head{
          margin-bottom: 15/50rem;
          font-weight: 600;
        }
      }

      // .StrategyInfo{
      //   div:first-child{
      //       border-bottom:none;
      //       line-height: 50/50rem;
      //       background-color: #E6E6E6;
      //       font-size: 26/50rem;
      //     }

      // }
      .BtnComponent{
        width: 40%;
        left: 30%;
      }
      input{
        width:70%;
        height:50/50rem;
        border-style: none;
      }
    }
  } 
</style>


