<template>
  <div class="SuggestOrder">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="remark">
	  	<div><span></span></div>
	    <textarea v-model="remark" placeholder="">
	    </textarea>
	  </div>
	  <div class="BtnComponent bottomBtn">
	  	<div @click='toChooseGoods'></div>
	  	<div @click="toVividDisplay"></div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"",//
        remark:'',//
      }
    },
    components:{
  	  componentHeader,
  	},
    created(){
        
    },
    methods:{
      // 
      f_Fanhui(){
      	this.$router.go(-1);
      },
      //  
      toChooseGoods(){
        this.$router.push({
          name:"ChooseGoods",
        })
      },
      // 
      toVividDisplay(){

        var that = this;

         var ProductData = {
          "cellphone":that.$store.state.userInfo.cellphone,///
          "visitId":that.$store.state.CurrentVisitId,//id
          "strRecommendedOrder":that.remark,//
        }
        console.log(ProductData)
        // 
        that.httpRequest("GetVisitRecommendedOrder.do",ProductData,function(ProductRes){
           console.log(ProductRes)
           // that.GoodsListData = ProductRes.data;

           that.$router.push({
             name:"VividDisplay",
           })
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .SuggestOrder{
  	overflow-x: hidden;
  }
</style>


