<template>
  <div class="CreateCustomerWeChat">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
	  <div class="container haveHeadcontainer">
      <div class="CustomerInfo">
        <div>
          <div></div>
          <div>{{ContactTel}}</div>
        </div>
        <div>
          <div></div>
          <div><input placeholder="" v-model='CustomerName'></input></div>
        </div>
        <div>
          <div></div>
          <div><input placeholder="" v-model='Consignee'></input></div>
        </div>
        <div @click='toChooseAddress'>
          <div></div>
          <div><span>{{Location}}</span><div><i class="iconfont icon-xiangyou"></i></div></div>
        </div>
        <div>
          <div></div>
          <div><input placeholder="" v-model='AddressDetail'></input></div>
        </div>
        <div  class="lnglat"  @click='toLnglat'>
          <div></div>
          <div><span>{{lnglat}}</span><div><i class="iconfont icon-xiangyou"></i></div></div>
        </div>
      </div>
      <div class="BusinessLicense">
        <div class="uploadImg">
          <div></div>
          <el-upload  :action = " HOST_xsy + 'wxInsterOrgnUpload.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="upload_ref" :limit="2" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
      <div class="IDcard">
        <div class="uploadImg">
          <div></div>
          <el-upload  :action = " HOST_xsy + 'wxInsterOrgnUpload.do'" :auto-upload="false"  :data="uploadParams1"  list-type="picture-card"  ref="upload_ref1" :limit="2" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview">
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
        </div>
      </div>
      <div class="BtnComponent bottomBtn">
        <div @click='CustomerSave'></div>
      </div>
       <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  export default{
    data(){
      return{
      	headerText:"",//
        ContactTel:'',//
        storeID:'',//ID
        CustomerName:'',//
        Consignee:'',//
        Location:'',//
        AddressDetail:'',//
        longitude:'',//
        latitude:'',//
        lnglat:'',//
        province:'',//
        cityChinese:'',//
        districict:'',//
        street:'',//
        dialogImageUrl: '',//
        dialogVisible: false,//
        uploadParams:{},// 
        uploadParams1:{},// 
        uploadSuccessNum:0,//
        uploadTotalNum:0,//
        uploadFailNum:0,//
        ifTips:false,//
        tips_Msg:"",//
        LoginOrReg:'',//
        yzm:'',//
        phoneNum:"",//
      } 
    },
    components:{
	    componentHeader,
	  },
    mounted(){
      window.SetSendLocation = this.SetSendLocation;
    },
    created(){

      var that = this;
      that.ContactTel = that.$store.state.WXRegIdentifyPhone;//
      that.CustomerName = that.$store.state.StoreInfo.CustomerName;//
      that.Consignee = that.$store.state.StoreInfo.Consignee;//
      that.Location = that.$store.state.StoreInfo.Location;//
      that.AddressDetail = that.$store.state.StoreInfo.AddressDetail;//
      that.longitude = that.$store.state.StoreInfo.longitude;//
      that.latitude = that.$store.state.StoreInfo.latitude;//
      that.lnglat = that.$store.state.StoreInfo.lnglat;//
      that.LoginOrReg = that.$store.state.WXRegLoginOrReg;//
      that.yzm = that.$store.state.WXRegCode;//
    },
    methods:{
    	// 
    	f_Fanhui(){
        var that = this;
        this.$store.state.StoreInfo = {};
        this.$router.push({
          name:'WXVerify',
          query:{
            phoneNum:that.ContactTel,
            LoginOrReg : that.LoginOrReg,//
            yzm:that.yzm,//
          }
        })
      },
      toLnglat(){
        this.saveInfo();
        this.$router.push({
          name:"SendLnglat",
           query:{
            whoPush:"CreateCustomerWeChat",
          }
        })
      },
      //push
      saveInfo(){
        var that = this;
        that.$store.state.StoreInfo.CustomerName = that.CustomerName;//
        that.$store.state.StoreInfo.Consignee = that.Consignee;//
        that.$store.state.StoreInfo.Location = that.Location;//
        that.$store.state.StoreInfo.AddressDetail = that.AddressDetail;//
        that.$store.state.StoreInfo.longitude = that.longitude;//
        that.$store.state.StoreInfo.latitude = that.latitude;//
        that.$store.state.StoreInfo.lnglat = that.lnglat;//
      },
      //   
      toChooseAddress(){
        this.saveInfo();
        this.$router.push({
          name:"ChooseAddress",
          query:{
            create_whoPush:"WeChat"
          }
        })
      },
      // 
      uploadSuccess  (response, file, fileList){

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 

          that.$store.state.StoreInfo = {};

          that.$alert('', '', {
            confirmButtonText: '',
            callback(){
              that.$router.push({
                name:"WXLogin",
              })
            }
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 
          that.$alert("", '', {
            confirmButtonText: ''
          })
        }
      },
      // 
      uploadError  (err, file, fileList){
        var that = this;

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 

          that.uploadSuccessNum = 0;
          that.uploadFailNum = 0;
          that.uploadTotalNum = 0;
          that.$emit('isLoading', false);

          that.$alert("", '', {
            confirmButtonText: ''
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 
          that.$alert("", '', {
            confirmButtonText: ''
          })
        }
      },
      // 
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      // 
      CustomerSave:function(){
        var that = this;  
        if(!this.CustomerName){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
        if(!this.Consignee){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
         if(!this.Location){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }

        if(!this.AddressDetail){
          that.$alert("", '', {
            confirmButtonText: '',
          })  
          return;
        }
        if(!$("div.BusinessLicense div.uploadImg ul>li").length){
          that.$alert('', '', {
            confirmButtonText: ''
          })
          return;
        }

        if($("div.IDcard div.uploadImg ul>li").length < 2 ){
          that.$alert('', '', {
            confirmButtonText: ''
          })
          return;
        }

        that.uploadTotalNum = $("div.uploadImg ul>li").length;

        var SubmitData = {
          "cellphone":that.$store.state.userInfo.cellphone,//
          "storesName":that.CustomerName,//
          "storesAdress":that.AddressDetail,//
          "province":that.$store.state.StoreInfo.province,//
          "cityChinese":that.$store.state.StoreInfo.cityChinese,//
          "districict":that.$store.state.StoreInfo.districict,//
          "street":that.$store.state.StoreInfo.street,//
          "storesContactName":that.Consignee,//
          "storesTel":that.ContactTel,//
          "longitude":that.longitude,//
          "latitude":that.latitude,//
        }
        // 
        that.httpRequest("wxInsterOrgn.do",SubmitData,function(Res){
          
          that.uploadParams.types =  "BusinessLicense";//
          that.uploadParams1.types =  "IDcard";//
          that.uploadParams.storeId =  Res.data;//id
          that.uploadParams1.storeId =  Res.data;//id
          
          // 
          that.$refs.upload_ref.submit();
          that.$refs.upload_ref1.submit();
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateCustomerWeChat{
    overflow-x: hidden;
    padding-bottom: 150/50rem;
    .container{
      font-size: 28/50rem;
      .CustomerInfo{
        &>div{
          line-height: 60/50rem;
          padding:10/50rem 20/50rem;
          box-sizing:border-box;
          border-bottom:2/50rem solid @PublicBorderColor;
          display: flex;
          &>div{
            &:nth-child(1){
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &:nth-child(2){
              color: #666;
              flex-grow: 1;
              padding-left:20/50rem;
              box-sizing:border-box;
              overflow: hidden;
              display: flex;
              line-height: 60/50rem;
              input{
                flex-grow: 1;
                line-height: 60/50rem;
                border:none;
              }
              span{
                flex-grow: 1;
              }
              div{
                width: 50/50rem;
                flex-shrink:0;
                position: relative;
                i.icon-xiangyou{
                  position:absolute;
                  top: 50%;
                  left: 60%;
                  transform: translate(-50%,-50%);
                  font-size: 40/50rem;
                }
              }
            }
          }
        }
      }
      .uploadImg{
        border-bottom:1/50rem solid @PublicBorderColor;
        padding: 25/50rem 20/50rem;
        box-sizing:border-box;
        &>div{
          padding-bottom:15/50rem;
        }
      } 
      .BtnComponent{
        width: 40%;
        left: 30%;
      }
    }
  }  
</style>


