<template>
  <div class="PutInStorageDetails">
  	<!--  -->
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class='PutInOrderInfo'>
        <div class='infoHead'></div>
        <div class='infoContainer'>
          <div><span></span><span>{{orderInfo.IN_NO}}</span></div>
          <div v-if="orderInfo.OMSNO"><span>{{orderInfo.OMSNO}}</span></div>
          <div><span></span><span>{{orderInfo.ADDDATE}}</span></div>
          <div><span></span><span>{{orderInfo.TRANSFER_TYPE_text}}</span></div>
          <div><span></span><span>{{orderInfo.STATUS_text}}</span></div>
          <div v-if="orderInfo.REMARK"><div><span class="ASD"></span></div><div><span>{{orderInfo.REMARK}}</span></div></div>
          <div class="flexDiv">
            <div v-if='orderInfo.transferType =="PURCHASE_IN" || orderInfo.transferType =="SALE_OUT"'><span></span><span>{{orderInfo.PAY_PRICE}}</span></div>
            <div><span></span><span>{{orderInfo.orderQty}}</span></div>
            <div v-if='orderInfo.MJ_PRICE>0'><span></span><span>{{orderInfo.MJ_PRICE}}</span></div>
           <!--  <div><span>{{orderInfo.orderWt}}</span></div>
            <div><span>{{orderInfo.orderVol}}</span></div> -->
          </div>
        </div>
      </div>

      <div class='PutInOrderInfo'>
        <div class='infoHead'></div>
        <div class='infoContainer'>
          <div><div><span class="ASD ASD_TWO"></span></div><div><span>{{orderInfo.consignorName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO"></span></div><span>{{orderInfo.consignorContact}}&nbsp; {{orderInfo.consignorTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consignorTelephone' @click="callPhone(orderInfo.consignorTelephone)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div v-if="orderInfo.consignorAddress"><span></span><span>{{orderInfo.consignorAddress}}</span></div>
        </div>
      </div>

      <div class='PutInOrderInfo'>
        <div class='infoHead'></div>
        <div class='infoContainer'>
          <div><div><span class="ASD ASD_TWO"></span></div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO"></span></div><div><span>{{orderInfo.consigneeContact}}&nbsp; {{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-if='orderInfo.consigneeAddress'><span></span><span>{{orderInfo.consigneeAddress}}</span></div>
        </div>
      </div>
      <!--  -->
      <componentCargoInfo :goodsArr='goodsArr' :TRANSFER_TYPE='orderInfo.transferType' titleHead=''></componentCargoInfo>

      <!--  -->
      <componentCargoInfo :goodsArr='giftsArr' titleHead=''></componentCargoInfo>
      <!--   -->
      <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType != "TRANSFER_IN"'>
        <div @click='cancelOutbound'></div>
        <div @click='confirmOutbound' v-if='$store.state.CurrentRole =="osp"'></div>
      </div>
      <!--    -->
      <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType == "TRANSFER_IN"'>
        <div @click='cancelTransferIn'></div>
        <div v-if='$store.state.CurrentRole =="osp"' @click='confirmTransferIn'></div>
      </div>
    </div>
	  
     <div class="selfconfirm" v-if="selfconfirm_show">
      <div class="selfconfirm_contianer">
        <div class="selfconfirm_head"></div>
        <div class="selfconfirm_content">{{confirmcontent}}</div>
        <div class="selfconfirm_btn">
          <div @click='selfconfirm_cancel()'></div>
          <div @click='selfconfirm_confirm_()'></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentCargoInfo from '@/components/Widgets/componentCargoInfo'
  export default{
  	data(){
  	  return{
  	  	headerText:'',
  	  	TextContent:'',
  	  	orderInfo:{},//
        goodsArr: [],//
        giftsArr:[],//
        selfconfirm_show:false,//confirm
        confirmcontent:'',//confirm
  	  }
  	},
  	components:{
	    componentHeader,
      componentCargoInfo
	  },
  	created(){
      
      var that = this;

      var LineData = {
        "transferId":that.$route.query.TRANSFER_ID,//id
      }
      console.log(LineData)

      // 
      that.httpRequest("searchTransferReceiptDetail.do",LineData,function(SupRes){
         console.log(SupRes)
         
         if(SupRes.data.length){

            if(SupRes.data[0].orderInfo.transferType == 'PURCHASE_IN'){

              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'OTHER_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN_ALL'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '()';

            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_RETURN_IN_PART'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '()';
            }else if(SupRes.data[0].orderInfo.transferType == 'TRANSFER_IN'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'SALE_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'TRANSFER_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }else if(SupRes.data[0].orderInfo.transferType == 'PURCHASE_RETURN_OUT'){
              
              SupRes.data[0].orderInfo.TRANSFER_TYPE_text = '';
            }

            if(SupRes.data[0].orderInfo.STATUS == 'NEW'){

              SupRes.data[0].orderInfo.STATUS_text = '';

            }else if(SupRes.data[0].orderInfo.STATUS == 'FINISH'){

              SupRes.data[0].orderInfo.STATUS_text = '';

            }else if(SupRes.data[0].orderInfo.STATUS == 'CANCEL'){

              SupRes.data[0].orderInfo.STATUS_text = '';
            }else{
              SupRes.data[0].orderInfo.STATUS_text = SupRes.data[0].orderInfo.STATUS;
            }

            that.orderInfo = SupRes.data[0].orderInfo;

            var goodsArr = [],giftsArr = [];

            for(var i=0;i<SupRes.data[0].detail.length;i++){

             if(SupRes.data[0].detail[i].PRODUCT_TYPE == 'GF'){

                giftsArr[giftsArr.length]=SupRes.data[0].detail[i];
              }else{
                goodsArr[goodsArr.length]=SupRes.data[0].detail[i];
              }
            }
            that.goodsArr = goodsArr;
            that.giftsArr = giftsArr;
         }else{
            // 
            that.$alert('', '', {
              confirmButtonText: '',
            })
         }
      })
  	},
  	methods:{
  	  // 
      f_Fanhui(){
      	this.$router.push({
          name:"WarehousingDetail",
        })
      },
      // 
      f_Text(){

      },
      // confirm
      selfconfirm_cancel (){
        this.selfconfirm_show = false;
      },
      // confirm
      selfconfirm_confirm(){

      },
      // confirm
      selfconfirm_confirm_ (){

        this.selfconfirm_show = false;
        this.selfconfirm_confirm();
      },
      selfconfirm(text,selfconfirm_confirm){
        this.selfconfirm_show = true;
        this.confirmcontent = text;
        this.selfconfirm_confirm = selfconfirm_confirm;
      },
  	  //
      confirmOutbound () {
        var that = this;

        this.selfconfirm('?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,///
            "transferId":that.$route.query.TRANSFER_ID,//id
            "businessId":that.$store.state.businessInfo.businessId,//id
          }
          console.log(ConfirmData)

          // 
          that.httpRequest("saveConfirmTransferReceipt.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '', {
              confirmButtonText: '',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      //
      cancelOutbound (){
        var that = this;

        this.selfconfirm('?',function(){
          var CancelData = {
            "transferId":that.$route.query.TRANSFER_ID,//id
          }
          console.log(CancelData)

          // 
          that.httpRequest("saveCancelTransferReceipt.do",CancelData,function(CancelRes){
             console.log(CancelRes)
             that.$alert(CancelRes.Msg, '', {
              confirmButtonText: '',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      // 
      confirmTransferIn() {

        var that = this;
        this.selfconfirm('?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,///
            "transferId":that.$route.query.TRANSFER_ID, //id
            "appUsersId":that.$store.state.userInfo.user_id, //id
            "type":"IN", // 
          }
          console.log(ConfirmData)

          // 
          that.httpRequest("saveTransferConfirm.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '', {
              confirmButtonText: '',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
      // 
      cancelTransferIn() {

        var that = this

        this.selfconfirm('?',function(){
          var ConfirmData = {
            "transferId":that.$route.query.TRANSFER_ID, // id
            "appUsersId":that.$store.state.userInfo.user_id, //id
            "type":"IN", // 
          }
          console.log(ConfirmData)

          // 
          that.httpRequest("saveTransferCancel.do",ConfirmData,function(ConfirmRes){
            console.log(ConfirmRes)

            that.$alert(ConfirmRes.Msg, '', {
              confirmButtonText: '',
              callback(){
                that.$router.push({
                  name:"WarehousingDetail",
                })
              }
            })
          })
        })
      },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .PutInStorageDetails{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    .container{
      .PutInOrderInfo{
        .infoContainer{
          font-size: 28/50rem;
          padding:15/50rem 15/50rem;
          border-radius:15/50rem;
          box-sizing: border-box;
          background-color:#fff;
          margin-bottom: 20/50rem;
          &>div.flexDiv{
            display: flex;
            &>div{
              flex-grow: 1;
              span:first-child{
                color: #000;
              }
            }
          }
          &>div{
            line-height: 50/50rem;
            display: flex;
            &>div:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
            }
            &>span:first-child{
              min-width: 130/50rem;
              flex-shrink:0;
              color: #000;
            }
            &>span:nth-child(2){
              flex-grow: 1;
            }
          }
        }
        span{
          color: #555;
        }
        span.ASD{
          letter-spacing: 59.8/50rem;
          color: #000;
          margin-left: 0;
        }
        span.ASD_TWO{
          letter-spacing: 15/50rem;
        }
      }
    }
  }
 /*   */
  .entryBtn{
    font-size: 26/50rem;
    width:100%;
    position: fixed;
    bottom: 30/50rem;
    left: 0;
    text-align: center;
    overflow: hidden;
    display: flex;
    padding:0 20/50rem;
    box-sizing:border-box;
    text-align: center;
    &>div{
      width: 50%;
      margin: 0 auto;
      height: 68/50rem;
      background-color: @PublicColor;
      color: #fff;
      float: left;
      line-height: 68/50rem;
      text-align: center;
      border-radius:34/50rem;
      &:nth-child(2){
        margin-left: 20/50rem;
      }
    }
  }
</style>


