<template>
  <div class="WarehousingDetail">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	  <div class="container haveHeadcontainer">
      <div class="CustomerSearch">
        <div>
          <input v-model='searchValue' placeholder="    ">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
      <div class="ListBox">
        <mt-loadmore  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class='putWarehouseList' @click='toPutDetails(Outboundindex)' v-for='(OutboundItem,Outboundindex) in OutboundOrderList'  :id="Outboundindex"  :key='Outboundindex'>
            <div><span>{{OutboundItem.IN_NO}}</span></div>
            <div v-if="OutboundItem.TMS_NO"><span>{{OutboundItem.TMS_NO}}</span></div>
            <div v-if="OutboundItem.OUT_NO"><span>{{OutboundItem.OUT_NO}}</span></div>
            <div>&nbsp;&nbsp;&nbsp;<span>{{OutboundItem.CONSIGNOR_NAME}}</span></div>
            <div>&nbsp;&nbsp;&nbsp;<span>{{OutboundItem.ADDWHO}}</span></div>
            <div><span>{{OutboundItem.ADDDATE}}</span><span class='float red' style='color:#28B45F;'>{{OutboundItem.TRANSFER_TYPE_text}}</span></div>
            <div><span>{{OutboundItem.ORDER_QTY}}</span><span class='float red' :style='{color:OutboundItem.STATUS_color}'>{{OutboundItem.STATUS_text}}</span></div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span></span>
      </div>
	  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"",//
        OutboundOrderList:[],//
        noDataShow:false,// 
        searchValue:'',//
        numberPerPage:15,//
        loadPage: 1 ,//
        allLoaded:true,//
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){
      this.reqPutInList();
    },
    watch:{

      // input
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqPutInList();
      }
    },
    methods:{
    	// 
    	f_Fanhui(){
    		this.$router.push({
          name:"InventoryManage",
        })
    	},
      // 
      reqPutInList(load){
        var that = this;

        var LineData = {
          "appUsersId":that.$store.state.userInfo.user_id,//id
          "businessId":that.$store.state.businessInfo.businessId,//id
          "strConsignorName":that.searchValue,//   
          "transferType":'',// 
          "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          "currentPage":that.loadPage,//
          "numberPerPage":that.numberPerPage,//
        }
        console.log(LineData)

        // 
        that.httpRequest("searchTransferReceipt.do",LineData,function(SupRes){
           console.log(SupRes)

           for(var i =0;i<SupRes.data.length;i++){

            if(SupRes.data[i].TRANSFER_TYPE == 'PURCHASE_IN'){

              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'OTHER_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN_ALL'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '()';

            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_RETURN_IN_PART'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '()';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'TRANSFER_IN'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'SALE_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'TRANSFER_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }else if(SupRes.data[i].TRANSFER_TYPE == 'PURCHASE_RETURN_OUT'){
              
              SupRes.data[i].TRANSFER_TYPE_text = '';
            }

            if(SupRes.data[i].STATUS == 'NEW'){

              SupRes.data[i].STATUS_color = '#FE5256';
              SupRes.data[i].STATUS_text = '';

            }else if(SupRes.data[i].STATUS == 'FINISH'){

              SupRes.data[i].STATUS_color = '#28B45F';
              SupRes.data[i].STATUS_text = '';

            }else if(SupRes.data[i].STATUS == 'CANCEL'){

              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = '';
            }else{
              SupRes.data[i].STATUS_color = '#777';
              SupRes.data[i].STATUS_text = SupRes.data[i].STATUS;
            }
          }

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          // 
          if (that.loadPage > 1) {

            if(SupRes.data.length){
              that.OutboundOrderList = that.OutboundOrderList.concat(SupRes.data);
            }
          } else {
            that.OutboundOrderList = SupRes.data;
          }

          // 
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }

          if(that.OutboundOrderList.length){
            that.noDataShow = false;
            
          }else{
            // 
            that.noDataShow = true;
          }
        })
      },
      // 
      toPutDetails(idx) {
        var that = this;
        this.$router.push({
          name:"PutInStorageDetails",
          query:{
            TRANSFER_ID:that.OutboundOrderList[idx].TRANSFER_ID
          }
        })
      },
      // 
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.reqPutInList("TopLoad");
      },
      // 
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqPutInList("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .WarehousingDetail{
    height: 100%;
    .container{
      margin-top: 0;
      height: 100%;
      .CustomerSearch{
        font-size: 32/50rem;
        position: fixed;
        z-index: 50;
        background-color: #fff;
        top: 89/50rem;
        left: 0;
        .TopSearchBox;
      }
      .ListBox{
        padding-top:189/50rem;
        height: 100%;
        box-sizing:border-box;
      }
    }
    .putWarehouseList{
      width:100%;
      color: #555;
      padding: 20/50rem 15/50rem;
      box-sizing: border-box;
      border-bottom: 20/50rem solid #EEEFF2;
      background-color: #fff;
      div{
        line-height: 46/50rem;
      }
    }
    .float{
      float: right;
    }
  }
  .kong{
    height: 100/50rem;
  }
</style>


