<template>
  <div class="OrderConfirm">
    <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
    <div class="container haveHeadcontainer">
      <div class="DeliveryInfo">
        <div class='infoHead_'></div>
        <div class='infoContainer_'>
          <div><span></span><span>{{DeliveryInfo.CUSTOMER_NAME}}</span></div>
          <div><span></span><span>{{DeliveryInfo.CONTACT_NAME}}</span></div>
          <div><span></span><span>{{DeliveryInfo.CUSTOMER_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='DeliveryInfo.CUSTOMER_TEL' @click="callPhone(DeliveryInfo.CUSTOMER_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div><span></span><span>{{DeliveryInfo.CUSTOMER_ADDRESS }}</span></div>
        </div>
      </div>
      <div class="ReceiveInfo">
        <div class='infoHead_'></div>
        <div class='infoContainer_'>
          <div><span></span><span>{{ReceiptInfo.CUSTOMER_NAME}}</span></div>
          <div><span></span><span>{{ReceiptInfo.CONTACT_NAME}}</span></div>
          <div><span></span><span>{{ReceiptInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='ReceiptInfo.CONTACT_TEL' @click="callPhone(ReceiptInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
          <div><span></span><span>{{ReceiptInfo.CUSTOMER_ADDRESS}}</span></div>
        </div>
      </div>
      <div class='orderInfos'>
        <div class='infoHead_'></div>
        <div class='orderList' v-for='(dataItem,idx) in submitArr'  :id="idx"  :key='idx'>
          <div class='LeftIcon'><img :src="dataItem.PRODUCT_PIC?dataItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
          </div>
          <div class='listRight'>
            <div class="PRODUCT_NAME"><span>{{dataItem.PRODUCT_NAME}}</span></div>
            <div class="listRightBottom">
              <div class="listRightBottomLeft">
                <div>
                  <span></span>
                  <span class="listRight_rmb"></span>
                  <span class="listRight_rmbNUM">{{dataItem.UNIT_PRICE}}</span>
                </div>
                <div>
                  <span></span>
                  <span class="listRight_rmbNUM">{{dataItem.number}} </span>
                  <span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
                </div>
              </div>
              <div class="listRightBottomRight">
                <div v-if='dataItem.promotionStrategy=="TJCL"' class="priceAdjust">
                  <span></span>
                  <i class="iconfont icon-Untitled-" :id="idx"  @click='jian'></i>
                  <div @click='entryShow' :id='idx' class="priceAdjust_price"><span class="listRight_rmb"></span><span class="listRight_rmbNUM">{{dataItem.priceInit}}</span></div>
                  <i class="iconfont icon-jia1" :id="idx"  @click='jia'></i>
                </div>
                <div v-else>
                  <span></span>
                  <span class="listRight_rmb"></span>
                  <span class="listRight_rmbNUM">{{dataItem.DIS_PRICE}}</span>
                  <i v-if='$store.state.businessInfo.isEdit == "T"' @click="modify_price(dataItem)" class="iconfont icon-xie"></i>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='GiftInfo' v-if='giftInfo_IfShow'>
        <div class='infoHead_'></div>
        <div class="GiftContainer">
          <div class="SpecifyGiftList" v-if='SpecifyGift.length'>
            <div class='haveGift' v-for='(itemGift,idxGift) in SpecifyGift'  :id="idxGift"  :key='idxGift'>
              <div>
                <div class='LeftIcon'><img :src="itemGift.PRODUCT_PIC?itemGift.PRODUCT_PIC:'static/img/Order/goods.png'" alt=""></div>
                <div class='listCenter'>
                  <div class="PRODUCT_NAME"><span>{{itemGift.PRODUCT_NAME}}</span></div>
                  <div v-if='itemGift.PRODUCT_DESC'><span></span><span>{{itemGift.PRODUCT_DESC}}</span></div>
                </div>
                <div class='gift_right'>
                  {{itemGift.giveNum}} {{itemGift.UNIT_OF_MEASURE_STR}}
                </div>
              </div>
              <div class="bottleFee" v-if='itemGift.GIFT_TYPE == "WINE"'>
                <div>
                  <div><span>{{itemGift.bottleName}}</span></div>
                  <div><span></span><span> {{itemGift.bottlePrice}}</span>&nbsp;&nbsp;&nbsp; <span></span><span> {{parseFloat(parseFloat(itemGift.giveNum) * parseFloat(itemGift.bottlePrice)).toFixed(2)}}</span></div>
                </div>
              </div>
            </div>
          </div>
          <div class="OptionsGiftList"v-if='OptionsGiftChosenCart.length'>
            <div class='haveGift' v-for='(OpItemGift,OpIdxGift) in OptionsGiftChosenCart'  :id="OpIdxGift"  :key='OpIdxGift'>
              <div>
                <div class='LeftIcon'><img :src="OpItemGift.PRODUCT_PIC?OpItemGift.PRODUCT_PIC:'static/img/Order/goods.png'" alt=""></div>
                <div class='listCenter'>
                  <div class="PRODUCT_NAME"><span>{{OpItemGift.PRODUCT_NAME}}</span></div>
                  <div v-if='OpItemGift.PRODUCT_DESC'><span></span><span>{{OpItemGift.PRODUCT_DESC}}</span></div>
                </div>
                <div class='gift_right'>
                  {{OpItemGift.number}} {{OpItemGift.UNIT_OF_MEASURE_STR}}
                </div>
              </div>
              <div class="bottleFee" v-if='OpItemGift.GIFT_TYPE == "WINE"'>
                <div>
                  <div><span>{{OpItemGift.bottleName}}</span></div>
                  <div><span></span><span> {{OpItemGift.bottlePrice}}</span>&nbsp;&nbsp;&nbsp; <span></span><span> {{parseFloat(parseFloat(OpItemGift.number) * parseFloat(OpItemGift.bottlePrice)).toFixed(2)}}</span></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class='noneGift'>
          <span v-if='!SpecifyGift.length&&!OptionsAvailableGift.length'></span>
          <div v-if='OptionsAvailableGift.length' class="addGift"><span @click='_addGift'>{{OptionsGiftChosenCart.length?'':''}}</span></div>
        </div>
      </div>

      <div class='total'>
        <div class='infoHead_'></div>
          <div class="infoContainer_">
            <div><span></span><span>{{totalNum}}</span></div>
            <div v-if="sumMjPrice_cru>0"><span></span><span>{{sumMjPrice_cru}}</span></div>
            <div  v-if='giftInfo_IfShow'><span></span><span>{{totalPayment}}</span></div>
            <div class="pay_typeInfo"  v-if='giftInfo_IfShow'><span></span><span>{{PAY_TYPE}}
          <!--     <el-select  v-model="PAY_TYPE"   placeholder="">
                <el-option
                  v-for="item in pay_typeList"
                  :key="item.name"
                  :label="item.name"
                  :value="item.name">
                </el-option>
            </el-select> -->
            </span></div>
            <div v-if='MJ_hidden'><div></div><div>{{MJ_REMARK}}</div></div>
            <div v-if='MJ_hidden'><div></div><div>{{MJ_PRICE}}</div></div>
            <!-- <div><span>{{ACT_PRICE}}</span></div> -->
          </div>
      </div>
      <div class='arrDate' v-if='giftInfo_IfShow'>
          <div class='infoHead_'></div>
          <el-date-picker
            v-model="REQUEST_DELIVERY" :editable='false' 
            type="date"
            placeholder=""
            align="right"
            :picker-options="pickerOptions"
            format="yyyy  MM  dd " value-format="yyyy-MM-dd">
          </el-date-picker>
      </div>
      <textarea class="Remark" placeholder='' v-model="REMARK_VALUE" maxlength="-1"></textarea>
    </div>
    <div class="BtnComponent bottomBtn">
      <div class='onlyBtn' @click='orderSubmit'></div>
    </div>

    <div v-if="ifTips" class="msg_tips">
      <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
      </div>
    </div>

    <!--  -->
    <div class='entryNumber' v-if='isHidden'>
      <div class='entryContaniner'>
        <div class="entry_contaniner_header"></div>
        <div><span>{{submitArr[inputNum].priceMax}}</span></div>
        <div><span>{{submitArr[inputNum].priceMin}}</span></div>
        <input  v-model="inputValue" type='number'></input>
        <div class='entryBtn'>
          <div class='en_cancel' @click='cancel_entry'></div>
          <div class='en_ensure' @click='ensure_entry'></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import $ from 'jquery'
  export default{
    data(){
      return{
        headerText:'',
        submitArr: [],//
        giftInfo_IfShow:true,//
        SpecifyGift:[],//
        OptionsAvailableGift:[],//
        OptionsGiftChosenCart:[],//
        MJ_hidden: false,// 
        REMARK_VALUE: '',//
        isHidden: false,//
        inputValue: '',//
        inputNum: 0,//idx
        totalNum:0,//
        totalPrice:0,//
        totalPayment:0,//
        sumMjPrice_cru:0,//
        ifTips:false,//
        tips_Msg:"",//
        whoPush:"",//
        orderType:'',//
        storeId:0,//ID
        visitId:0,//id
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 2);
              picker.$emit('pick', date);
            }
          }]
        },
        REQUEST_DELIVERY:'',//
        PAY_TYPE:'',//
        // pay_typeList:[{'name':''},{'name':''},{'name':''},{'name':''}]
      }
    },
    components:{
      componentHeader,
    },
    mounted(){

      window.history.pushState(null, null, document.URL)
      window.addEventListener("popstate", this.onBrowserBack, false)
    },
    destroyed() {

      window.removeEventListener("popstate", this.onBrowserBack, false)
    },
    created(){

      var that = this,BackBottle = this.$store.state.BackBottle;

      this.ReceiptInfo = BackBottle.ConsigneeData_selected;//
      
      this.DeliveryInfo = BackBottle.DeliveryAddr_selected;//
      
      this.submitArr = BackBottle.g_submitArr; //

      this.REQUEST_DELIVERY = this.$store.state.ORDER_REQUEST_DELIVERY;//
      this.REMARK_VALUE = this.$store.state.ORDER_REMARK_VALUE;//

      if(this.$store.state.OptionsGiftChosenCart){
        this.OptionsGiftChosenCart = that.$store.state.OptionsGiftChosenCart;//
      }
      
      console.log(this.submitArr)

      this.totalNum = BackBottle.totalNum;//
      
      // 
      if(this.$route.query.whoPush){
          this.whoPush = this.$route.query.whoPush;
      }

      // 
      if(this.$route.query.orderType){
          this.orderType = this.$route.query.orderType;
      }

      // id
      if(this.$route.query.visitId){

        this.visitId = this.$route.query.visitId;
      }

      //   ()
      for (var i = 0; i < that.submitArr.length; i++) {

          let mx = that.submitArr[i];
          that.totalPrice += parseFloat(mx.UNIT_PRICE) * mx.number;
      }
      this.totalPrice = this.totalPrice.toFixed(2);

      // 
      if((that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN') || (that.whoPush == 'SalesOrder' && that.orderType == 'SALE_OUT') || (this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){

        //
        var OrderDetails=[],SalesPromotion_consigneeId=0;
        for (var m = 0; m < that.submitArr.length; m++) {
          OrderDetails[m] = {
            "UNIT_PRICE":that.submitArr[m].UNIT_PRICE,//
            "DIS_PRICE":that.submitArr[m].DIS_PRICE,//
            "PRODUCT_ID":that.submitArr[m].PRODUCT_ID,//id
            "PO_QTY":that.submitArr[m].number,//
            "PO_UNIT":that.submitArr[m].UNIT_OF_MEASURE,//  
            "PO_UNIT_STR":that.submitArr[m].UNIT_OF_MEASURE_STR,// 
            "FL_PRICE":that.submitArr[m].FL_PRICE,//
          }
        }

        // 
        if(that.whoPush == 'SalesOrder' && that.orderType == 'SALE_OUT'){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//id id 
        }
         // 
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN'){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//id  ospid
        }
        // 
        if((that.whoPush == 'CheckInventory' && that.orderType == 'SALE_OUT')||(that.whoPush == 'HistoryVisit' && that.orderType == 'SALE_OUT')){
          SalesPromotion_consigneeId =  that.ReceiptInfo.CUSTOMER_ID//id id 
        }

        var SalesPromotion_postData = {
          "businessId": that.$store.state.businessInfo.businessId,
          "consigneeId": SalesPromotion_consigneeId,//id id 
          "OrderDetails":OrderDetails,
        }

        // 
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){

           SalesPromotion_postData.outOrderIds = that.$store.state.SumMergeOrderList;//
        }

        console.log(SalesPromotion_postData)

        that.httpRequest("simulatedOrder.do",SalesPromotion_postData,function(promotionres){

          if(!that.$store.state.OptionsAvailableGift.length){
            that.submitArr = promotionres.data.detail; //
          }
          
          // GIVE  
          // STRAGY  
          that.SpecifyGift = promotionres.data.GIVE;
          that.OptionsAvailableGift = promotionres.data.STRAGY;
          that.PAY_TYPE = promotionres.data.PAY_TYPE;//
          
          that.totalNum =0;
          for(var gj=0;gj<that.submitArr.length;gj++){

            that.totalNum += parseInt(that.submitArr[gj].poQty);
          }

          for(var fh=0;fh < promotionres.data.GIVE.length;fh++){
            that.totalNum += parseInt(promotionres.data.GIVE[fh].giveNum);
          }

          for(var as=0;as < that.OptionsGiftChosenCart.length;as++){
            that.totalNum += parseInt(that.OptionsGiftChosenCart[as].number);
          }
          that.CalculateTotalPrice();
        })
      }else{

        //   
        that.giftInfo_IfShow = false;
      }
    },
    methods:{
      onBrowserBack() {
        
        this.f_Fanhui()
      },
      // 
      f_Fanhui(){

        var that = this;
        this.$store.state.OptionsGiftChosenCart = [];//
        this.$store.state.OptionsAvailableGift = [];//
        this.$store.state.OptionsGiftNumberList = {};// numberjson 
        this.$store.state.ORDER_REQUEST_DELIVERY='';//
        this.$store.state.ORDER_REMARK_VALUE='';//
        // 
        if(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              transferOutId:that.$route.query.transferOutId
            }
          })
          return;
        }

        // 
        if(that.whoPush == 'InventoryManage' && that.orderType == "TRANSFER" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              IFreceORdeliv_transition:that.$route.query.IFreceORdeliv_transition
            }
          })
          return;
        }
        //  
        if(that.whoPush == 'HistoryVisit' && that.orderType == "SALE_OUT" ){

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              orderConfirmattached:'orderConfirm',// 
            }
          })
          return;
        }

        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          // 

          that.$router.push({
            name:'ChooseGoods',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          })
          return;
        }
        // / 
        if(that.whoPush == 'CheckInventory' && that.orderType == 'SALE_OUT'){
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            that.$router.go(-1);
            return;
          }
        }

        this.$router.push({
          name:'ChooseGoods',
          query:{
            whoPush:that.whoPush,
            orderType:that.orderType
          }
        })
      },
        //   
      orderSubmit () {
        var that = this;
        var submitArr = this.submitArr;
        var OrderDetails=[];

        // 

        // this.orderType == "SALE_OUT" || 

        // || this.orderType == "SALE_RETURN_IN"  

        //   ||   || 
        if( (this.whoPush == 'InventoryManage' && this.orderType == "OTHER_OUT") || (this.whoPush == 'InventoryManage' && this.orderType == "SALE_RETURN_IN") || (this.whoPush == 'InventoryManage' && this.orderType == "TRANSFER") ||(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART" )) {

            //  
            for (var m = 0; m < submitArr.length; m++) {
              OrderDetails[m] = {
                "productId": "" + submitArr[m].PRODUCT_ID+"",
                "pirchaseQuantity":submitArr[m].number,//
                "unitPrice":submitArr[m].UNIT_PRICE,
                "payment":submitArr[m].UNIT_PRICE,
                "amount":submitArr[m].number * submitArr[m].UNIT_PRICE?submitArr[m].number * submitArr[m].UNIT_PRICE:0,
                "unit":submitArr[m].UNIT_OF_MEASURE,//
              }
            }

            // /
            var consignorId = 0,consigneeId = 0;
            if(that.DeliveryInfo.CUSTOMER_ID){
                // ID
                consignorId = that.DeliveryInfo.CUSTOMER_ID;
            }
            if(that.ReceiptInfo.CUSTOMER_ID){
                // id
                consigneeId = that.ReceiptInfo.CUSTOMER_ID;
            }

            //   usersId0
            var usersId = 0;//id
            if(that.whoPush == 'InventoryManage' && that.orderType == "TRANSFER"){

              usersId = that.$store.state.userInfo.user_id;

              // false osp  true
              if(that.$route.query.IFreceORdeliv_transition){
                
                usersId = consignorId;
                consignorId = 0;// ID
              }else{
                usersId = consigneeId;
                consigneeId = 0;// id
              }
            }

            var transferOutId = '';//id,
            if(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART"){
              transferOutId = that.$route.query.transferOutId;
            }

            var postData = {
              "appUsersId": that.$store.state.userInfo.user_id,//
              "usersId":usersId,//id,0
              "businessId": that.$store.state.businessInfo.businessId,
              "consignorId":consignorId,// ID
              "consigneeId": consigneeId,//id
              "transferOutType":that.orderType,//
              "pirchaseQuantity":that.totalNum,//
              "amount":that.totalPrice?that.totalPrice:0,
              "TransferOutDetails":OrderDetails,
              "visitId":visitId,//id0
              "transferOutId":transferOutId,//id,,''
              "remark": that.REMARK_VALUE,
            }

            this.httpRequest("saveTransferOut.do",postData,function(res){

              that.$store.state.BackBottle={
                g_submitArr:[],
                totalNum:0,
                ConsigneeData_selected:[],//
                DeliveryAddr_selected:[],//
                isShowSelectStore:false,//
              };//
              that.$store.state.OptionsAvailableGift=[];//
              that.$store.state.OptionsGiftChosenCart=[];//
              that.$store.state.OptionsGiftNumberList = {};// numberjson 
              that.$store.state.ORDER_REQUEST_DELIVERY='';//
              that.$store.state.ORDER_REMARK_VALUE='';//
              that.ifTips = true;
              that.tips_Msg = "";

              // 
              if(that.whoPush == 'OutboundDetail' && that.orderType == "SALE_RETURN_IN_PART" ){

                setTimeout(function(){

                  that.$router.push({
                    name:that.whoPush,
                    query:{
                      whoPush:'orderConfirm',
                      transferOutId:that.$route.query.transferOutId
                    }
                  })
                },2000)
                return;
              }

              setTimeout(function(){

                that.$router.push({
                  name:that.whoPush,
                })
              },2000)
            })
        }// 
        else if(
          (
          (this.whoPush == 'SalesOrder' && this.orderType == "SALE_OUT")     || 
          (this.whoPush == 'CheckInventory' && this.orderType == "SALE_OUT") || 
          (this.whoPush == 'HistoryVisit' && this.orderType == "SALE_OUT")  
          ) && this.$store.state.visitObject != "OSP"
          ){

            var payment,//
            STRATEGY_ID = [];//id

            //  
            for (var mn = 0; mn < submitArr.length; mn++) {

              //   
              if(submitArr[mn].promotionStrategy=="TJCL"){

                payment = submitArr[mn].priceInit;
                STRATEGY_ID[STRATEGY_ID.length] = submitArr[mn].strategyDetailId;//id
              }else{
                payment = submitArr[mn].DIS_PRICE;
              }
              OrderDetails[mn] = {
                "productId": submitArr[mn].PRODUCT_ID+"",
                "pirchaseQuantity":submitArr[mn].number,//
                'unit':submitArr[mn].UNIT_OF_MEASURE,//
                "unitPrice":submitArr[mn].UNIT_PRICE,//
                "payment":payment ,//
                "PRODUCT_TYPE":submitArr[mn].PRODUCT_TYPE,//
              }
            }

            // 
            for(var mq = 0; mq < that.SpecifyGift.length; mq++){

              STRATEGY_ID[STRATEGY_ID.length] = that.SpecifyGift[mq].strategyDetailId;//id

              OrderDetails[OrderDetails.length] = {
                "productId":  that.SpecifyGift[mq].PRODUCT_ID+"",
                "pirchaseQuantity":that.SpecifyGift[mq].giveNum ,//
                'unit':that.SpecifyGift[mq].UNIT_OF_MEASURE,//
                "unitPrice":that.SpecifyGift[mq].UNIT_PRICE,//
                "payment":0,//
                "PRODUCT_TYPE":that.SpecifyGift[mq].PRODUCT_TYPE,//
                "bottlePrice":that.SpecifyGift[mq].bottlePrice,//
                "bottleId":that.SpecifyGift[mq].bottleId,//id
                "GIFT_TYPE":that.SpecifyGift[mq].GIFT_TYPE,//
              }
            }

            // 
            for(var mc = 0; mc < that.OptionsGiftChosenCart.length; mc++){

              STRATEGY_ID[STRATEGY_ID.length] = that.OptionsGiftChosenCart[mc].strategyDetailId;//id

              OrderDetails[OrderDetails.length] = {
                "productId": that.OptionsGiftChosenCart[mc].PRODUCT_ID+"",
                "pirchaseQuantity":that.OptionsGiftChosenCart[mc].number,//
                'unit':that.OptionsGiftChosenCart[mc].UNIT_OF_MEASURE,//
                "unitPrice":that.OptionsGiftChosenCart[mc].UNIT_PRICE,//
                "payment":0,//
                "PRODUCT_TYPE":that.OptionsGiftChosenCart[mc].PRODUCT_TYPE,//
                "bottlePrice":that.OptionsGiftChosenCart[mc].bottlePrice,//
                "bottleId":that.OptionsGiftChosenCart[mc].bottleId,//id
                "GIFT_TYPE":that.OptionsGiftChosenCart[mc].GIFT_TYPE,//
              }
            }

            // /
            var consignorId = 0,consigneeId = 0;
            if(that.DeliveryInfo.CUSTOMER_ID){
                // ID
                consignorId = that.DeliveryInfo.CUSTOMER_ID;
            }
            if(that.ReceiptInfo.CUSTOMER_ID){
                // id
                consigneeId = that.ReceiptInfo.CUSTOMER_ID;
            }

            var visitId = 0;//id0
            if((that.whoPush == 'CheckInventory' && that.orderType == "SALE_OUT")|| (that.whoPush == 'HistoryVisit' && that.orderType == "SALE_OUT")){
                visitId = that.visitId;
            }
            console.log(OrderDetails)

            var transferOutType = that.$store.state.G_AppOrWX == "WX"?'STORE_PURCHASE':that.orderType;

            var postData = {
              "appUsersId": that.$store.state.userInfo.user_id,//
              "businessId": that.$store.state.businessInfo.businessId,
              "consignorId":consignorId,// ID
              "consigneeId": consigneeId,//id
              "transferOutType": transferOutType,//
              "pirchaseQuantity":that.totalNum,//
              "REQUESTED_DELIVERY_DTE": that.REQUEST_DELIVERY?that.REQUEST_DELIVERY + ' 00:00:00':'',//
              'STRATEGY_ID':STRATEGY_ID,//id
              'payPrice':that.totalPayment,//
              'mjPrice':that.sumMjPrice_cru,//
              "TransferOutDetails":OrderDetails,
              "visitId":visitId,//id0
              "remark": that.REMARK_VALUE,
            }
            console.log(postData)

            this.httpRequest("saveSellOut.do",postData,function(res){

                that.$store.state.BackBottle={
                    g_submitArr:[],
                    totalNum:0,
                    ConsigneeData_selected:[],//
                    DeliveryAddr_selected:[],//
                    isShowSelectStore:false,//
                };//
                that.$store.state.OptionsAvailableGift=[];//
                that.$store.state.OptionsGiftChosenCart=[];//
                that.$store.state.OptionsGiftNumberList = {};// numberjson 
                that.$store.state.ORDER_REQUEST_DELIVERY='';//
                that.$store.state.ORDER_REMARK_VALUE='';//
                that.ifTips = true;
                that.tips_Msg = "";

                setTimeout(function(){

                  // 
                  if((that.whoPush == 'CheckInventory' && that.orderType == "SALE_OUT")|| (that.whoPush == 'HistoryVisit' && that.orderType == "SALE_OUT")){

                    if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
                      that.$router.go(-2);
                    }else{
                      that.$router.push({
                        name:'VividDisplay',
                        query:{
                          visit_whoPush:"OrderConfirm"
                        }
                      })
                    }
                  }else{
                    that.$router.push({
                      name:'QuerySalesOrder',
                    })
                  }
                },2000)
            })
        }
        // 
        else if(this.orderType == "PURCHASE_IN" || this.$store.state.visitObject == "OSP"){

           var payment,//
            STRATEGY_ID = [];//id

            //  
            for (var mn = 0; mn < submitArr.length; mn++) {

              //   
              if(submitArr[mn].promotionStrategy=="TJCL"){

                payment = submitArr[mn].priceInit;
                STRATEGY_ID[STRATEGY_ID.length] = submitArr[mn].strategyDetailId;//id
              }else{
                payment = submitArr[mn].DIS_PRICE;
              }
              OrderDetails[mn] = {
                "productId": submitArr[mn].PRODUCT_ID+"",
                "pirchaseQuantity":submitArr[mn].number,//
                'unit':submitArr[mn].UNIT_OF_MEASURE,//
                "unitPrice":submitArr[mn].UNIT_PRICE,//
                "payment":payment ,//
                "PRODUCT_TYPE":submitArr[mn].PRODUCT_TYPE,//
                "FL_PRICE":submitArr[mn].FL_PRICE,//
              }
            }

              // 
            for(var mq = 0; mq < that.SpecifyGift.length; mq++){

              STRATEGY_ID[STRATEGY_ID.length] = that.SpecifyGift[mq].strategyDetailId;//id

              OrderDetails[OrderDetails.length] = {
                "productId":  that.SpecifyGift[mq].PRODUCT_ID+"",
                "pirchaseQuantity":that.SpecifyGift[mq].giveNum ,//
                'unit':that.SpecifyGift[mq].UNIT_OF_MEASURE,//
                "unitPrice":that.SpecifyGift[mq].UNIT_PRICE,//
                "payment":0,//
                "PRODUCT_TYPE":that.SpecifyGift[mq].PRODUCT_TYPE,//
                "bottlePrice":that.SpecifyGift[mq].bottlePrice,//
                "bottleId":that.SpecifyGift[mq].bottleId,//id
                "GIFT_TYPE":that.SpecifyGift[mq].GIFT_TYPE,//
              }
            }

            // 
            for(var mc = 0; mc < that.OptionsGiftChosenCart.length; mc++){

              STRATEGY_ID[STRATEGY_ID.length] = that.OptionsGiftChosenCart[mc].strategyDetailId;//id

              OrderDetails[OrderDetails.length] = {
                "productId": that.OptionsGiftChosenCart[mc].PRODUCT_ID+"",
                "pirchaseQuantity":that.OptionsGiftChosenCart[mc].number,//
                'unit':that.OptionsGiftChosenCart[mc].UNIT_OF_MEASURE,//
                "unitPrice":that.OptionsGiftChosenCart[mc].UNIT_PRICE,//
                "payment":0,//
                "PRODUCT_TYPE":that.OptionsGiftChosenCart[mc].PRODUCT_TYPE,//
                "bottlePrice":that.OptionsGiftChosenCart[mc].bottlePrice,//
                "bottleId":that.OptionsGiftChosenCart[mc].bottleId,//id
                "GIFT_TYPE":that.OptionsGiftChosenCart[mc].GIFT_TYPE,//
              }
            }

            var postData = {
              "appUsersId": that.$store.state.userInfo.user_id,
              "businessId": that.$store.state.businessInfo.businessId,
              "orgnizationId": that.ReceiptInfo.CUSTOMER_ID,
              "REQUEST_DELIVERY": that.REQUEST_DELIVERY?that.REQUEST_DELIVERY + ' 00:00:00':'',//
              "TOTAL_QTY":that.totalNum,//
              "OrderDetails":OrderDetails,
              "CONSIGNEE_REMARK": that.REMARK_VALUE,//
              'payPrice':that.totalPayment,//
              'mjPrice':that.sumMjPrice_cru,//
              'STRATEGY_ID':STRATEGY_ID,//id
              "PAYMENT_TYPE": "",//
              "outOrderIds":that.$store.state.SumMergeOrderList,//id
              "visitId":that.visitId,//id
            }

            console.log(postData)
            this.httpRequest( "savePurchaseOrders.do",postData,function(res){
                that.ifTips = true;
                that.tips_Msg = "";
                
                that.$store.state.BackBottle={
                    g_submitArr:[],
                    totalNum:0,
                    ConsigneeData_selected:[],//
                    DeliveryAddr_selected:[],//
                    isShowSelectStore:false,//
                };//
                that.$store.state.OptionsAvailableGift=[];//
                that.$store.state.OptionsGiftChosenCart=[];//
                that.$store.state.OptionsGiftNumberList = {};// numberjson 
                that.$store.state.ORDER_REQUEST_DELIVERY='';//
                that.$store.state.ORDER_REMARK_VALUE='';//

                setTimeout(function(){

                  if(that.whoPush == 'PurchaseOrder' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
                    // 

                    that.$router.push({
                      name:'QuerySalesOrder',
                    })
                    return;
                  }

                  // osp
                  if(that.$store.state.visitObject == "OSP"){
                    that.$router.push({
                      name:'VividDisplay',
                      query:{
                        visit_whoPush:"OrderConfirm"
                      }
                    })
                    return;
                  }

                  //
                  that.$router.push({
                    name:"QueryPurchaseOrder",
                  })
                },2000)
            })
        }
        //  
        else if(this.whoPush == 'InventoryManage' && this.orderType == "OTHER_IN"){

            //  
            for (var m = 0; m < submitArr.length; m++) {
              OrderDetails[m] = {
                "productId": "" + submitArr[m].PRODUCT_ID+"",
                "pirchaseQuantity": submitArr[m].number,
                "transferOutQuantity": submitArr[m].number,
                "unitPrice": submitArr[m].UNIT_PRICE,//
                "amount":submitArr[m].number * submitArr[m].UNIT_PRICE?submitArr[m].number * submitArr[m].UNIT_PRICE:0,
              }
            }
            // id
            var consignorId = 0;
            if(that.DeliveryInfo.CUSTOMER_ID){
                // ID
                consignorId = that.DeliveryInfo.CUSTOMER_ID;
            }

            var postData = {
                "appUsersId": that.$store.state.userInfo.user_id,
                "businessId": that.$store.state.businessInfo.businessId,
                "orgnizationId": that.ReceiptInfo.CUSTOMER_ID,//id
                "consignorId":consignorId,// ID
                "transferType": "OTHER_IN",
                "pirchaseQuantity":that.totalNum,//
                "transferOutQuantity":that.totalNum,//
                "amount":that.totalPrice?that.totalPrice:0,
                "TransferReceiptDetails":OrderDetails,
                "remark":that.REMARK_VALUE,//
            }
            console.log(postData)

            this.httpRequest( "saveTransferReceipt.do",postData,function(res){
                that.ifTips = true;
                that.tips_Msg = "";

                that.$store.state.BackBottle={
                    g_submitArr:[],
                    totalNum:0,
                    ConsigneeData_selected:[],//
                    DeliveryAddr_selected:[],//
                    isShowSelectStore:false,//
                };//
                that.$store.state.OptionsAvailableGift=[];//
                that.$store.state.OptionsGiftChosenCart=[];//
                that.$store.state.OptionsGiftNumberList = {};// numberjson 
                that.$store.state.ORDER_REQUEST_DELIVERY='';//
                that.$store.state.ORDER_REMARK_VALUE='';//
                setTimeout(function(){
                  //
                  that.$router.push({
                    name:that.whoPush,
                  })
                },2000)
            })
        }
      },
      //  
      _addGift  () {
        var that = this;

        this.$store.state.OptionsAvailableGift = this.OptionsAvailableGift;//
        this.$store.state.BackBottle.g_submitArr = this.submitArr;
        this.$store.state.ORDER_REQUEST_DELIVERY = this.REQUEST_DELIVERY;//
        this.$store.state.ORDER_REMARK_VALUE = this.REMARK_VALUE;//
        console.log(this.submitArr)

        //  
        if(that.whoPush == 'HistoryVisit' && that.orderType == "SALE_OUT" ){

          that.$router.push({
            name:'AddFreeGift',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              orderConfirmattached:'orderConfirm',// 
              visitId:that.visitId,//id  
            }
          })
          return;
        }

        //  
        if(that.whoPush == 'CheckInventory' && that.orderType == "SALE_OUT" ){

          // / 
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            that.$router.push({
              name:'AddFreeGift',
              query:{
                whoPush:that.whoPush,
                orderType:that.orderType,
                visitId:that.visitId,//id  
                VisitModifyData:'VisitModifyData'
              }
            })
            return;
          }else{
            that.$router.push({
              name:'AddFreeGift',
              query:{
                whoPush:that.whoPush,
                orderType:that.orderType,
                visitId:that.visitId,//id  
              }
            })
          }
          
          return;
        }

        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          // 

          that.$router.push({
            name:'AddFreeGift',
            query:{
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          })
          return;
        }

        this.$router.push({
          name:'AddFreeGift',
          query:{
            whoPush:that.whoPush,
            orderType:that.orderType
          }
        })
      },
      //
      jian (e){

        var that = this;

        if ((parseFloat(that.submitArr[e.currentTarget.id].priceInit) -1 ).toFixed(2) < parseFloat(that.submitArr[e.currentTarget.id].priceMin)){

          that.submitArr[e.currentTarget.id].priceInit = parseFloat(that.submitArr[e.currentTarget.id].priceMin).toFixed(2);

        }else{
          that.submitArr[e.currentTarget.id].priceInit =parseFloat(parseFloat(that.submitArr[e.currentTarget.id].priceInit) - 1).toFixed(2);
        }

        that.CalculateTotalPrice();
      },
      //
      jia (e) {

        var that = this;

        if ((parseFloat(that.submitArr[e.currentTarget.id].priceInit) +1) > parseFloat(that.submitArr[e.currentTarget.id].priceMax)){

          that.submitArr[e.currentTarget.id].priceInit = parseFloat(that.submitArr[e.currentTarget.id].priceMax).toFixed(2);
        }else{
          that.submitArr[e.currentTarget.id].priceInit =parseFloat(parseFloat(that.submitArr[e.currentTarget.id].priceInit) + 1).toFixed(2);
        }

        that.CalculateTotalPrice();
      },
      //   
      entryShow (e){
        console.log(e.currentTarget.id)
        this.isHidden = true;
        this.inputNum = e.currentTarget.id;//IDX
      },
      //  
      ensure_entry (){

        var that = this;

        // return
        if (parseInt(that.inputValue).toString() == "NaN" || parseInt(that.inputValue)<0) {

          this.isHidden = false;
           return;
        }

        if(parseFloat(that.inputValue) >= parseFloat(that.submitArr[that.inputNum].priceMax)){

          that.submitArr[that.inputNum].priceInit = parseFloat(that.submitArr[that.inputNum].priceMax).toFixed(2);
          that.isHidden = false;
          that.CalculateTotalPrice();
          return;
        }

        if(parseFloat(that.inputValue) <= parseFloat(that.submitArr[that.inputNum].priceMin)){

          that.submitArr[that.inputNum].priceInit = parseFloat(that.submitArr[that.inputNum].priceMin).toFixed(2);
          that.isHidden = false;
          that.CalculateTotalPrice();
          return;
        }

        that.submitArr[that.inputNum].priceInit = parseFloat(that.inputValue).toFixed(2);
        that.isHidden = false;

        that.CalculateTotalPrice();
      },
      //  
      cancel_entry  () {
        this.isHidden = false;
      },
      // 
      CalculateTotalPrice(){

        var sumPayPrice = 0,//
        sumUNIT_PRICE = 0,//
        that = this,
        payment = 0;

        for (var mn = 0; mn < that.submitArr.length; mn++) {

          //   
          if(that.submitArr[mn].promotionStrategy=="TJCL"){

            payment = that.submitArr[mn].priceInit;

          }else{
            payment = that.submitArr[mn].DIS_PRICE;
          }

          sumPayPrice += (parseFloat(payment) * parseInt(that.submitArr[mn].number));//
       
          sumUNIT_PRICE += (parseFloat(that.submitArr[mn].UNIT_PRICE) * parseInt(that.submitArr[mn].number));//
        }

        //      
        for(var n = 0; n < that.SpecifyGift.length; n++){

          if(that.SpecifyGift[n].GIFT_TYPE == "WINE"){

            let mf = that.SpecifyGift[n];

            sumPayPrice = parseFloat(sumPayPrice + parseFloat(mf.bottlePrice) * mf.giveNum).toFixed(2);
          }
        }

        //     
        for(var n = 0; n < that.OptionsGiftChosenCart.length; n++){

          if(that.OptionsGiftChosenCart[n].GIFT_TYPE == "WINE"){

            let mr = that.OptionsGiftChosenCart[n];

            sumPayPrice = parseFloat(sumPayPrice+ parseFloat(mr.bottlePrice) * mr.number).toFixed(2);
          }
        }

        that.totalPayment = parseFloat(sumPayPrice).toFixed(2);//
        that.sumMjPrice_cru = parseFloat(sumUNIT_PRICE - sumPayPrice).toFixed(2)>=0?parseFloat(sumUNIT_PRICE - sumPayPrice).toFixed(2):0;//
      },
      // 
      modify_price(item){

        var that = this

        var inputValue = item.DIS_PRICE

        this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d+(\.\d+)?$/,
          inputErrorMessage: '',
          inputValue: inputValue,
          inputType: 'number',
          closeOnClickModal: false
        }).then(({ value }) => {

          item.DIS_PRICE = value
          that.CalculateTotalPrice()
        }).catch(() => { })

        // 
        this.$nextTick(function(){

          $(".el-message-box__input .el-input__inner").select()
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .OrderConfirm{
    width:100%;
    height:100%;
    box-sizing: border-box;
    background-color: #f4f4f4;
    overflow-x: hidden;
    .container{
      overflow-x: hidden;
      &>div{
        background-color:#fff;
        margin-bottom: 20/50rem;
        padding:0 25/50rem;
        box-sizing: border-box;
        border-radius: 10/50rem;
        font-size: 28/50rem;
        color: #222;
        .infoHead_{
          width: 100%;
          padding:20/50rem 0;
          font-weight: 700;
        }
        .infoContainer_{
          &>div{
            display: flex;
            padding:20/50rem 0;
            box-sizing:border-box;
            border-top:1/50rem solid #eee;
            &>span{
              flex-grow: 1;
              &:nth-child(1){
                width: 130/50rem;
                flex-shrink:0;
              }
              &:nth-child(2){
                width: 520/50rem;
                font-size: 28/50rem;
                color: #666;
              }
            }
          }
        }
        &.total>.infoContainer_>div{
          &>span{
            &:nth-child(2){
              font-size: 36/50rem;
              text-align: right;
            }
          }
          &.pay_typeInfo{
            &>span{
              &:nth-child(2){
                font-size: 32/50rem;
              }
            }
          }
        }
      }
    }
    .orderInfos{
      .orderList{
        display: flex;
        border-top:  1/50rem solid @PublicBorderColor;
        .listRight{
          font-size: 26/50rem;
          flex-grow: 1;
          width: 500/50rem;
          .listRightBottom{
            display: flex;
            padding-bottom:6/50rem;
            &>div{
              .listRight_rmb{
                font-size: 20/50rem;
              }
              .listRight_rmbNUM{
                font-size: 34/50rem;
                font-weight: 600;
              }
              &.listRightBottomLeft{
                width: 270/50rem;
                line-height: 40/50rem;
                flex-shrink:0;
              }
              &.listRightBottomRight{
                flex-grow: 1;
                line-height: 50/50rem;
                .priceAdjust{
                  line-height: 80/50rem;
                  overflow: hidden;
                  &>span{
                    float: left;
                  }
                  &>i{
                    font-size: 40/50rem;
                    float: left;
                    color: @PublicColor;
                  }
                  &>div{
                    float: left;
                    &.priceAdjust_price{
                      min-width:130/50rem; 
                      text-align: center;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    .LeftIcon{
      width:130/50rem;
      flex-shrink:0;
      position:relative;
      img{
        width: 90/50rem;
        position:absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
    }
    .PRODUCT_NAME{
      padding-top:15/50rem;
      line-height: 40/50rem;
      span{
        font-weight:600;
        font-size: 28/50rem;
        color: #000;
      }
    }
    .GiftInfo{
      width: 100%;
      background-color: #F1F3F4;
      .GiftContainer{
        width: 100%;
        &>div{
          &>div.haveGift{
            width: 100%;
            border-top:  1/50rem solid @PublicBorderColor;
            padding:15/50rem 0;
            &>div:first-child{
              display: flex;
              .listCenter{
                flex-grow: 1;
                line-height: 50/50rem;
                font-size: 26/50rem;
                .PRODUCT_NAME{
                  line-height: 50/50rem;
                }
              }
              .gift_right{
                text-align: center;
                width: 100/50rem;
                flex-shrink:0;
                line-height: 100/50rem;
                color: #000;
                font-weight: 600;
                font-size: 30/50rem;
              }
            }
            .bottleFee{
              padding-bottom: 0;
            }
          }
        }
        .SpecifyGiftList{
          border-bottom:  1/50rem solid #1e8cf8;
        }
        .OptionsGiftList{
          border-bottom:  1/50rem solid @PublicBorderColor;
        }
      }
      .noneGift{
        text-align: center;
        line-height: 100/50rem;
        .addGift{
          span{
            color: #fff;
            display: inline-block;
            width: 190/50rem;
            height: 60/50rem;
            line-height: 60/50rem;
            border-radius:30/50rem;
            font-size: 26/50rem;
            background-color:@PublicColor;
          }
        }
      }
    }
    .arrDate{
      .el-date-editor.el-input{
        width:100%;
        margin-bottom: 20/50rem;
      }
      .el-input{
        font-size: 32/50rem;
      }
    }
    .Remark{
      width: 100%;
      height: 100/50rem;
      padding: 10/50rem;
      box-sizing:border-box;
      font-size: 28/50rem;
      outline: none;
      border:none;
      border-radius:10/50rem;
      margin-bottom: 150/50rem;
    }
  }
  .entryNumber{
    .entryContaniner{
      width:600/50rem;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      background-color: #fff;
      font-size: 30/50rem;
      padding:10/50rem  35/50rem 0;
      border-radius: 10/50rem;
      box-sizing: border-box;
      overflow: hidden;
      &>div{
        line-height: 50/50rem;
      }
      .entry_contaniner_header{
        text-align: center;
        font-size: 32/50rem;
        font-weight:  600;
      }
      input{
        border: 1px solid #E5E5E5;
        height: 55/50rem;
        width: 100%;
        border-radius: 10/50rem;
        // text-align: center;
        margin-top: 20/50rem;
        padding:0  10/50rem;
        box-sizing:border-box;
      }
      .entryBtn{
        display: flex;
        margin: 30/50rem 0;
        overflow: hidden;
        &>div{
          flex-grow: 1;
          height:60/50rem;
          margin-left: 50/50rem;
          background-color: @PublicColor;
          color: #fff;
          text-align: center;
          line-height: 60/50rem;
          border-radius: 10/50rem;
          &.en_cancel{
            margin-left: 0;
          }
        }
      }
    }
  }
</style>


