<template>
  <div class="IntoTheShop">
	  <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>
    <!-- <componentVisitSteps :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentVisitSteps> -->
   
	  <div class="container haveHeadcontainer">
      <div class="addr"><span></span><span>{{storesData.ADDRESS1}}</span></div>
      <div class="addr">
        <span></span><textarea v-model="CurrentLocation"></textarea>
      </div>
      <div class="updateBtn"><span @click='updateBtn'></span></div>
      <div class="uploadImg">
        <div></div>
        <el-upload  :action = " HOST_xsy + 'GetVisitEnterShop.do'" :auto-upload="false"  :data="uploadParams"  list-type="picture-card"  ref="upload_ref" :limit="2" :multiple="true"   :before-upload="beforeUpload"  :on-error="uploadError" :on-success="uploadSuccess"    :on-preview="handlePictureCardPreview" >
          <!-- :headers='fileHeaderData' -->
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </div>
      <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>
    <div class="BtnComponent bottomBtn">
        <div @click="toCheckInventory"></div>
      </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentVisitSteps from '@/components/Widgets/componentVisitSteps'
  import $ from "jquery"
  export default{
    data(){
      return{
      	headerText:"",//
        storesData:{},//
        dialogImageUrl: '',//
        dialogVisible: false,//
        uploadParams:{},// 
        CurrentLocation:'',//"",//
        uploadSuccessNum:0,//
        uploadTotalNum:0,//
        uploadFailNum:0,//
        ifTips:false,
        tips_Msg:"",
        lng:0,
        lat:0,
        // lastData:'',//
        // fileHeaderData:{responseType: 'blob'},//
      }
    },
    components:{
	    componentHeader,
      componentVisitSteps
	  },
    mounted(){

      this.TelliOSORAndroidVueOfGetCurrentLocMounted();
      window.SetCurrAddress = this.SetCurrAddress;
      $('.el-upload__input').attr('capture','camera');
    },
    created(){

      var that = this;
      
      if(that.$route.query.LineId){
        that.LineId = that.$route.query.LineId;
      }

      var storesData = {
        "cellphone":that.$store.state.userInfo.cellphone,//
        "userId":that.$store.state.userInfo.user_id,//id
        "storesId":that.$store.state.CurrentStoresId,//id
      }
      // 
      that.httpRequest("GetStoreOne.do",storesData,function(storesRes){
         that.storesData = storesRes.data;
      })
      // 
      this.uploadParams.actualVisitingAddress = encodeURIComponent(this.CurrentLocation);//
    },
    methods:{
    	// 
    	f_Fanhui(){

        this.$router.go(-1);
    	},
      // iOS/Android
      SetCurrAddress:function(address, lng, lat, bdcode) {

        this.CurrentLocation = address;
        this.lng = lng
        this.lat = lat
      },
      //   
      toCheckInventory(){

        var that = this;

        if(!that.CurrentLocation.length){
          
          that.$alert("", '', {
            confirmButtonText: ''
          })
          return;
        }

        if(!$("div.uploadImg ul>li").length){

          that.$alert('', '', {
            confirmButtonText: ''
          })
          return;
        }

        var postData = {
          "actualVisitingAddress":that.CurrentLocation,//
          "cellphone":that.$store.state.userInfo.cellphone,//
          "userId":that.$store.state.userInfo.user_id,//id
          "storesId":that.$store.state.CurrentStoresId,//id
          "strLicense":that.LineId,//id
          "visitLevel":that.$store.state.visitObject,//
        }

        console.log(postData)

        this.httpRequest("GetEnterShop.do",postData,function(res){
          console.log(res)

          that.uploadParams.visitId = res.data.PRATY_VISIT_ID;//id
          that.uploadParams.userId = that.$store.state.userInfo.user_id;//id
          that.uploadParams.cellphone = that.$store.state.userInfo.cellphone;//
          that.uploadParams.businessCode = that.$store.state.businessInfo.businessCode;//

          //  id
          that.$store.state.CurrentVisitId = res.data.PRATY_VISIT_ID;

          that.uploadTotalNum = $("div.uploadImg ul>li").length;

          // 
          that.$refs.upload_ref.submit();
        })
      },
      // 
      updateBtn(){
        var that = this;

        if(!that.CurrentLocation.length){
          
          that.$alert("", '', {
            confirmButtonText: ''
          })
          return
        }
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {

          var storesUpdateData = {
            "cellphone":that.$store.state.userInfo.cellphone,//
            "userId":that.$store.state.userInfo.user_id,//id
            "storesId":that.$store.state.CurrentStoresId,//id
            "storesAdress":that.CurrentLocation,//  
            "longitude":that.lng,//
            "latitude":that.lat,//
          }
          // 
          that.httpRequest("updateCustomerAddress.do",storesUpdateData,function(storesUpdateRes){
            
            that.ifTips = true;
            that.tips_Msg = storesUpdateRes.Msg;

            setTimeout(function(){
              that.ifTips = false;
            },2000)

            var storesData = {
              "cellphone":that.$store.state.userInfo.cellphone,//
              "userId":that.$store.state.userInfo.user_id,//id
              "storesId":that.$store.state.CurrentStoresId,//id
            }
            console.log(storesData)
            // 
            that.httpRequest("GetStoreOne.do",storesData,function(storesRes){
               console.log(storesRes)

               that.storesData = storesRes.data;
            })
          }) 
        }).catch(() => {});
      },
      //     
      ChangeVisitStatus(){
        
        this.$router.push({
          name:"CreateNewVisit",
          query:{
            visit_whoPush:'IntoTheShop'
          }
        })
      },
      // 
      uploadSuccess  (response, file, fileList){

        console.log(response)

        var that = this;

        this.uploadSuccessNum ++;

        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
          // 

          that.ChangeVisitStatus();

          return;

        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 

          that.ChangeVisitStatus();
        }
      },
      // 
      uploadError  (err, file, fileList){
        console.log(err)
        var that = this;

        this.uploadFailNum++;

        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
          // 

          that.uploadSuccessNum = 0;
          that.uploadFailNum = 0;
          that.uploadTotalNum = 0;
          that.$emit('isLoading', false);

          that.$alert("", '', {
            confirmButtonText: ''
          })
          return;
        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
          // 

          that.ChangeVisitStatus();
        }
      },
      // 
      handlePictureCardPreview (file) {

          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
      },
      beforeUpload2 (file){

        var that = this;
        return new Promise(function(resolve, reject){

          var image = new Image();
          image.src = URL.createObjectURL(file);

          image.onload = function(){
            var canvas = document.createElement('canvas')
            var ctx = canvas.getContext('2d')
            var initSize = image.src.length
            var { width } = image, { height } = image
            canvas. width = width
            canvas.height = height
            ctx.fillRect(0, 0, canvas.width, canvas.height)
            ctx.drawImage(image, 0, 0, width, height)
            
            // 0.1
            var compressData = canvas.toDataURL('image/jpeg', 0.1)
            
            // base64Blob
            var byteString;
            if(compressData.split(',')[0].indexOf('base64') >= 0) {
                byteString = atob(compressData.split(',')[1])
            }else {
                byteString = unescape(compressData.split(',')[1])
            }
            var mimeString = compressData
                .split(',')[0]
                .split(':')[1]
                .split(';')[0];
            var ia = new Uint8Array(byteString.length)
            for( var i = 0; i < byteString.length; i += 1) {
                ia[i] = byteString.charCodeAt(i)
            }

            console.log(new Blob([ia], {type: mimeString}))
            var qwe = new Blob([ia], {type: mimeString});

            resolve(qwe)
          };
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .IntoTheShop{
    overflow-x: hidden;
    .container{
      font-size: 28/50rem;
      &>div{
        width: 100%;
        padding:0 20/50rem;
        box-sizing: border-box;
        overflow: hidden;
        &.addr{
          padding:10/50rem 20/50rem;
          display: flex;
          border-bottom: 1/50rem solid #e1e1e1;
          line-height: 50/50rem;
          &>span:first-child{
            min-width: 130/50rem;
            flex-shrink:0;
          }
          textarea{
            flex-grow: 1;
            height: 150/50rem;
            line-height: 50/50rem;
            font-size: 28/50rem;border:none;
            outline: none;
          }
        }
        &.updateBtn{
          text-align: center;
          span{
            margin: 30/50rem auto 10/50rem;
            display: inline-block;
            padding:0 35/50rem;
            line-height: 70/50rem;
            color: #fff;
            border-radius: 35/50rem;
            background-color: @PublicColor;
          }
        }
        &.uploadImg{
          &>div:first-child{
            margin: 20/50rem 0;
            font-weight: 600;
          }
        }
      }
    }
  }
</style>


