<template>
  <div class="CustomerVisit">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfChakanShow='true' @f_Chakan='f_Chakan' ></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="FixedHead">
	  	<div class="CustomerSearch">
	  	  <div>
	  	    <input v-model='searchValue' placeholder="">
	  	    <i class="iconfont icon-sousuo"></i>
	  	  </div>
	    </div>
	    <div class="selectCondition">
  		  <div @click='selectBoxShow("")'>
  		    <span></span>
  		    <i class="iconfont icon-xiangyou"></i>
  		    <span>{{SupplierName}}</span>
  		  </div>
  		  <div @click='selectBoxShow("")'>
  		    <span></span>
  		    <i class="iconfont icon-xiangyou"></i>
  		    <span>{{VisitRouteName}}</span>
  		  </div>
        <div @click='selectBoxShow("")'>
          <span></span>
          <i class="iconfont icon-xiangyou"></i>
          <span>{{VisitStateName}}</span>
        </div>
	    </div>
	  </div>
    <div class="CustomerList">
      <mt-loadmore  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
        <div class="CustomerItem" @click='toCreateNewVisit(index)'  v-for='(dataItem,index) in CustomerListData'  :id="index"  :key='index'>
          <div class="firmName">
            <span>{{dataItem.storesName}}</span>
            <span class="vi_status" :style='{backgroundColor:dataItem.color}'>{{dataItem.visitState}}</span>
          </div>
          <div class="firmAddr"><span>{{dataItem.address1}}</span></div>
          <div v-if="dataItem.lastvisitTime"><span></span><span>{{dataItem.lastvisitTime}}</span></div>
          <div class="HaveBeenVisit">
            <div>{{dataItem.visitCount}}</div>
            <div>
              <i class="iconfont icon-xie"></i>
              <span></span>
            </div>
            <div @click='toHistoryVisit(index)'>
              <i class="iconfont icon-chakan"></i>
              <span></span>
            </div>
          </div>
        </div>
      </mt-loadmore>
      <div class="kong"></div>
    </div>
    <div class="noData" v-if="noDataShow">
      <i class="iconfont icon-meiyoushuju-copy"></i>
      <span></span>
    </div>
	</div>
  <div class="SelectMeng" v-if='StatisticsShow'>
    <div class="CustomerVisitStatistics">
      <div class="StatisticsHead">
        <div><span></span></div>
        <div><span>{{CurrentDate}}</span></div>
      </div>
      <div>
        <div>
          <div>{{TargetVisits}}</div>
          <div></div>
        </div>
        <div>
          <div>{{TodayVisits}}</div>
          <div></div>
        </div>
        <div>
          <div>{{VisitsRate}}</div>
          <div></div>
        </div>
      </div>
      <div>
        <div class="monthVisit">&nbsp; <span>{{MonthVisits}} </span>/<span>{{MonthTargetVisits}}</span>
          <span>{{VisitsRateMonth}}</span>
        </div>
        <div class="progressBorder">
           <div class="progressMonth">{{ drawLine(this.progressMonth)}}</div>
        </div>
      </div>
      <p><span @click='StatisticsHide'></span></p>
    </div>
  </div>
	<componentSelectBox v-if="SelectBoxFlag" v-bind:SelectBoxList='SelectBoxList' @selectedItem='selectedItem' :BouncedName="BouncedName" @selectCancel="selectCancel"></componentSelectBox>
  <div class="BtnComponent bottomBtn" v-if='!noDataShow'>
    <div @click="toRecomRoute"></div>
  </div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentSelectBox from '@/components/Widgets/componentSelectBox'
  import componentReportBar from '@/components/Widgets/componentReportBar'
  import $ from "jquery"
  export default{
    data(){
      return{
      	headerText:"",//
      	SupplierName:'',//
        suppliersId:'',//id
      	VisitRouteName:'',//
        VisitRouteId:"",//id
        VisitStateName:'',//
      	SelectBoxFlag:false,//
      	BouncedName:'',//
        searchValue:'',//
        cruuentSeleText:"",//
        StatisticsShow:false,//
        CurrentDate:'',//
      	CustomerListData:[],//
        SelectBoxList:[],//
        supplierList:[],//
        VisitLineList:[],//
        noDataShow:false,//
        numberPerPage:15,//
        loadPage: 1 ,//
        allLoaded:true,//
        autoFill:false,//
        TargetVisits:'',//
        TodayVisits:'',//
        VisitsRate:'',//
        MonthTargetVisits:'',//
        MonthVisits:'',//
        progressMonth:'',
        VisitsRateMonth:'',//
      }
    },
    components:{
	    componentHeader,
	    componentSelectBox,
      componentReportBar
	  },
    created(){
      var that = this;

      var i_do = "findSuppliers.do"
      if(this.$store.state.visitObject == "OSP"){
        i_do = "findClassA.do"
      }

      var VisitStateList =  that.$store.state.VisitStateList;//

      // 
      if(that.$store.state.VisitConditionUserHabits.VisitState){

        this.VisitStateName = that.$store.state.VisitConditionUserHabits.VisitState;
      }else{
        this.VisitStateName = VisitStateList[0].name;//
      }

      var SupplierData = {
        "cellphone":that.$store.state.userInfo.cellphone,//
        "userId":that.$store.state.userInfo.user_id,//id
        "businessId":that.$store.state.businessInfo.businessId,//ID
      }
      // 
      this.httpRequest(i_do,SupplierData,function(res){
        if(res.data.length){
          var idx = that.$store.state.CustomerVisit_supplierListIdx;
          that.supplierList = res.data;
          that.SupplierName = res.data[idx].name;//
          that.suppliersId = res.data[idx].suppliersId;//id

          that.getVisitLine();
        }else{
          that.$alert('', '', {
            confirmButtonText: '',
          })
        }
      })
    },
    watch:{

      // input
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.reqStoreInfo();
      }
    },
    methods:{
    	// 
    	f_Fanhui(){

        this.$store.state.VisitConditionUserHabits = {
          VisitLine:'',//
          VisitState:'',//
        }//

    	  this.$router.push({
          name:"Index",
        })
    	},
      toRecomRoute(){

        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName;//
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//

        this.$router.push({
          name:"RecomRoute",
          query:{
            suppliersId:this.suppliersId,
            VisitRouteId:this.VisitRouteId,
          }
        })
      },
      drawLine(progress){
        setTimeout(function (){
          var a = (progress  * 100).toFixed(1);
          if(a > 100){ a = 100;}
          $('.progressMonth').css("width", a +'%');
        }, 30);
      },
      // 
      getVisitLine(){

        var that = this;
        var LineData = {
          "suppliersId":that.suppliersId,//id
        }
        // 
        that.httpRequest("GetVisitLine.do",LineData,function(LineRes){
           
           if(LineRes.data.length){
            that.VisitLineList = LineRes.data;

            if(that.$store.state.VisitConditionUserHabits.VisitLine){

              that.VisitRouteName = that.$store.state.VisitConditionUserHabits.VisitLine;
            }else{
              that.Date= new Date();
              var m = that.Date.getDay();
              that.VisitRouteName = "" + "".charAt(m);//
            }
            
            for(var i=0; i<LineRes.data.length; i++){
              if(that.VisitRouteName == LineRes.data[i].name){
                that.VisitRouteId = LineRes.data[i].visitId;
              }
            }
            // 
            that.reqStoreInfo();

           }else{

            that.$alert('', '', {
              confirmButtonText: '',
            })
          }
        })
      },
      // 
      reqStoreInfo(load){

        var that = this,
            VisitStateList = this.$store.state.VisitStateList;

        var LineData = {
          "suppliersId":that.suppliersId,//id
          "cellphone":that.$store.state.userInfo.cellphone,//
          "userId":that.$store.state.userInfo.user_id,//id
          "businessId":that.$store.state.businessInfo.businessId,//id
          "strLineId":that.VisitRouteId,//id
          "strStoreName":that.searchValue,//
          "strStoreState":that.VisitStateName,//
          "currentPage":that.loadPage,//
          "numberPerPage":that.numberPerPage,//
        }

        var i_do = "findStores.do"
        if(this.$store.state.visitObject == "OSP"){
          i_do = "findClassB.do"
        }

        // 
        that.httpRequest(i_do,LineData,function(SupRes){
          // console.log(SupRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }

          for(var i=0;i<SupRes.data.length;i++){

            for (var j = 0;j < VisitStateList.length; j++) {
              
              if(SupRes.data[i].visitState == VisitStateList[j].name){
                SupRes.data[i].color = VisitStateList[j].color;
              }
            }
          }

          // 
          if (that.loadPage > 1) {
            if(SupRes.data.length){
              that.CustomerListData = that.CustomerListData.concat(SupRes.data);
            }
          } else {
            that.CustomerListData = SupRes.data;
          }
          // 
          if (SupRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
           
          if(that.CustomerListData.length){
            that.noDataShow = false;
           
          }else{
            that.noDataShow = true;
          }
        })
      },
    	// 
    	f_Chakan(){
        var that = this;
        if(!this.CurrentDate){
          this.CurrentDate = this.getNowTime('getDate');
        }
         //
       var VisitData = {
          "appUsersId": that.$store.state.userInfo.user_id,//id
          "businessId": that.$store.state.businessInfo.businessId,//ID
          "orgnizationId": that.suppliersId,
        }
        this.httpRequest("visitCountReport.do",VisitData,function(res){
        console.log(res.data)
          if(res.data){
            that.TargetVisits = res.data.targetSum;//
            that.TodayVisits = res.data.visitSum;//
            that.MonthTargetVisits = res.data.targetSumMonth;//
            that.MonthVisits = res.data.visitSumMonth;//

            if(res.data.targetSum == 0 || res.data.targetSumMonth == 0){
              that.VisitsRate = "100%";
              that.progressMonth = 1;
            }else{
              that.VisitsRate = (that.TodayVisits/that.TargetVisits  * 100).toFixed(1)+'%';//
              that.progressMonth = that.MonthVisits/that.MonthTargetVisits;
              that.VisitsRateMonth = (that.progressMonth  * 100).toFixed(1)+'%';//
            }
           
            that.StatisticsShow = true;

          }else{
            that.$alert('', '', {
              confirmButtonText: '',
            })
          }
        })
    	},
      //  
      StatisticsHide(){
        this.StatisticsShow = false;
      },
      //  
    	selectBoxShow(text){
    		this.BouncedName = text;
    		this.SelectBoxFlag=true;

        if(text == ''){
          this.SelectBoxList = this.supplierList;
          this.cruuentSeleText = '';

        }else if(text == ''){
          this.SelectBoxList = this.VisitLineList;
          this.cruuentSeleText = '';

        }else if(text == ''){
          this.SelectBoxList = this.$store.state.VisitStateList;
          this.cruuentSeleText = '';
        }
    	},
      // 
      selectedItem(index){

        var that = this;
        if(this.cruuentSeleText == ''){
          this.SupplierName = this.supplierList[index].name;
          this.suppliersId = this.supplierList[index].suppliersId;
          this.loadPage = 1;
          this.allLoaded = false;
          this.$store.state.CustomerVisit_supplierListIdx = index;
          // 
          that.getVisitLine();

        }else if(this.cruuentSeleText == ''){
          this.VisitRouteName = this.VisitLineList[index].name;
          this.VisitRouteId = this.VisitLineList[index].visitId;
          this.loadPage = 1;
          this.allLoaded = false;
          // 
          that.reqStoreInfo();

        }else if(this.cruuentSeleText == ''){
          this.VisitStateName = this.$store.state.VisitStateList[index].name;
          this.loadPage = 1;
          this.allLoaded = false;
          // 
          that.reqStoreInfo();
        }

        this.SelectBoxFlag = false;
      },
    	//  
    	selectCancel(){
    	  this.SelectBoxFlag = false;
    	},
      //   
      toCreateNewVisit(index){

        var that = this;
  
        this.$store.state.CurrentStoresId = this.CustomerListData[index].storesId;//id
        
        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName ;//
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//

        if(that.CustomerListData[index].visitState == ""){

          var histData = {
            "storesId":this.$store.state.CurrentStoresId,//id
            "startTime":'',//
            "endTime":'',//
            "currentPage":1,//
            "numberPerPage":1,//
          }
          // 
          that.httpRequest("GetVisitHistory.do",histData,function(histRes){

            var m = histRes.data[0];

            if(m.VISIT_STATES != ""){

              that.$alert('""', '', {
                confirmButtonText: '',
              })
              return;
            }

            //  id
            that.$store.state.CurrentVisitId = m.PRATY_VISIT_ID;

            if(m.VISIT_STEPS == ''){
              that.$router.push({
                name:"CreateNewVisit",
              })
            }else if(m.VISIT_STEPS == ''){
              that.$router.push({
                name:"CheckInventory",
              })
            }else if(m.VISIT_STEPS == ''){
              that.$router.push({
                name:"ChooseGoods",
                query:{ 
                  CurrentStoresId:that.$store.state.CurrentStoresId,//id
                  visitId:that.$store.state.CurrentVisitId,//id
                  whoPush:"HistoryVisit",
                  orderType:"SALE_OUT",
                }
              })
            }else if(m.VISIT_STEPS == ''){
               that.$router.push({
                name:"VividDisplay",
              })
            }else if(m.VISIT_STEPS == ''){
              that.$router.push({
                name:"LeaveStore",
              })
            }else{
             
              that.$router.push({
                name:"LeaveStore",
                query:{
                  pageName:'',
                }
              })
            }
          })
        }else{

          that.$router.push({
            name:"IntoTheShop",
            query:{
              LineId:that.VisitRouteId
            }
          })
        }
      },
      //   
      toHistoryVisit(index){
        event.stopPropagation();

        var cruuentCont = this.CustomerListData[index];

        this.$store.state.CurrentStoresId = cruuentCont.storesId;//id

        this.$store.state.CurrentStoresHistoryInfo = {
          storesName:cruuentCont.storesName,//
          lastvisitTime:cruuentCont.lastvisitTime,//
          visitCount:cruuentCont.visitCount,//
        }

        this.$store.state.VisitConditionUserHabits.VisitLine = this.VisitRouteName ;//
        this.$store.state.VisitConditionUserHabits.VisitState = this.VisitStateName;//

        this.$router.push({
          name:"HistoryVisit",
        })
      },
      // 
      loadTop() {

        this.loadPage = 1;

        this.allLoaded = false;

        this.reqStoreInfo("TopLoad");
      },
      // 
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.reqStoreInfo("BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CustomerVisit{
  	overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
    }
  	.FixedHead{
      background-color:#fff;
      z-index: 50;
  	  width: 100%;
  	  position:fixed;
  	  top: 89/50rem;
  	  left: 0;
  	  .CustomerSearch{
  	    .TopSearchBox;
  	  }
      .selectCondition{
        &>div{
          height: 80/50rem;
          &>span:nth-child(3){
            text-align: right;
            width: 430/50rem;
            overflow: hidden;
            text-overflow: ellipsis;/**/
            white-space: nowrap;/**/
          }
        }
      }
  	}
  	.CustomerList{
      height: 100%;
      overflow: scroll;
      padding-top: 430/50rem;
      box-sizing:border-box;
      .CustomerItem{
        border-bottom:10/50rem solid #f0f0f0;
        font-size: 28/50rem;
        padding-left: 20/50rem;
        box-sizing:border-box;
        color: #666;
        overflow: hidden;
        &>div{
          line-height: 55/50rem;
          overflow: hidden;
        }
        .firmName{
          line-height: 65/50rem;
          &>span{
            &:first-child{
              font-size: 30/50rem;
              color: #000;
              font-weight: 600;
            }
            &.vi_status{
              float: right;
              background-color: #5965D8;
              padding:0  25/50rem;
              margin-top: 7.25/50rem;
              height: 50/50rem;
              line-height: 50/50rem;
              color: #fff;
              border-radius: 25/50rem 0 0 25/50rem;
            }
          }
        }
        .firmAddr{
          padding-right:20/50rem;
          line-height: 45/50rem;
        }
        .HaveBeenVisit{
          margin-top: 5/50rem;
          line-height: 60/50rem;
          &>div{
            float: right;
            overflow: hidden;
            margin-right: 25/50rem;
            i{
              float: left;
              font-size: 30/50rem;
            }
            span{
              float: left;
              margin-left: 10/50rem;
            }
            &:first-child{
              float: left;
            }
            &:last-child{
              i{
                font-size: 40/50rem;
              }
            }
          }
        }
      }
  	}
    .SelectMeng{
      font-size: 32/50rem;
      .CustomerVisitStatistics{
        width: 710/50rem;
        margin:0;
        position:absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        .monthVisit{
          font-size: 28/50rem;
          padding: 20/50rem 25/50rem 0 25/50rem;
          color: #666;
          span:nth-child(3){
            float: right;
          }
        }
        .progressBorder{
          height: 12/50rem;
          border:1/50rem solid  @PublicColor;
          margin: 8/50rem  20/50rem  0  20/50rem;
          padding: 5/50rem 5/50rem 5/50rem 5/50rem;
          border-radius: 10/50rem;
          .progressMonth{
            height: 12/50rem;
            background-color:  @PublicColor;
            border-radius: 10/50rem;
          }
        }
        
        p{
          text-align: center;
          span{
            font-size: 34/50rem;
            font-weight: 600;
            padding:20/50rem 30/50rem;
            display: inline-block;
          }
          i{
            font-size: 45/50rem;
          }
        }
      }
    }
    .BtnComponent{
      padding:0 250/50rem;
    }
  }
</style>


