<template>
  <div class="Home">
	<div class="container">
	  <div class='headImg'>
		<div class='background'></div>
		<div class='background_blank'></div>
		<div class='head'>
		  <div class="image"></div>
		  <img  class="userinfo-avatar" src="static/img/Home/DefaultAvatar.png">
		  <div class='userName'>{{nickname}}</div>
		  <div class='userName'>{{cellphone}}</div>
		</div>
		<div class="stripInfo">
		  <div class="myOrder" v-if='$store.state.G_AppOrWX == "WX"'>
		  	<div class="title"></div>
		  	<div class="orderState">
		  	  <div @click="toall"><i class="iconfont icon-quanbudingdan"></i><span></span></div>
		  	  <div @click="topay"><i class="iconfont icon-daifukuan"></i><span></span></div>
		  	  <div @click="toget"><i class="iconfont icon-daishouhuo"></i><span></span></div>
		  	  <div @click="finish"><i class="iconfont icon-yiwancheng"></i><span></span></div>
		  	  <div @click="cancel"><i class="iconfont icon-yiquxiao"></i><span></span></div>
		  	</div>
		  </div>
		  <div>
		   	<div>
		   	  <div><i class="iconfont icon-yewu" ></i></div>
			  <div></div>
		   	</div>
		    <div>
		      <div>{{businessname}}</div>
		      <div></div>
			</div>
		  </div>
		  <div v-if='$store.state.userInfo.visitType && $store.state.G_AppOrWX != "WX" && $store.state.CurrentRole == "ospSalesman"' @click='UpdateType'>
		   	<div>
		   	  <div><i class="iconfont icon-yonggongzongliang" ></i></div>
			  <div></div>
		   	</div>
		    <div>
		      <div class="updateClass">{{$store.state.userInfo.visitType}}</div>
		      <div><i  class="iconfont icon-xiangyou"></i></div>
			</div>
		  </div>
		  <div v-if='$store.state.CurrentRoleData.RePassword' @click="toPassword">
		  	<div>
		  	  <div><i class="iconfont icon-xiugaimima"></i></div>
		  	  <div></div>
		  	</div>
		  	<div>
		  	  <div></div>
		  	  <div><i class="iconfont icon-xiangyou"></i></div>
		  	</div>
		  </div>

		  <div v-if='$store.state.CurrentRoleData.AppQuestion' @click="toQuestion">
		  	<div>
		      <div><i class="iconfont icon-wenhao"></i></div>
		      <div></div>
		  	</div>
		  	<div>
		  	  <div></div>
		  	  <div><i class="iconfont icon-xiangyou"></i></div>
		  	</div>
		  </div>

		  <div @click='updateAppVersion'>
		  	<div>
		      <div><i class="iconfont icon-guanyu"></i></div>
		      <div>{{ $store.state.G_AppOrWX == "WX" ? "" : "" }}</div>
		  	</div>
		  	<div>
		  	  <div class="updateClass">{{VersionNum}}.{{$store.state.version}}</div>
		  	  <div><i class="iconfont icon-xiangyou"></i></div>
		  	</div>
		  </div>
		</div>
		<div class='Exit_logon'>
		  <i class="iconfont icon-icon-yxj-switch-account" @click="exit"></i>
		  <div><span @click="exit"></span></div>
		</div>
	  </div>
	  <div class="SelectMeng" v-if='UpdateVisitTypeHide'>
	    <div class="SelectWarehouses SelectMengContent">
	      <div class="SelectTitle SelectMengTitle"></div>
	      <div class="SelectMengItem">
	        <div class="RadvisitType">
	          <div>&nbsp;</div>
	          <template>
				<el-radio v-model="radvisitType" label=""></el-radio>
				<el-radio v-model="radvisitType" label=""></el-radio>
			  </template>
		      <div>&nbsp;</div>
	        </div>
	      </div>
	      <div class="selectBtn">
	        <div @click="Cancel"></div>
	        <div @click="Confirm"></div>
	      </div>
	    </div>
	  </div>
	</div>
	<componentFooter v-if='$store.state.G_AppOrWX != "WX"'></componentFooter>
  </div>
</template>
<script type="text/javascript">
  import componentFooter from '@/components/componentFooter'
  import componentHeader from '@/components/componentHeader'
  export default{
	name:"HomeIndex",
	data(){
	  return{
	  	headerText:"",//
	  	nickname:"",//
	  	cellphone:"",//
	  	VersionNum: "",//
	  	businesscode:"",//
	  	UpdateVisitTypeHide:false,//
	  	radvisitType:"",//
	  	storeCode:'',//
	  }
	},
	components:{
	  componentHeader,
	  componentFooter,
	},
	created(){
	  this.nickname = this.$store.state.userInfo.userName;
	  this.cellphone = this.$store.state.userInfo.cellphone;
	  this.VersionNum =  this.$store.state.App_Version;
	  this.businessname = this.$store.state.businessInfo.name;
	  this.radvisitType = this.$store.state.userInfo.visitType;
	},
	methods:{
	   
	  // App
	  updateAppVersion(){
		try { CallAndroidOrIOS("APPVUE") } catch(error) { }
	  	try { CallAndroidOrIOS.callAndroid("APPVUE") } catch(error) { }
	  },
	  //
	  toall(){
	  	var that = this;
	  	that.$router.push({
	  	  name:"QuerySalesOrder",
	  	  query:{
	  	  	orderState:"",
	  	  },
	    })
	  },
	  //
	  topay(){
	  	var that = this;
	  	that.$router.push({
	  	  name:"QuerySalesOrder",
	  	  query:{
	  	  	orderState:"NEW",
	  	  },
	    })
	  },
	  //
	  toget(){
	  	var that = this;
	  	that.$router.push({
	  	  name:"QuerySalesOrder",
	  	  query:{
	  	  	orderState:"PAID",
	  	  },
	    })
	  },
	  //
	  finish(){
	  	var that = this;
	  	that.$router.push({
	  	  name:"QuerySalesOrder",
	  	  query:{
	  	  	orderState:"FINISH",
	  	  },
	    })
	  },
	  //
	  cancel(){
	  	var that = this;
	  	that.$router.push({
	  	  name:"QuerySalesOrder",
	  	  query:{
	  	  	orderState:"CANCEL",
	  	  },
	    })
	  },
	  //
	  UpdateType(){
	  	this.UpdateVisitTypeHide = true;
	  },
	   //  
      Confirm(){
        var that = this;
        that.UpdateVisitTypeHide = false;
        if(that.radvisitType == that.$store.state.userInfo.visitType){
	      that.$alert("", '', {
            confirmButtonText: '',
          }) 
        }else{
          var way;
          if(that.radvisitType ==  ""){
          	way = "car";
          }
    	  if(that.radvisitType ==  ""){
    	  	way = "walk";
    	  }
	      var UpdateData = {
	        "appUsersId":that.$store.state.userInfo.user_id,//id
	        "way":way,//
	      }
	      that.httpRequest("updateVisitWay.do",UpdateData,function(UpdateRes){
	       
	        if(UpdateRes.status==1){
	          that.$store.state.userInfo.visitType = that.radvisitType;
	          that.$alert("", '', {
	            confirmButtonText: '',
	          }) 
	        }else {
	          that.$alert("", '', {
	            confirmButtonText: '',
	          }) 
	        }
	      }) 
	    }
      },
      //  
      Cancel(){
        this.UpdateVisitTypeHide = false;
        this.radvisitType = this.$store.state.userInfo.visitType;
      },
	  //   
	  toPassword(){
	  	this.$router.push({
	  	  name:"RePassword",
	  	})
	  },
	  //  App 
	  toQuestion(){

	  	this.$router.push({
	  	  name:"AppQuestion",
	  	})
	  },
	  //  
	  exit(){

	    var that = this;

	    this.$confirm('?', '', {
	      confirmButtonText: '',
	      cancelButtonText: '',
	      type: 'warning'
	    }).then(() => {
  
  		  if(that.$store.state.G_AppOrWX == 'WX'){
  		  	
  		  	that.getWXCookie("e");

  		  	if(that.storeCode){
  		  	  that.setWXCookie(that.storeCode,'',30);
  		  	}
  		  	
  		  	that.$router.push({
		  	  name:"WXLogin"
		    })
  		  }else{
  		  	that.$router.push({
		  	  name:"Login"
		    })
  		  }
		}).catch(() => { });
	  }
	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Home{
	height: 100%;
	overflow-x: hidden;
	background-color: #F4F4F4;
	header{
	  color: #fff;
	  background: linear-gradient(to bottom,@PublicColor,#419DF9);
	  border-bottom:none;
	}
	.container{
	  background-color: #F4F4F4;
	  .headImg{
		width: 100%;
		height: 100%;
		overflow: scroll;
		position: relative; 
		font-size: 26/50rem;
		.userName{
		  font-size: 34/50rem;
		  color: @PublicColor;
		  text-align: center;
		  margin-bottom: 40/50rem;
		  i{
		  	font-size: 35/50rem;
		  	margin-left: 10/50rem;
		  	color: #777;
		  }
		}
		.background{
		  width:100%;
		  height: 350/50rem;
		  background: linear-gradient(to bottom,#419DF9,#92C7FC);
		}
		.background_blank{
		  width: 650/50rem;
		  height: 310/50rem;
		  position: absolute;
		  top:150/50rem;
		  left: 50/50rem;
		  background-color: #fff;
		  z-index: 33;
		  border-radius: 10/50rem;
		}
		.head{
		  width:100%;
		  height: 350/50rem;
		  position: absolute;
		  left: 0/50rem;
		  z-index: 45;
		  top: 75/50rem;
		}
		.userinfo-avatar{
		  width: 150/50rem;
		  height: 150/50rem;
		  border-radius: 50%;
		  display: block;
		  margin: 0 auto 40/50rem; 
		}
		.stripInfo{
		  margin-top: 140/50rem;
		  font-size: 28/50rem;
		  .myOrder{
		  	background-color: #fff;
		  	height: 140/50rem;
		  	margin-bottom: 20/50rem;
		  	.title{
		  	  font-size: 20/50rem;
		  	  font-weight: 550;
		  	  line-height: 50/50rem;
		  	  padding-left: 20/50rem;
		  	}
		  	.orderState{
		  	  float: left;
		  	  width: 95%;
		  	  text-align: center;
		  	  padding-left: 2.5%;
		  	  &>div{
		  	  	float: left;
		  	  	width: 19%;
		  	  	i{ 
		  	  	  font-size: 50/50rem; 
		  	  	  color:@PublicColor;
		  	  	}
		  	  	span{
		  	  	  display: block; 
		  	  	  font-size: 24/50rem;
		  	  	}
		  	  }
		  	}
		  }
		  &>div:not(.myOrder){
		  	display: flex;
		  	width: 100%;
		    line-height: 40/50rem;
		    background-color: #fff;
		    margin-bottom: 20/50rem;
		    padding:20/50rem 20/50rem;
		    box-sizing: border-box;
		  	&>div:nth-child(1){
			  flex-shrink: 0;
			  display: flex;
			  &>div:nth-child(1){
			  	min-width: 60/50rem;
			    flex-shrink: 0;
			    text-align: left;
			    i{
			      color: @PublicColor;
			      font-size: 36/50rem;
			    }
			    i.icon-wenhao{
			      font-size: 42/50rem;
			    }
			    i.icon-yonggongzongliang{
			      font-size: 40/50rem;
			    }
			  }
		  	}
		  	&>div:nth-child(2){
			  flex-grow:1;
			  padding-left:20/50rem;
			  box-sizing:border-box;
			  display: flex;
			  &>div:nth-child(1){
				flex-grow: 1;
				&.updateClass{
				  text-align: right;
				}
			  }
			  &>div:nth-child(2){
				min-width: 60/50rem;
			    flex-shrink: 0;
			    text-align: center;
			  }
		  	}
		  }
		}
	    //*  */
	    .Exit_logon{
		  margin-top: 50/50rem;
		  margin-bottom: 120/50rem;
		  &>i{
		  	display: block;
		  	margin: 0 auto;
		  	width: 114/50rem;
		  	font-size: 114/50rem;
		  	color: @PublicColor;
		  }
		  &>div{
		  	text-align: center;
		  	color: @PublicColor;
		  	font-size: 28/50rem;
		  	margin-top: 10/50rem;
		  }
		}
	  }
	}
  }
</style>


