<template>
  <div class="CreateNewVisit">
	<componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui'></componentHeader>

	<div class="container haveHeadcontainer">
	  <div class="VisitDate">
	  	<span></span>
	  	<span>{{CurrentDate}}</span>
	  </div>
	  <div class="orderInfo">
		<div class="infoContainer">
		  <div>
		  	<span></span>
		  	<span>{{storesData.CODE}}</span>
		  </div>
		  <div>
		  	<span></span>
		  	<span>{{storesData.NAME}}</span>
		  </div>
		  <div>
		  	<span></span>
		  	<span>{{storesData.CUSTOMER_GRADE}}</span>
		  </div>
		 <!--  <div>
		  	<span></span>
		  	<span>{{storesData.CUSTOMER_STATUS}}</span>
		  </div> -->
		  <div>
		  	<span> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
		  	<span>{{storesData.CHANNEL}}</span>
		  </div>
		  <div>
		  	<span></span>
		  	<span>{{storesData.VISIT_ROUTE}}</span>
		  </div>
		  <!-- <div>
		  	<span></span>
		  	<span>{{storesData.WEEKLY_VISITING_FREQUENCY}}</span>
		  </div> -->
		 <!--  <div>
		  	<span>/</span>
		  	<span>{{storesData.SALES}}</span>
		  </div> -->
	  	</div>
	  	<div class="infoContainer">
	  	  <div>
	  	  	<span></span>
	  	  	<span>{{storesData.CONTACT_NAME}}</span>
	  	  </div>
	  	  <div>
	  	  	<span></span>
	  	  	<span>{{storesData.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='storesData.CONTACT_TEL' @click="callPhone(storesData.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span>
	  	  </div>
	  	  <div class="ADDRESS1">
	  	  	<span></span>
	  	  	<span>{{storesData.ADDRESS1}}</span>
	  	  </div>
	    </div>
	  </div>
	  <div class="v_remark">
	  	<textarea v-model="v_remark" placeholder=""></textarea>
	  </div>
	  <div class="BtnComponent bottomBtn">
	  	<div @click='toModifyCustomerInfo'></div>
	  	<div @click='toIntoTheShop'></div>
	  </div>
	</div>
  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"",//
      	CurrentDate:'',//
      	storesData:{},//
      	v_remark:"",//
      }
    },
    components:{
  	  componentHeader,
  	},
    created(){

      var that = this;
    	
      this.CurrentDate = this.getNowTime('getDate');
      var storesData = {
        "cellphone":that.$store.state.userInfo.cellphone,//
        "userId":that.$store.state.userInfo.user_id,//id
        "storesId":that.$store.state.CurrentStoresId,//id
      }
      // 
      that.httpRequest("GetStoreOne.do",storesData,function(storesRes){
         that.storesData = storesRes.data;
      })
    },
    methods:{
      // 
      f_Fanhui(){

      	if(this.$route.query.visit_whoPush && this.$route.query.visit_whoPush =='IntoTheShop'){
          this.$router.push({
            name:"CustomerVisit",
          })
        }else{
          this.$router.go(-1);
        }
      },
      //  
      toModifyCustomerInfo(){
      	console.log(this.storesData)
      	this.$router.push({
          name:"ModifyCustomerInfo",
          query:{
          	// "storesId":this.$store.state.CurrentStoresId,//id
          	"storeInfo":this.storesData,//
          },
        })
      },
      //   
      toIntoTheShop(){

	      var that = this;
	    	
	      this.CurrentDate = this.getNowTime('getDate');
	      var sureData = {
          "cellphone":that.$store.state.userInfo.cellphone,///
	      	"types":"CONFIRM",
	      	"visitId":that.$store.state.CurrentVisitId,//id
	        "remarks":that.v_remark,//
	      }
	      // 
	      that.httpRequest("GetVisitPicStatus.do",sureData,function(sureRes){

          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){

            that.$router.go(-1);
            
          }else{
            that.$router.push({
              name:"CheckInventory",
              query:{
                  visit_whoPush:"CreateNewVisit"
                }
            })
          }
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .CreateNewVisit{
  	overflow-x: hidden;
  	min-height: 100%;
  	padding:16/50rem;
    box-sizing: border-box;
  	background-color: @GrayBackground;
  	.VisitDate{
  	  text-align: center;
  	  line-height: 70/50rem;
  	}
  	.orderInfo{
  	  .infoContainer{
		&>div{
		  display: flex;
		  &>span:first-child{
	        min-width: 130/50rem;
            flex-shrink:0;
	      }
	      span:nth-child(2){
	        flex-grow: 1;
	      }
		}
  	  }
  	  span{
  	  	color: #000;
  	  }
  	}
  	.v_remark{
  	  textarea{
  	    width: 100%;
  	    height: 120/50rem;
  	    border-radius:15/50rem;
  	    outline: none;
  	    padding:16/50rem;
  	    box-sizing:border-box;
  	    font-size: 28/50rem;
  	    border:none;
  	  }
  	}
  }
</style>


