<template>
  <div class="ChooseGoods">
    <componentHeader :headerText="headerText" :IfFanhuiShow= ' $store.state.G_AppOrWX == "WX" ? false :true' @f_Fanhui='f_Fanhui' :IfTextShow='IfTextShow' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
    <div class="noData" v-if="noDataShow">
      <i class="iconfont icon-meiyoushuju-copy"></i>
      <span>,</span>
    </div>
    <div class="container" v-if="!noDataShow">
      <div class="fixedTop">
        <div class="HorizontalNav">
          <div class="BrandItem" v-for='(BrandItem,BrandIdx) in HnavBrandList'  :id="BrandIdx"  :key='BrandIdx'    :class="{ Hnav_active:HnavBrandIndex == BrandIdx}" @click="BrandItemClick(BrandIdx)">{{BrandItem.PRODUCT_BRAND}}
          </div>
        </div>
        <div v-if="isShowSelectStore" class="selectStore">
          <div @click="toSelectStores">{{ returnOrtransrerText }}{{SelectedInfoName}}</div>
        </div>
        <!--  -->
        <div v-if="isShowSelectReceiveAndDeliver" class="selectReceiveAndDeliver">
          <div class="select_Receive" @click='toSelectSalesMan("Receive")'>
            <div>
              <span></span>
              <span>{{receORdeliv_consignee}}</span>
              <i v-if='!IFreceORdeliv_transition' class="iconfont icon-xiangyou"></i>
            </div>
          </div>
          <div class="select_transition" @click='receORdeliv_transition'>
            <i class="iconfont icon-jiaohuan"></i>
          </div>
          <div class="select_Deliver" @click='toSelectSalesMan("Deliver")'>
            <div>
              <span></span>
              <span>{{receORdeliv_consignor}}</span>
              <i v-if='IFreceORdeliv_transition' class="iconfont icon-xiangyou"></i>
            </div>
          </div>
        </div>
        <div class="OtherNav">
          <div class="CSearch">
            <div>
              <i class="iconfont icon-sousuo"></i>
              <input v-model='searchValue' placeholder="">
            </div>
          </div>
          <div class="otherInfoBtn"><div @click='Address'>/</div></div>
        </div>
      </div>
      
      <div class="LowerBody">
        <div class="VerticalNav">
          <div v-for='(TypeItem,TypeIdx) in VnavTypeList'  :id="TypeIdx"  :key='TypeIdx'
          :class="{ Vnav_active:VnavTypeIdx == TypeIdx}" @click="TypeListClick(TypeIdx)">{{TypeItem.PRODUCT_TYPE}}</div>
          <span class="navKong"></span>
        </div>
        <div class="GoodsDisplay">
          <mt-loadmore  bottomPullText="" bottomDropText="" bottomLoadingText=""  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div class='selectGoodsList' v-for='(dataItem,index) in goodsList'  :id="index"  :key='index' @click='showPromotionInfo(index)'>
            <div class="selectGoodsListItem">
              <div class='LeftIcon'>
                <img :src="dataItem.PRODUCT_PIC?dataItem.PRODUCT_PIC:'static/img/Order/goods.png'" alt="">
              </div>
              <div class='listCenter'>
                <div class="goodsNumame">
                  <span>{{dataItem.PRODUCT_NAME}}</span>
                </div>
                <div class="specifications">
                  <span></span><span>{{dataItem.PRODUCT_DESC}}</span>
                </div>
                <div class="specifications">
                  <span></span><span>{{dataItem.UNIT_OF_MEASURE_STR}}</span>
                </div>
                <div class="moneyGroup">
                  <div>
                    <span>{{dataItem.DIS_PRICE}}</span>
                    <div v-if="whoPush == 'InventoryManage' && orderType == 'TRANSFER'" class="InventoryQtyClass"> : {{dataItem.DB_NUM}}</div>
                    <div v-if="orderType == 'SALE_OUT'" class="InventoryQtyClass"> : {{dataItem.KC_NUM}}</div>
                  </div>
                  <div class='listLast'>
                    <i class="iconfont icon-Untitled-" :id="index"  @click='jian'></i>
                    <span @click='entryShow($event, number[dataItem.PRODUCT_ID])' :id='index' data-element="" >{{number[dataItem.PRODUCT_ID]}}</span>
                    <i class="iconfont icon-jia1" :id="index"  @click='jia'></i>
                  </div>
                </div>
              </div>
            </div>
            <div class="promotionInfo" v-if='dataItem.CPCL.length'>
              <div class="promotionShow"><i class="iconfont icon-xiangxiaxialakuang"></i><span></span></div>
              <div class="promotionHide">
                <div @click='promotionInstruction(index,PIindex)'  v-for='(PIItem,PIindex) in dataItem.CPCL'  :id="PIindex"  :key='PIindex'>
                  <div>
                    <div class="PI-icon-d">
                      <i class="iconfont icon-cuxiao"></i>
                    </div>
                    <div class="PI-name-d">
                      <span>{{ PIItem.name }}</span>
                      <span class="promotionDeShow" v-if='PIItem.detail.length' style="color: #666;"></span>
                    </div>
                    <div class="PI-nav-d" v-if='PIItem.detail.length'>
                      <i class="iconfont icon-xiangxiaxialakuang"></i>
                    </div>
                  </div>
                  <div class="promotionDeHide">
                    <div class="promotionInstru" >
                    <div v-for='(DeItem,Deindex) in PIItem.detail' :id="Deindex" :key='Deindex'>
                      <span>{{DeItem.product_name}}</span> <span>&nbsp;&nbsp;&nbsp;{{DeItem.num}}</span><span>{{DeItem.unitStr}}</span>
                    </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          </mt-loadmore>
        </div>
      </div>
    </div>
    <div class="SelectMeng" v-if='AddressShow'>
      <div class="AddressConfirm">
        <div class="AddressConfirmHead">
          <span>/</span>
          <i class="iconfont icon-guanbi" @click='AddressShowHide'></i>
        </div>
        <div class="Info">
          <div v-if='ReceiptInfo.CUSTOMER_NAME||ReceiptInfo.CONTACT_NAME||ReceiptInfo.CONTACT_TEL||ReceiptInfo.CUSTOMER_ADDRESS' class="ReceiveInfo">
            <div><span>&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{ReceiptInfo.CUSTOMER_NAME}}</span></div>
            <div><span></span><span>{{ReceiptInfo.CONTACT_NAME}}</span><span>&nbsp;&nbsp;&nbsp;&nbsp;{{ReceiptInfo.CONTACT_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='ReceiptInfo.CONTACT_TEL' @click="callPhone(ReceiptInfo.CONTACT_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span></span><span>{{ReceiptInfo.CUSTOMER_ADDRESS}}</span></div>
            <span></span>
            </div>
          <div v-if="DeliveryInfo.CUSTOMER_NAME||DeliveryInfo.CONTACT_NAME||DeliveryInfo.CUSTOMER_TEL||DeliveryInfo.CUSTOMER_ADDRESS" class="DeliveryInfo">
            <div><span>&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{DeliveryInfo.CUSTOMER_NAME}}</span></div>
            <div><span></span><span>{{DeliveryInfo.CONTACT_NAME}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span>{{DeliveryInfo.CUSTOMER_TEL}}&nbsp;&nbsp;&nbsp;<i v-if='DeliveryInfo.CUSTOMER_TEL' @click="callPhone(DeliveryInfo.CUSTOMER_TEL)" class="iconfont icon-dianhua-copy"></i></span></div>
            <div><span></span><span>{{DeliveryInfo.CUSTOMER_ADDRESS}}</span></div>
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <!--  /  -->
    <div class="SelectMeng" v-if='IFSelectReceiveMeng'>
      <div class="IFSelectReceiveMeng">
          <div>{{ReceiveORDeliverHeaderName}}</div>
          <div>{{ReceiveORDeliverName}}</div>
          <i class="iconfont icon-guanbi" @click='SelectReceiveMengHide'></i>
      </div>
    </div>
    <!--  -->
    <div class='foot_cart'>
      <div class='foot_top'   @click="animat_cartShowIf" >
        <div class='totalNum'>
          <div><i class="iconfont icon-shoppingCart-full"></i></div>
          <div>
            <div> {{totalPrice}}</div>
            <div>{{totalNum}}</div>
          </div>
        </div>
        <div class='submit' @click='submitClick' ></div>
      </div>
      <div class='foot_bottom'>
        <div class="OutermostGray"  @click="animat_cartShowIf"></div>
        <div class="footBo_move">
        <div class="footBottom_head"></div>
        <div class="footBottom_fixedBox">
          <div class='cartGoodsList' v-for='(cartItem,carindex) in submitArr'  :id="carindex"  :key='carindex' >
            <div class='cartListLeft'>{{ cartItem.PRODUCT_NAME }}</div>
            <div class='listLast'>
              <i class="iconfont icon-Untitled-" :id="carindex" @click='jian2'></i>
              <span @click='entryShow($event, cartItem.number)' :id="carindex"  data-element="">{{cartItem.number}}</span>
              <i class="iconfont icon-jia1" :id="carindex" @click='jia2'></i>
            </div>
          </div>
        </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class='entryNumber' v-if="isHidden">
      <div class='entry_contaniner'>
        <div class='entry_contaniner_header'></div>
        <div class='clearNum'>
          <input id='autofocus_input' bindinput='goodsNum' ref='autofocus_input'  v-model='inputValue' type='number'></input>
          <i @click='clearNum' v-if="inputValue" class='iconfont icon-guanbi'></i>
        </div>
        <div class='entryBtn'>
          <div class='en_cancel' @click='cancel_entry'></div>
          <div class='en_ensure' @click='ensure_entry'></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from "jquery"
  import componentHeader from '@/components/componentHeader'
  export default{
    name:"ChooseGoods",
      data(){
        return{
          headerText:"",
          AddressShow:false,///
          goodsList: [],//
          HnavBrandList:[],//  
          VnavTypeList:[],//   
          HnavBrandIndex:0,//
          VnavTypeIdx:0,//
          totalNum:0, //
          totalPrice:0,//
          isHidden:false,//
          cartShow:0,//
          submitArr:[],
          element:'',//
          inputFocus: false,//
          inputValue: "",//
          ShoppingCartDetailHide:false,//
          ReceiptInfo:{},//
          DeliveryInfo:{},//
          number:{},
          inputNum:0,//idx
          firstReq:false,//
          whoPush:"",//
          orderType:'',//
          isShowSelectStore:false, //  /
          SelectedInfoName:'',// / 
          returnOrtransrerText:"",//
          isShowSelectReceiveAndDeliver:false,//   
          receORdeliv_consignee:'',//  
          receORdeliv_consignor:'',//  
          IFreceORdeliv_transition:false,// osp
          IFSelectReceiveMeng:false,// / 
          ReceiveORDeliverName:'',// /
          ReceiveORDeliverHeaderName:'',// / 
          visitId:0,//id
          CurrentStoresId:0,//ID
          IfTextShow:false,// 
          TextContent:'',// 
          noDataShow:false,//
          // 
          numberPerPage:15,//
          loadPage: 1 ,//
          allLoaded:true,//
          autoFill:false,//
          searchValue:'',//
          promotionName:'',//
        }
      },
      components:{
      componentHeader,
    },
    watch:{

      // input
      searchValue(val, oldVal){
        this.loadPage = 1;
        this.allLoaded = false;
        this.resquestProductList();
      }
    },
    mounted(){

      window.history.pushState(null, null, document.URL)
      window.addEventListener("popstate", this.onBrowserBack, false)

      var LowerBodyHeight = parseFloat($('.fixedTop').css('height')) + parseFloat($('header').css('height')) + 'px';

      $('.LowerBody').css('paddingTop',LowerBodyHeight);
    },
    destroyed() {

      window.removeEventListener("popstate", this.onBrowserBack, false)
    },
    created(){

      var that = this;

      var BackBottleData = this.$store.state.BackBottle;

      // 
      this.submitArr = BackBottleData.g_submitArr;//
      this.ReceiptInfo = BackBottleData.ConsigneeData_selected;//
      this.DeliveryInfo = BackBottleData.DeliveryAddr_selected;//
      this.totalNum = BackBottleData.totalNum;//    

      //
      if(this.$route.query.whoPush){ 
        this.whoPush = this.$route.query.whoPush;
      }

      //
      if(this.$route.query.orderType){
          this.orderType = this.$route.query.orderType;
      }

       // /
      if(this.$route.query.ReceiptInfo){ 

        this.ReceiptInfo = this.$route.query.ReceiptInfo
      }

      // 
      if(this.$route.query.DeliveryInfo){ 

        this.DeliveryInfo = this.$route.query.DeliveryInfo
      }

        // ***********************************************************
      // 
      if(this.whoPush == 'PurchaseOrder' && this.orderType == 'PURCHASE_IN'){
          this.headerText = '';
          var getParentData = {
          "customerId":that.ReceiptInfo.CUSTOMER_ID,
          }
          console.log(getParentData)
        that.httpRequest("getCustomerParent.do",getParentData,function(res){

          that.DeliveryInfo = res.data;
        })

        //  
        if(this.$route.query.CombinedOrder && this.$route.query.CombinedOrder =='CombinedOrder'){
          var CombinedData = {
            "outOrderIds":that.$store.state.SumMergeOrderList,
            'consignee_id':that.ReceiptInfo.CUSTOMER_ID,//osp id
          }
          console.log(CombinedData)
          that.httpRequest("mergeSaleOrder.do",CombinedData,function(res){

            console.log(res)
            that.submitArr = res.data;
            var totalNum = 0;

            for(var tty =0 ;tty < res.data.length;tty++){

              totalNum = parseInt(totalNum) + parseInt(res.data[tty].number);
            }
            
            that.totalNum = totalNum;
          })
        }
      }
      // 
      if(this.whoPush == 'SalesOrder' && this.orderType == 'SALE_OUT'){

        if(that.$store.state.G_AppOrWX == 'WX'){
          this.headerText = '';

        }else{
          this.headerText = '';
        }
      }

      // 
      if((this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){
          
          this.headerText = '';// 
          this.IfTextShow = true;// 

          // id
        if(this.$route.query.visitId){ 

          this.visitId = this.$route.query.visitId;
        }

        if(!BackBottleData.totalNum){

          // ID
          if(this.$route.query.CurrentStoresId){ 

            this.CurrentStoresId = this.$route.query.CurrentStoresId;
          }

          var getStoresData = {
            "cellphone":that.$store.state.userInfo.cellphone,//
            "userId":that.$store.state.userInfo.user_id,//id
            "storesId":that.CurrentStoresId,//id
          }
          console.log(getStoresData)
          // 
          that.httpRequest("GetStoreOne.do",getStoresData,function(storesRes){
            console.log(storesRes)

            that.ReceiptInfo = storesRes.data;//
            that.DeliveryInfo = storesRes.data.suppliers;//
          })
        }
      }

      // 
      if(this.whoPush == 'InventoryManage' && this.orderType == 'OTHER_IN'){

        this.headerText = '';
        // 
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "";
        // 
        if(this.$route.query.CustomerInfo){
          this.DeliveryInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.DeliveryInfo.CUSTOMER_NAME;
      }

      // 
      if(this.whoPush == 'InventoryManage' && this.orderType == 'OTHER_OUT'){

        this.headerText = '';
        // 
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "";
        // 
        if(this.$route.query.CustomerInfo){
          this.ReceiptInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.ReceiptInfo.CUSTOMER_NAME;
      }
      
      // ()
      if(this.whoPush == 'InventoryManage' && this.orderType == 'SALE_RETURN_IN'){
          
        this.headerText = '';

        // 
        this.isShowSelectStore = true;

        this.returnOrtransrerText = "";
        // 
        if(this.$route.query.CustomerInfo){
          this.DeliveryInfo = this.$route.query.CustomerInfo;
        }
        this.SelectedInfoName = this.DeliveryInfo.CUSTOMER_NAME;
      }

      // (  )
      if(this.whoPush == 'OutboundDetail' && this.orderType == 'SALE_RETURN_IN_PART'){
      
        this.headerText = '';
      }
      // 
      if(this.whoPush == 'InventoryManage' && this.orderType == 'TRANSFER'){
          
        this.headerText = '';
        this.isShowSelectReceiveAndDeliver = true;

        // 
        if(that.$route.query.IFreceORdeliv_transition){
          this.IFreceORdeliv_transition = that.$route.query.IFreceORdeliv_transition;
        }else{
          var ProductData = {
            "storeName":'',//
            "cellphone":that.$store.state.userInfo.cellphone,//
            "userId":that.$store.state.userInfo.user_id,//id
            "suppliersId":that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
            "currentPage":1,//
            "numberPerPage":5,//
          }
          console.log(ProductData)
          // 
          that.httpRequest("findUserList.do",ProductData,function(StoresRes){
            console.log(StoresRes)

            if(StoresRes.data.length==1){
                
              that.ReceiptInfo = {
                "CUSTOMER_ID":StoresRes.data[0].APP_USERS_ID,
                "CONTACT_NAME":StoresRes.data[0].USER_NAME,
                "CONTACT_TEL":StoresRes.data[0].CELLPHONE,
                "CUSTOMER_TEL":StoresRes.data[0].CELLPHONE,
              }
              that.receORdeliv_consignee = that.ReceiptInfo.CUSTOMER_NAME?that.ReceiptInfo.CUSTOMER_NAME:that.ReceiptInfo.CONTACT_NAME;//  
            }
          })
        }

        // false osp  true
        if(this.IFreceORdeliv_transition){
          // 
          if(this.$route.query.CustomerInfo){
            this.DeliveryInfo = this.$route.query.CustomerInfo;
          }
        }else{
          // 
          if(this.$route.query.CustomerInfo){
            this.ReceiptInfo = this.$route.query.CustomerInfo;
          }
        }
        this.receORdeliv_consignee = this.ReceiptInfo.CUSTOMER_NAME?this.ReceiptInfo.CUSTOMER_NAME:this.ReceiptInfo.CONTACT_NAME;//  
        this.receORdeliv_consignor = this.DeliveryInfo.CUSTOMER_NAME?this.DeliveryInfo.CUSTOMER_NAME:this.DeliveryInfo.CONTACT_NAME;//  
      }

      var TypeData = {
        "businessId":this.$store.state.businessInfo.businessId,
        "appUsersType":this.$store.state.G_AppOrWX,//app
        "appUsersId": that.$store.state.userInfo.user_id,//id
      }
      console.log(TypeData)
      // 
      this.httpRequest("searchProductType.do",TypeData,function(res){

        that.firstReq = true;//
        that.noDataShow = false;//
        that.HnavBrandList = res.data; // 
        
        that.HnavBrandIndex = 0;
        that.VnavTypeIdx=0;

        if(res.data.length){

        that.VnavTypeList = res.data[0].TYPE;
         
        // 
        that.resquestProductList();

        }else{

        that.noDataShow = true;
        }
      })
    },
    methods:{
      onBrowserBack() {
        
        this.f_Fanhui()
      },
      // 
      f_Fanhui(){
          
        var that = this;

        this.$store.state.BackBottle={
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//
          DeliveryAddr_selected:[],//
        };//
        this.$store.state.OptionsAvailableGift=[];//
        this.$store.state.OptionsGiftChosenCart=[];//
        this.$store.state.OptionsGiftNumberList = {};// numberjson 
        this.$store.state.ORDER_REQUEST_DELIVERY='';//
        this.$store.state.ORDER_REMARK_VALUE='';//

        if(this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT'){
        // 

          // / 
          if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){
            that.$router.go(-1);
          }else{
            that.$router.push({
              name:'CustomerVisit',
            })
          }
          
        }else if(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT'){
        //  
          
          if(that.$route.query.orderConfirmattached && that.$route.query.orderConfirmattached == 'orderConfirm'){

            that.$router.push({
              name:'HistoryVisit',
              query:{
                orderConfirmattached:'orderConfirm'
              }
            })
            
          }else{
            this.$router.go(-1);
          }

        }else if(this.whoPush == 'OutboundDetail' && this.orderType == 'SALE_RETURN_IN_PART'){
          // 
          this.$router.push({
            name:that.whoPush,
            query:{
              transferOutId:that.$route.query.transferOutId,
              whoPush:'ChooseGoods'
            }
          })
        }else if(this.whoPush == 'PurchaseOrder' && this.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
          //  
          this.$router.push({
            name:'QuerySalesOrder'
          })
        }else{
          this.$router.push({
            name:that.whoPush,
          })
        }
      },
      //   
      f_Text(){
        var that = this;
        this.$store.state.BackBottle={
          g_submitArr:[],
          totalNum:0,
          ConsigneeData_selected:[],//
          DeliveryAddr_selected:[],//
        };//
        this.$store.state.OptionsAvailableGift=[];//
        this.$store.state.OptionsGiftChosenCart=[];//
        this.$store.state.OptionsGiftNumberList = {};// numberjson 
        this.$store.state.ORDER_REQUEST_DELIVERY='';//
        this.$store.state.ORDER_REMARK_VALUE='';//

        if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){

          that.$router.go(-1);
        }else{
          this.$router.push({
            name:"VividDisplay",
          })
        }
      },
      //    
      receORdeliv_transition(){

        var DeliveryInfo = this.DeliveryInfo;

        this.DeliveryInfo = this.ReceiptInfo;
        this.ReceiptInfo = DeliveryInfo;

        this.IFreceORdeliv_transition = !this.IFreceORdeliv_transition;
        this.receORdeliv_consignee = this.ReceiptInfo.CUSTOMER_NAME?this.ReceiptInfo.CUSTOMER_NAME:this.ReceiptInfo.CONTACT_NAME;//  
        this.receORdeliv_consignor = this.DeliveryInfo.CUSTOMER_NAME?this.DeliveryInfo.CUSTOMER_NAME:this.DeliveryInfo.CONTACT_NAME;//  

        this.loadPage = 1;
        this.allLoaded = false;
        this.resquestProductList();
      },
      //  
      toSelectSalesMan(type){

        var that = this;
        that.$store.state.BackBottle.g_submitArr = that.submitArr;
        that.$store.state.BackBottle.totalNum = that.totalNum;// 
        that.$store.state.BackBottle.ConsigneeData_selected = that.ReceiptInfo;//
        that.$store.state.BackBottle.DeliveryAddr_selected = that.DeliveryInfo;//

        // false osp  true
        if((!this.IFreceORdeliv_transition && type == 'Deliver') || (this.IFreceORdeliv_transition && type == 'Receive')){

          if(!this.IFreceORdeliv_transition){
            this.ReceiveORDeliverName = this.receORdeliv_consignor;
            this.ReceiveORDeliverHeaderName = '';
          }else{
            this.ReceiveORDeliverName = this.receORdeliv_consignee;
            this.ReceiveORDeliverHeaderName = '';
          }

          this.IFSelectReceiveMeng = true;
          return;
        }

        this.$router.push({
          name:'SelectSalesman',
          query:{
            whoPush:"ChooseGoods",
            whoPush_order:that.whoPush,
            orderType:that.orderType,
            IFreceORdeliv_transition:that.IFreceORdeliv_transition
          }
        })
      },
      //  
      toSelectStores(){

        var that = this;

        that.$store.state.BackBottle.g_submitArr = that.submitArr;
        that.$store.state.BackBottle.totalNum = that.totalNum;// 
        that.$store.state.BackBottle.ConsigneeData_selected = that.ReceiptInfo;//
        that.$store.state.BackBottle.DeliveryAddr_selected = that.DeliveryInfo;//

        // 
        this.$router.push({
          name:'SelectStores',
          query:{
            whoPush:"ChooseGoods",
            whoPush_order:that.whoPush,
            orderType:that.orderType,
          }
        })
      },
      // 
      resquestProductList(load){
        var that = this;
        var transferOutId = '',consignee_id = '';

        // (  )
        if(this.whoPush == 'OutboundDetail' && this.orderType == 'SALE_RETURN_IN_PART'){
          transferOutId = this.$route.query.transferOutId;
        }

        //     consignee_id
        //     consignee_id
        if((this.whoPush == 'PurchaseOrder' && this.orderType == 'PURCHASE_IN')||(this.whoPush == 'SalesOrder' && this.orderType == 'SALE_OUT')){
          consignee_id = this.ReceiptInfo.CUSTOMER_ID;
        }

        //   consignee_id
        if((this.whoPush == 'CheckInventory' && this.orderType == 'SALE_OUT')||(this.whoPush == 'HistoryVisit' && this.orderType == 'SALE_OUT')){
          consignee_id = this.CurrentStoresId;
        }

        var ProductData = {
          "productName":that.searchValue,
          "businessId":that.$store.state.businessInfo.businessId,
          "appUsersId":that.$store.state.userInfo.user_id,//id
          "productBrand":that.HnavBrandList[that.HnavBrandIndex].PRODUCT_BRAND_ID,
          "productType":that.VnavTypeList[that.VnavTypeIdx].PRODUCT_TYPE_ID,
          "transferOutId":transferOutId,//id  
          "currentPage":that.loadPage,//
          "numberPerPage":that.numberPerPage,//
          'consignee_id':consignee_id,//id
          "userType":that.$store.state.G_AppOrWX,//app
        }

        // 
        if(that.whoPush == "InventoryManage" && that.orderType == "TRANSFER"){

          // false osp  true
          if(that.IFreceORdeliv_transition){

            if(that.receORdeliv_consignor){
              ProductData.opeType = 'IN';//IN   OUT

              ProductData.dbUserId = that.DeliveryInfo.CUSTOMER_ID;//userid  IN 
              ProductData.superiorId = that.ReceiptInfo.CUSTOMER_ID;//ospid
            }
          }else{

            ProductData.opeType = 'OUT';//IN   OUT
            ProductData.superiorId = that.DeliveryInfo.CUSTOMER_ID;//ospid
          }
        }
        // 
        else if(that.orderType == "SALE_OUT"){
          ProductData.superiorId = that.DeliveryInfo.CUSTOMER_ID;//ospidid
        }

        // 
        if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
           ProductData.outOrderIds = that.$store.state.SumMergeOrderList;//
           console.log(that.$store.state.SumMergeOrderList)
        }
        console.log(ProductData)
        that.httpRequest("searchProductList.do",ProductData,function(res){
          console.log(res.data)
          
          if(load && load == "BottomLoad"){

            that.$refs.loadmore.onBottomLoaded();
          }

          for(var i = 0;i<res.data.length;i++){

            that.number[res.data[i].PRODUCT_ID] = 0;
          }

          // 
          if (that.loadPage > 1) {

            if(res.data.length){
              that.goodsList = that.goodsList.concat(res.data);
            }
          }
          else {
            that.goodsList = res.data;
          }

          // 
          if (res.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          }
          else {
            that.allLoaded = false;
          }

          var BackBottleData = that.$store.state.BackBottle;

          // 
          if(that.firstReq){

            that.firstReq = false;

            // 
            for(var j = 0; j < that.submitArr.length; j++){

              that.totalPrice += that.submitArr[j].number * that.submitArr[j].DIS_PRICE;
            }

            // 
            for(var i = 0; i < that.goodsList.length; i++){

              for(var j = 0; j < that.submitArr.length; j++){

                if(that.submitArr[j].PRODUCT_ID == that.goodsList[i].PRODUCT_ID){

                  // 
                  that.number[that.goodsList[i].PRODUCT_ID] = that.submitArr[j].number;
                  break;
                }
              }
            }
            that.totalPrice = that.totalPrice.toFixed(2);

          }else{
            // 
            for(var i = 0; i < that.goodsList.length; i++){

              for(var j = 0; j < that.submitArr.length; j++){

                if(that.submitArr[j].PRODUCT_ID == that.goodsList[i].PRODUCT_ID){

                  // 
                  that.number[that.goodsList[i].PRODUCT_ID] = that.submitArr[j].number;
                  break;
                }
              }
            }
          }
        })
      },
      //
      submitClick (){

        event.stopPropagation();

        var that = this;

        if (that.totalNum){

          // 
          if(that.whoPush == "InventoryManage" && that.orderType == "SALE_RETURN_IN"){
            if(!that.SelectedInfoName) {
              that.$alert("", '', {
                confirmButtonText: ''
              })
              return;
            }
          }

           //
          if(that.whoPush == "InventoryManage" && that.$route.query.orderType=='OTHER_IN'){
            
            if(!that.SelectedInfoName) {
              that.$alert("", '', {
                confirmButtonText: ''
              })
              return;
            }
          }

           //
          if(that.whoPush == "InventoryManage" && that.$route.query.orderType=='OTHER_OUT' ){
            
            if(!that.SelectedInfoName) {
              that.$alert("", '', {
                confirmButtonText: ''
              })
              return;
            }
          }

          // 
          if(that.whoPush == "InventoryManage" && that.orderType == "TRANSFER" ){

            if(that.IFreceORdeliv_transition){

              if(!that.receORdeliv_consignor) {
                that.$alert("", '', {
                  confirmButtonText: ''
                })
                return;
              }

            }else{
              if(!that.receORdeliv_consignee) {
                that.$alert("", '', {
                  confirmButtonText: ''
                })
                return;
              }
            }
          }

          that.$store.state.BackBottle.g_submitArr = that.submitArr;
          that.$store.state.BackBottle.totalNum = that.totalNum;// 
          that.$store.state.BackBottle.ConsigneeData_selected = that.ReceiptInfo;//
          that.$store.state.BackBottle.DeliveryAddr_selected = that.DeliveryInfo;//

          var query;
          //   id
          if((that.whoPush == 'CheckInventory' && that.orderType == 'SALE_OUT')||(that.whoPush == 'HistoryVisit' && that.orderType == 'SALE_OUT')){

            if(that.$route.query.VisitModifyData&&that.$route.query.VisitModifyData=='VisitModifyData'){

              query = {
                whoPush:that.whoPush,
                orderType:that.orderType,
                visitId:that.visitId,
                VisitModifyData:'VisitModifyData',
                pageName:that.$route.query.pageName?that.$route.query.pageName:'',
              }
            }else{
              query = {
                whoPush:that.whoPush,
                orderType:that.orderType,
                visitId:that.visitId
              }
            }
            
          }else if(that.whoPush == 'OutboundDetail' && that.orderType == 'SALE_RETURN_IN_PART'){
            query = {
              whoPush:that.whoPush,
              orderType:that.orderType,
              transferOutId:that.$route.query.transferOutId
            }
                
          }else if(that.whoPush == 'InventoryManage' && that.orderType == 'TRANSFER'){
            // 
            query = {
              whoPush:that.whoPush,
              orderType:that.orderType,
              IFreceORdeliv_transition:that.IFreceORdeliv_transition
            }
          }else if(that.whoPush == 'PurchaseOrder' && that.orderType == 'PURCHASE_IN' && that.$route.query.CombinedOrder && that.$route.query.CombinedOrder == 'CombinedOrder'){
            // 
            query = {
              whoPush:that.whoPush,
              orderType:that.orderType,
              CombinedOrder:'CombinedOrder'
            }
          }else{
            query = {
              whoPush:that.whoPush,
              orderType:that.orderType
            }
          }
          //
          that.$router.push({
            name:"OrderConfirm",
            query:query
          })

        }else{

          that.$alert("", '', {
            confirmButtonText: ''
          })
        }
      },
      //
      BrandItemClick(index){

        this.HnavBrandIndex = index;
        this.VnavTypeIdx=0;
        this.VnavTypeList = this.HnavBrandList[index].TYPE;
        this.loadPage = 1;
        this.allLoaded = false;
        this.resquestProductList();
      },
      //
      TypeListClick(index){

        this.VnavTypeIdx = index;
        this.loadPage = 1;
        this.allLoaded = false;
        this.resquestProductList();
      },
      // /
      Address(){
        this.AddressShow=true;
      },
      // / 
      AddressShowHide(){
        this.AddressShow = false;
      },
      //
      animat_cartShowIf(){

        let that = this;

        if($(".footBo_move").is(":animated")){
            return;
        }
        // if(!this.totalNum){ 
        //     return;
        // }

        let footBottom_height= $(".footBo_move").css("height");

        // 
        if( that.ShoppingCartDetailHide){

          $(".footBo_move").animate({"marginTop":0},500);
          $(".OutermostGray").fadeOut(500);
          that.ShoppingCartDetailHide = false;
        }else{

          $(".OutermostGray").fadeIn(500)
          that.ShoppingCartDetailHide = true;
          $(".footBo_move").animate({"marginTop": "-" + footBottom_height},500);
        }    
      },
      //   
      cancel_entry(){

      this.isHidden = false;
      },
      //    
      ensure_entry() {
        var self=this;

        // return
        if (parseInt(self.inputValue).toString() == "NaN" || parseInt(self.inputValue)<0) {

          this.isHidden = false;
           return;
        }

        var number = this.number;
        var submitArr = this.submitArr;

        // (  )
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){

          if(parseInt(self.inputValue)>self.goodsList[self.inputNum].KX_NUM){
              return;
          }
        }

        var isRepeat = false;

        var totalNum = this.totalNum;

        var totalPrice = this.totalPrice;

        if (submitArr.length){

          if (self.element == '') {

            for (var i = 0; i < submitArr.length; i++) {

              if (submitArr[i].PRODUCT_ID == self.goodsList[self.inputNum].PRODUCT_ID) {

                submitArr[i].number = parseInt(self.inputValue);

                isRepeat = true;
              }
            }

            if (!isRepeat && self.inputValue != 0) {

              submitArr[submitArr.length] = self.goodsList[self.inputNum];

              submitArr[submitArr.length - 1].number = parseInt(self.inputValue);
            } 

            // 
            totalNum = parseInt(self.totalNum) - number[self.goodsList[self.inputNum].PRODUCT_ID] + parseInt(self.inputValue);

            //
            totalPrice = parseFloat(self.totalPrice) + (parseInt(self.inputValue) - number[self.goodsList[self.inputNum].PRODUCT_ID]) * self.goodsList[self.inputNum].DIS_PRICE;
            
            //number
            number[self.goodsList[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);

            // 
            if (self.inputValue == 0) {

              for (var j = 0; j < submitArr.length; j++) {

                if (submitArr[j].PRODUCT_ID == self.goodsList[self.inputNum].PRODUCT_ID) {

                  submitArr.splice(j, 1);
                }
              }
            }

          } else if (self.element == '') {

            // 
            totalNum = parseInt(self.totalNum) - submitArr[self.inputNum].number + parseInt(self.inputValue);

            //
            totalPrice = parseFloat(self.totalPrice) + (parseInt(self.inputValue) - parseInt(submitArr[self.inputNum].number)) * submitArr[self.inputNum].DIS_PRICE;

            //number
            number[submitArr[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);

            submitArr[self.inputNum].number = parseInt(self.inputValue);

            // 
            if (self.inputValue == 0) {  

              submitArr.splice(self.inputNum, 1);
            }
          }
      
        }else{

          if (self.inputValue != 0 ){

            submitArr[0] = self.goodsList[self.inputNum];

            submitArr[0].number = parseInt(self.inputValue);

            totalNum = parseInt(self.inputValue);

            totalPrice = totalNum * self.goodsList[self.inputNum].DIS_PRICE;

            number[self.goodsList[self.inputNum].PRODUCT_ID] = parseInt(self.inputValue);
          }
        }

        this.isHidden = false;
        this.number =number;
        this.submitArr = submitArr;
        this.totalNum = totalNum;
        this.totalPrice = totalPrice.toFixed(2);
      },
      //  
      entryShow(e, v){

        event.stopPropagation();

        var self=this;

        this.clearNum();

        if (e.currentTarget.dataset.element==''){

          this.element = '';
        } else if (e.currentTarget.dataset.element == ''){

          this.element = '';
        }

        this.isHidden = true;
        this.inputNum = e.currentTarget.id;//idx

        // 
        this.$nextTick(function(){
          if(v != 0){
            $("#autofocus_input").val(v)
          }
          $("#autofocus_input").focus()
          $("#autofocus_input").select()
        })
      },
      // input
      clearNum(){
        this.inputValue = "";
        // this.$refs['autofocus_input'].focus();
      },
      //   
      jian(e){

        event.stopPropagation();
      
        var self = this;
  
        if (self.number[self.goodsList[e.currentTarget.id].PRODUCT_ID]>0){

          var number = self.number;

          number[self.goodsList[e.currentTarget.id].PRODUCT_ID] = number[self.goodsList[e.currentTarget.id].PRODUCT_ID] - 1;

          var totalPrice = parseFloat(self.totalPrice) - parseFloat(self.goodsList[e.currentTarget.id].DIS_PRICE);

          var submitArr = self.submitArr;

          // 
          for (var i = 0; i < submitArr.length;i++){
    
            if (submitArr[i].PRODUCT_ID == self.goodsList[e.currentTarget.id].PRODUCT_ID){
    
              submitArr[i].number = number[self.goodsList[e.currentTarget.id].PRODUCT_ID];

              // 
              if (number[self.goodsList[e.currentTarget.id].PRODUCT_ID] == 0) {
    
                submitArr.splice(i, 1);
              }
            }
          }

          self.number = number;
          self.totalNum = self.totalNum - 1;
          self.totalPrice =  totalPrice.toFixed(2);
          self.submitArr = submitArr;
        }
      },
      //   
      jia(e){
        event.stopPropagation();
        var self = this,
        number = this.number;

        // (  )
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){
          if((number[self.goodsList[e.currentTarget.id].PRODUCT_ID] + 1) > parseInt(self.goodsList[e.currentTarget.id].KX_NUM)){
            return;
          }
        }

        number[self.goodsList[e.currentTarget.id].PRODUCT_ID] = parseFloat(number[self.goodsList[e.currentTarget.id].PRODUCT_ID])+1;

        var totalPrice = parseFloat(self.totalPrice) + parseFloat(self.goodsList[e.currentTarget.id].DIS_PRICE);

        var submitArr = self.submitArr;

        var isRepeat=false;

        if (submitArr.length==0){

          submitArr[0] = self.goodsList[e.currentTarget.id];
          submitArr[0].number = 1;
         
        }else{
          for (var j = 0; j < submitArr.length; j++) {

            if (submitArr[j].PRODUCT_ID == self.goodsList[e.currentTarget.id].PRODUCT_ID) {

              isRepeat = true;
              submitArr[j].number = number[self.goodsList[e.currentTarget.id].PRODUCT_ID];
            }
          }

          if (!isRepeat){

            submitArr[submitArr.length] = self.goodsList[e.currentTarget.id];

            submitArr[submitArr.length-1].number = 1;
          }
        }

        this.number = number;
        this.submitArr = submitArr;
        this.totalNum = self.totalNum + 1;
        this.totalPrice =  totalPrice.toFixed(2);
      },
      //  
      jian2(e){ 

        event.stopPropagation();

        var self=this;

        var submitArr = this.submitArr;
        var g_number=this.number;

        submitArr[e.currentTarget.id].number--;

        //   
        var totalPrice = parseFloat(self.totalPrice) - parseFloat(self.submitArr[e.currentTarget.id].DIS_PRICE);

        for (var i = 0; i < self.goodsList.length; i++) {

          if (self.goodsList[i].PRODUCT_ID == submitArr[e.currentTarget.id].PRODUCT_ID) {

            g_number[self.goodsList[i].PRODUCT_ID]--;
           
          }
        }

        // 
        if (submitArr[e.currentTarget.id].number == 0) {

          submitArr.splice(e.currentTarget.id, 1);

          self.$nextTick(function() {
            var footBottom_height= $(".footBo_move").css("height");

            $(".footBo_move").animate({"marginTop": "-" + footBottom_height},300);
          })
        } 

        this.number = g_number;
        this.totalNum = self.totalNum - 1;
        this.totalPrice = totalPrice.toFixed(2);
        this.submitArr = submitArr;
      },
      //  
      jia2(e){

        event.stopPropagation();

        var self = this,
        submitArr = this.submitArr,
        g_number = this.number;

        // (  )
        if(self.whoPush == 'OutboundDetail' && self.orderType == 'SALE_RETURN_IN_PART'){

          if((submitArr[e.currentTarget.id].number +1)>submitArr[e.currentTarget.id].KX_NUM){
            return;
          }
        }

        submitArr[e.currentTarget.id].number++;

        // 
        var totalPrice = parseFloat(self.totalPrice) + parseFloat(self.submitArr[e.currentTarget.id].DIS_PRICE);

        for (var i = 0; i < self.goodsList.length; i++) {
          if (self.goodsList[i].PRODUCT_ID == submitArr[e.currentTarget.id].PRODUCT_ID) {

            g_number[self.goodsList[i].PRODUCT_ID]++;
          }
        }

        this.submitArr = submitArr;
        this.number = g_number;
        this.totalNum = self.totalNum + 1;
        this.totalPrice = totalPrice.toFixed(2);
      },
      //  
      showPromotionInfo(idx){
        if($('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').css('display') == 'none'){
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionShow .icon-xiangxiaxialakuang').css('transform','rotate(0deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').stop().show(500);
        }else{
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionShow .icon-xiangxiaxialakuang').css('transform','rotate(-90deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide').stop().hide(500);
        }
      },
    
      // 
      promotionInstruction(idx,PIindex){
        event.stopPropagation();
        if($('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').css('display') == 'none'){
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .PI-nav-d .icon-xiangxiaxialakuang').css('transform','rotate(0deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').stop().show(500);
        }else{
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .PI-nav-d .icon-xiangxiaxialakuang').css('transform','rotate(-90deg)');
          $('.selectGoodsList:eq('+idx+') .promotionInfo .promotionHide>div:eq('+PIindex+') .promotionDeHide').stop().hide(500);
        }
      },
      //  
      SelectReceiveMengHide(){
        this.IFSelectReceiveMeng = false;
      },
      // 
      loadBottom(){

        var that = this;

        if (that.allLoaded){

          that.$refs.loadmore.onBottomLoaded();
          return;
        }else{

          that.loadPage += 1;
        }
        this.resquestProductList("BottomLoad");
      }
    }
  }
</script>
