          height: 100%;
          overflow-y: scroll;
          overflow-x: hidden;
          box-sizing:border-box;
          background-color:#fff;
          padding-bottom: 150/50rem;
          .selectGoodsList{
            border-bottom: 3/50rem solid #f4f4f4;
            .selectGoodsListItem{
              width: 100%;
              box-sizing:border-box;
              font-size: 26/50rem;
              padding: 10/50rem 0;
              display: flex;
              .LeftIcon{
                width:120/50rem;
                position:relative;
                flex-shrink:0;
                img{
                  width: 90/50rem;
                  position:absolute;
                  top:50%;
                  left: 50%;
                  transform: translate(-50%,-50%);
                }
              }
              .listCenter{
                flex-grow: 1;
                .goodsNumame{
                  font-size: 28/50rem;
                  font-weight: 600;
                  line-height: 60/50rem;
                }
                .specifications{
                  color: #777;
                  line-height: 40/50rem;
                }    
                .moneyGroup{
                  overflow: hidden;
                  line-height: 75/50rem;
                  &>div:first-child{
                    float: left;
                    color: red;
                    font-size: 35/50rem;
                  }
                  .InventoryQtyClass{
                    color: #666;
                    font-size: 26/50rem;
                    display: inline-block;
                    margin-left: 20/50rem;
                  }
                }
              }
            }
            .promotionInfo{
              width: 100%;
              line-height: 50/50rem;
              .promotionShow,.promotionDeShow{
                .icon-xiangxiaxialakuang{
                  transform: rotate(-90deg);
                  transition: all 0.5s;
                  color: #666;
                }
              }
              .promotionHide,.promotionDeHide{
                display: none;
                div{
                  .PI-icon-d{
                    float: left;
                    width: 120/50rem;
                  }
                  .PI-name-d{
                    word-wrap: break-word;
                    float: left;
                    width: calc(100% - 3.4rem);
                  }
                  .PI-nav-d{
                    height: 50/50rem;
                    float: right;
                    width: 50/50rem;
                  }
                  overflow: hidden;
                  margin-bottom: 5/50rem;
                  .icon-cuxiao{
                    color: @PublicColor;
                    margin-left: 60/50rem;
                    margin-right: 15/50rem;
                  }
                  .icon-xiangxiaxialakuang{
                    transform: rotate(-90deg);
                    transition: all 0.5s;
                    color: #666;
                    margin-left: 0;
                  }
                }
              }
              .promotionDeHide{
                div{
                  padding-left: 60/50rem;
                }
              }
              &>div{
                overflow: hidden;
                margin-bottom: 10/50rem;
                i{
                  font-size: 45/50rem;
                  float: left;
                  margin-left: 20/50rem;
                }
                span{
                  float: left;
                }
              }
            }
          }
        }
      }
    }
    .noData{
      font-size: 30/50rem;
    }
    .SelectMeng{
      background-color:rgba(0,0,0,0.6);
      font-size: 30/50rem;
      .AddressConfirm{
        width: 710/50rem;
        position:absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #fff;
        padding:40/50rem;
        box-sizing:border-box;
        .AddressConfirmHead{
          span{
            font-weight: 600;
            font-size: 32/50rem;
            padding:20/50rem ;
            color: @PublicColor;
          }
          i{
            font-size: 40/50rem;
            position:absolute;
            transform: translate(-50%,50%);
            top: 0;
            right: 0;
            color: @PublicColor;
          }
        }
        .Info{
          width: 100%;
          font-size: 28/50rem;
          &>div{
            line-height: 50/50rem;
            border-top:3/50rem solid @PublicColor;
            padding-top:20/50rem;
            &>div{
              display: flex;
              span:first-child{
                min-width: 130/50rem;
                flex-shrink:0;
              }
              span:last-child{
                flex-grow: 1;
              }
            }
          }
          .ReceiveInfo{
            padding-bottom: 15/50rem;
          }
        }
      }
      //  / 
      .IFSelectReceiveMeng{
        width: 600/50rem;
        background-color:rgba(255,255,255,1);
        position:absolute;
        padding:50/50rem 15/50rem;
        top: 40%;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius:8/50rem;
        &>div{
          line-height: 70/50rem;
            text-align: center;
          &:first-child{
            font-weight: 600;
            font-size: 32/50rem;
          }
        }
        .icon-guanbi{
          position:absolute;
          top: 0;
          right: 0;
          font-size: 45/50rem;
          transform: translate(-20%,20%);
        }
      }
    }
    // 
    .listLast{
      width:185/50rem;
      line-height:75/50rem; 
      text-align: center;
      margin-right: 20/50rem;
      overflow: hidden;
      float: right;
      &>span{
        font-size: 28/50rem;
        width:80/50rem;
        overflow: hidden;
        float: left;
        text-align: center;
      }
      &>i{
        width: 50/50rem;
        font-size: 50/50rem;
        float: left;
        color: @PublicColor;
      }
    }
  }
</style>


