<template>
  <div class="OutboundDetail">
  	<!--  -->
	  <componentHeader :headerText="$store.state.G_AppOrWX != 'WX'?'':''" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfTextShow='true' :TextContent='TextContent' @f_Text='f_Text'></componentHeader>
	  <div class='putDetails haveHeadcontainer'>
	    <div class='orderInfo'>
	      <div class='infoHead'></div>
	      <div class='infoContainer'>
	        <div>{{$store.state.G_AppOrWX != "WX"?'':''}}<span>{{orderInfo.OUTNO}}</span></div>
          <div><span>{{orderInfo.ADDDATE}}</span></div>
          <div v-if='$store.state.G_AppOrWX != "WX"'><span>{{orderInfo.TYPE_text}}</span></div>
          <div><span>{{orderInfo.STATUS_text}}</span></div>
          <div v-if="orderInfo.REMARK"><span>{{orderInfo.REMARK}}</span></div>
	        <div class="flexDiv">
            <div v-if='orderInfo.transferType=="PURCHASE_IN" || orderInfo.transferType=="SALE_OUT" || $store.state.G_AppOrWX == "WX"'><span> {{orderInfo.PAY_PRICE}}</span></div>
	        	
            <div><span>{{orderInfo.orderQty}}</span></div>
            <div v-if='orderInfo.MJ_PRICE>0'><span>{{orderInfo.MJ_PRICE}}</span></div>
	         <!--  <div><span>{{orderInfo.orderWt}}</span></div>
            <div><span>{{orderInfo.orderVol}}</span></div> -->
	        </div>

          <!-- ,  -->
          <div v-if='$store.state.businessInfo.isEdit == "T"' class="flexDiv">
            <div v-if='orderInfo.transferType=="PURCHASE_IN" || orderInfo.transferType=="SALE_OUT" || $store.state.G_AppOrWX == "WX"'><span> {{orderInfo.TAKE_PRICE}}</span></div>

            <div><span>{{orderInfo.TAKE_NUM}}</span></div>
          </div>
          <!-- ,  -->

          <div v-if='orderInfo.REQUESTED_DELIVERY_DTE'><span>{{orderInfo.REQUESTED_DELIVERY_DTE?orderInfo.REQUESTED_DELIVERY_DTE.substring(0,10):orderInfo.REQUESTED_DELIVERY_DTE}}</span></div>
	      </div>
	    </div>

      <div class='orderInfo'>
        <div class='infoHead'></div>
        <div class='infoContainer customerInfo'>
          <div><div><span></span></div><div><span>{{orderInfo.consignorName}}</span></div></div>
          <div><div><span class="ASD ASD_TWO"></span></div><div><span v-if='orderInfo.consignorContact'>{{orderInfo.consignorContact}}&nbsp;&nbsp;</span><span>{{orderInfo.consignorTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consignorTelephone' @click="callPhone(orderInfo.consignorTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div><div><span></span></div><div><span>{{orderInfo.consignorAddress}}</span></div></div>
        </div>
      </div>

      <div class='orderInfo'>
        <div class='infoHead'></div>
        <div class='infoContainer customerInfo'>
          <div v-if='orderInfo.transferType=="TRANSFER_OUT"'><div><span class="ASD ASD_TWO"></span></div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div v-else><div><span></span></div><div><span>{{orderInfo.consigneeName}}</span></div></div>
          <div v-if='orderInfo.transferType=="TRANSFER_OUT"'><div><span></span></div><div><span>{{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-else><div><span class="ASD ASD_TWO"></span></div><div><span>{{orderInfo.consigneeContact}}&nbsp;&nbsp;&nbsp;</span><span>{{orderInfo.consigneeTelephone}}&nbsp;&nbsp;&nbsp;<i v-if='orderInfo.consigneeTelephone' @click="callPhone(orderInfo.consigneeTelephone)" class="iconfont icon-dianhua-copy"></i></span></div></div>
          <div v-if="orderInfo.consigneeAddress"><div><span></span></div><div><span>{{orderInfo.consigneeAddress}}</span></div></div>
        </div>
      </div>

      <!--  -->
     <!--  <div class='radio' v-if='orderInfo.transferType == "SALE_OUT" && orderInfo.STATUS == "NEW"'>
        <div class='infoHead'></div>
        <div class='infoContainer'>
          <el-radio v-model="OutboundObject" label="osp">{{orderInfo.consignorName}}</el-radio>
          <el-radio v-model="OutboundObject" label="user">{{orderInfo.createdUser}}</el-radio>
        </div>
      </div> -->
      <!--  -->
      <componentCargoInfo :goodsArr='goodsArr' :TRANSFER_TYPE='orderInfo.transferType' titleHead='' @Comp_edit="Comp_edit"></componentCargoInfo>

      <!--  -->
	    <componentCargoInfo :goodsArr='giftsArr' titleHead='' @Comp_edit="Comp_edit"></componentCargoInfo>

      <div v-if='$store.state.G_AppOrWX != "WX" && orderInfo.STATUS == "FINISH"&&orderInfo.transferType == "SALE_OUT" && orderInfo.HAVE_RETURN !="T"' class="returnGoods" @click="returnOnclick"></div>
      <div v-if='$store.state.G_AppOrWX != "WX" && orderInfo.STATUS == "FINISH"&&orderInfo.transferType == "SALE_OUT"&& orderInfo.HAVE_RETURN !="T"' class="returnGoods" @click="PartReturn"></div>
      <div v-if="ifTips" class="msg_tips">
        <div class="tips_content">
          <i class="iconfont icon-chenggong1"></i>
          <p>{{tips_Msg}}</p>
        </div>
      </div>
	  </div>

	  <!--/   -->
	  <div class='entryBtn' v-if='orderInfo.transferType != "TRANSFER_OUT"'>
	    <div v-if='orderInfo.STATUS == "NEW"' @click='cancelOutbound'></div>
      <div v-if='orderInfo.STATUS == "NEW" && $store.state.G_AppOrWX != "WX" &&( $store.state.CurrentRole =="osp" ||($store.state.CurrentRole =="ospSalesman" && $store.state.userInfo.visitType == "")) ' @click='confirmOutbound'></div>
      <div v-if='orderInfo.STATUS == "NEW" && $store.state.G_AppOrWX == "WX"' @click='pay'></div>
      <div v-if='orderInfo.STATUS == "PAID" && $store.state.G_AppOrWX == "WX"' @click='confirmOutbound'></div>
	  </div>

    <!-- /   -->
    <div class='entryBtn' v-if='orderInfo.STATUS == "NEW" && orderInfo.transferType == "TRANSFER_OUT" && $store.state.G_AppOrWX != "WX"'>
      <div @click='cancelTransferIn'></div>
      <div v-if='$store.state.CurrentRole =="osp"' @click='confirmTransferOut'></div>
    </div>

    <!--    -->
    <div class='entryBtn' v-if='orderInfo.STATUS == "CANCEL" && $store.state.G_AppOrWX != "WX"'>
      <div @click='reStore'></div>
    </div>

    <div class="selfconfirm" v-if="selfconfirm_show">
      <div class="selfconfirm_contianer">
        <div class="selfconfirm_head"></div>
        <div class="selfconfirm_content">{{confirmcontent}}</div>
        <div class="selfconfirm_btn">
          <div @click='selfconfirm_cancel()'></div>
          <div @click='selfconfirm_confirm_()'></div>
        </div>
      </div>
    </div>

  </div>
</template>
<script type="text/javascript">
  import componentHeader from '@/components/componentHeader'
  import componentCargoInfo from '@/components/Widgets/componentCargoInfo'
  import $ from 'jquery'
  import html2canvas from 'html2canvas'
  export default{
  	data(){
  	  return{
  	  	TextContent:'',
  	  	orderInfo:{},
        goodsArr: [],//
        giftsArr:[],//
        OutboundObject:'osp',// 
        ifTips:false,
        tips_Msg:"",
        selfconfirm_show:false,//confirm
        confirmcontent:'',//confirm
  	  }
  	},
  	components:{
	    componentHeader,
      componentCargoInfo,
	  },
  	created(){

      this.request()
  	},
  	methods:{
  	  // 
      f_Fanhui(){

        if(this.$route.query.whoPush && (this.$route.query.whoPush == 'ChooseGoods'|| this.$route.query.whoPush == 'orderConfirm')){
          this.$router.push({
            name:'QuerySalesOrder',
          })

          return;
        }
      	this.$router.go(-1);
      },
      request(){

        var that = this;

        if(that.$route.query.transferOutId){

          var LineData = {
            "transferOutId":that.$route.query.transferOutId,//id
          }
          console.log(LineData)

          // 
          that.httpRequest("searchTransferOutDetail.do",LineData,function(SupRes){
            console.log(SupRes)
            if(SupRes.data.length){

              if(SupRes.data[0].orderInfo.STATUS == 'NEW'){

                SupRes.data[0].orderInfo.STATUS_text = '';

              }else if(SupRes.data[0].orderInfo.STATUS == 'FINISH' &&SupRes.data[0].orderInfo.HAVE_RETURN != 'T'){

                SupRes.data[0].orderInfo.STATUS_text = '';

              }else if(SupRes.data[0].orderInfo.STATUS == 'CANCEL'){

                SupRes.data[0].orderInfo.STATUS_text = '';

              }else if(SupRes.data[0].orderInfo.HAVE_RETURN == 'T'){

                SupRes.data[0].orderInfo.STATUS_text = '';
              }else{
                SupRes.data[0].orderInfo.STATUS_text = SupRes.data[0].orderInfo.STATUS;
              }

              // 
              if(SupRes.data[0].orderInfo.transferType == 'SALE_OUT'){

                SupRes.data[0].orderInfo.TYPE_text = '';
              }else if(SupRes.data[0].orderInfo.transferType == 'OTHER_OUT'){

                SupRes.data[0].orderInfo.TYPE_text = '';
              }else if(SupRes.data[0].orderInfo.transferType == "TRANSFER_OUT"){

                SupRes.data[0].orderInfo.TYPE_text = '';
              }else if(SupRes.data[0].orderInfo.transferType == "PURCHASE_RETURN_OUT"){

                SupRes.data[0].orderInfo.TYPE_text = '';
              }else{
                SupRes.data[0].orderInfo.TYPE_text = SupRes.data[0].orderInfo.transferType;
              }

              that.orderInfo = SupRes.data[0].orderInfo;
              
              var goodsArr = [],giftsArr = [];

              for(var i=0;i<SupRes.data[0].detail.length;i++){

               if(SupRes.data[0].detail[i].PRODUCT_TYPE == 'GF'){

                  giftsArr[giftsArr.length]=SupRes.data[0].detail[i];
                }else{
                  goodsArr[goodsArr.length]=SupRes.data[0].detail[i];
                }
              }
              that.goodsArr = goodsArr;
              that.giftsArr = giftsArr;

              // 
              if(that.orderInfo.TAKE_PRICE == 0){
                that.orderInfo.TAKE_PRICE = that.orderInfo.PAY_PRICE
              }
              if(that.orderInfo.TAKE_NUM == 0){
                that.orderInfo.TAKE_NUM = that.orderInfo.orderQty
              }
              for(var i = 0; i < that.goodsArr.length; i++){
                if(that.goodsArr[i].TAKE_PO_NUM == 0){
                  that.goodsArr[i].TAKE_PO_NUM = that.goodsArr[i].pirchaseQuantity
                }
                if(that.goodsArr[i].TAKE_PRICE == 0){
                  that.goodsArr[i].TAKE_PRICE = that.goodsArr[i].payment
                }
              }
              for(var i = 0; i < that.giftsArr.length; i++){
                if(that.giftsArr[i].TAKE_PO_NUM == 0){
                  that.giftsArr[i].TAKE_PO_NUM = that.giftsArr[i].pirchaseQuantity
                }
                if(that.giftsArr[i].TAKE_PRICE == 0){
                  that.giftsArr[i].TAKE_PRICE = that.giftsArr[i].payment
                }
              }
            }else{
              // 
              that.$alert('', '', {
                confirmButtonText: '',
              })
           }
          })
        }
      },
      // 
      Comp_edit(p, type){

        var that = this

        var inputValue = 0
        if(type == ""){
          inputValue = p.TAKE_PO_NUM
        }else if(type == ""){
          inputValue = p.TAKE_PRICE
        }

        this.$prompt('' + type, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^\d+(\.\d+)?$/,
          inputErrorMessage: '',
          inputValue: inputValue,
          inputType: 'number',
          closeOnClickModal: false
        }).then(({ value }) => {

          if(type == ""){
            p.TAKE_PO_NUM = value
          }else if(type == ""){
            p.TAKE_PRICE = value
          }

          var total_qty = 0
          var total_price = 0
          for(var i = 0; i < that.goodsArr.length; i++){
            total_qty += parseInt(that.goodsArr[i].TAKE_PO_NUM)
            total_price +=  that.goodsArr[i].TAKE_PRICE * that.goodsArr[i].TAKE_PO_NUM
          }
          for(var i = 0; i < that.giftsArr.length; i++){
            total_qty += parseInt(that.giftsArr[i].TAKE_PO_NUM)
            total_price +=  that.giftsArr[i].TAKE_PRICE * that.giftsArr[i].TAKE_PO_NUM
          }
          console.log(total_qty)
          console.log(total_price)
          that.orderInfo.TAKE_NUM = total_qty
          that.orderInfo.TAKE_PRICE = total_price

          that.modify_qty_price(p)

        }).catch(() => { })

        // 
        this.$nextTick(function(){

          $(".el-input__inner").select()
        })

      },
      modify_qty_price(p){

        var that = this

        var TransferOutDetails = [{"productId":p.productId, "transferOutDetailId":p.transferOutDetailId, "takeNum":p.TAKE_PO_NUM, "takePrice":p.TAKE_PRICE}]

        var postData = {
          "appUsersId":that.$store.state.userInfo.user_id, //id
          "transferOutId":that.$route.query.transferOutId, //id
          "takePoNumAll":that.orderInfo.TAKE_NUM,
          "takeAmount":that.orderInfo.TAKE_PRICE,
          "TransferOutDetails":TransferOutDetails
        }
        // 
        that.httpRequest("updTranDelivery.do",postData,function(res){

          that.ifTips = true
          that.tips_Msg = ""

          setTimeout(function(){
            that.ifTips = false
            that.request()
          },1000)
        })
      },
      downloadURI(uri, name){
        var link = document.createElement("a")
        link.download = name
        link.href = uri
        link.click()
      },
      setImage(){

        html2canvas(document.body).then(function(canvas) {
          

          //base64  
         var dataUrl = canvas.toDataURL();  
        // console.log(dataUrl);  HBuilder    
         var b = new plus.nativeObj.Bitmap();  

          b.loadBase64Data(dataUrl,function(){  
              console.log("");  
          },function(){  
              console.log("");  
          }); 

          b.save('_www/img1.jpg',{overwrite:true},function(){  
              console.log("");  
          },function(){  
              console.log("");  
          });  

          plus.gallery.save( '_www/img1.jpg', function () {  
            console.log( "" );  
          },function(){  
            console.log( "" );
          })

        });

       

      },
      getBlob (canvas){ //blob
        var data = canvas.toDataURL("image/jpeg", 1);
        this.dataurl=data
        console.log(this.dataurl)
        data = data.split(',')[1];
        data = window.atob(data);
        var ia = new Uint8Array(data.length);
        for (var i = 0; i < data.length; i++) {
          ia[i] = data.charCodeAt(i);
        }
        return new Blob([ia], {
          type: "image/jpeg"
        });
      },
      // 
      f_Text(){

        this.setImage()
        return

        // 
        var p_org = this.goodsArr.concat(this.giftsArr)
        var p_deal = []
        for(var i = 0; i < p_org.length; i++){
          var p = {}
          p.name = p_org[i].productName
          p.qty = p_org[i].TAKE_PO_NUM
          p.uom = p_org[i].UNIT_STR
          p.price = p_org[i].TAKE_PRICE
          p_deal.push(p)
        }

        var post_json = {}

        // 
        post_json.header = ""
        
        // 
        post_json.cone_code = this.orderInfo.consigneeCode
        post_json.cone_tel = this.orderInfo.consigneeTelephone
        post_json.cone_name = this.orderInfo.consigneeName
        post_json.cone_address = this.orderInfo.consigneeAddress

        // 
        post_json.product = p_deal

        // 
        post_json.total_qty = parseFloat(this.orderInfo.TAKE_NUM).toFixed(1);
        post_json.total_price = parseFloat(this.orderInfo.TAKE_PRICE).toFixed(2);
        post_json.cono_tel = this.orderInfo.consignorTelephone
        post_json.cono_name = this.orderInfo.consignorContact
        post_json.order_no = this.orderInfo.OUTNO

        var p_deal_json = JSON.stringify(post_json)
        console.log("" + p_deal_json)

        // 
        try { CallAndroidOrIOS.callAndroid("", p_deal_json) }
        catch(error) { }
        // 
        try { CallAndroidOrIOS("", p_deal_json) }
        catch(error) { }
      },
      reStore(){

        var that = this;

        this.selfconfirm('?',function(){

          var CancelData = {
            "cellphone":that.$store.state.userInfo.cellphone,///
            "transferOutId":that.$route.query.transferOutId,//id
            "type":"reStore", // 
          }
          // 
          that.httpRequest("saveCancelTransferOut.do",CancelData,function(CancelRes){
            console.log(CancelRes)

            that.ifTips = true;
            that.tips_Msg = "";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      pay(){

        var that = this;
        
        var mch_create_ip = returnCitySN["cip"];
        var postData = {
          "cellphone":this.$store.state.userInfo.cellphone, //
          "orderId":this.orderInfo.TRANSFER_ID,             //id
          "total_fee":this.orderInfo.PAY_PRICE,             //
          "mch_create_ip":mch_create_ip,
          "sub_openid":this.$store.state.GopenId,
        }
        this.httpRequest("wxPay.do",postData,function(res){
          
          var pay_info = JSON.parse(res.data.pay_info)
          function onBridgeReady(){
            WeixinJSBridge.invoke(
              'getBrandWCPayRequest', {
                "appId":pay_info.appId,     //XXXpay_infoappId    
                "timeStamp":pay_info.timeStamp, //XXXpay_infotimeStamp     
                "nonceStr":pay_info.nonceStr,  //XXXpay_infononceStr   
                "package":pay_info.package,   //XXXpay_infopackage  
                "signType":pay_info.signType,  //XXXpay_infosignType
                "paySign":pay_info.paySign    //XXXpay_infopaySign
              },
            function(res){
              if(res.err_msg == "get_brand_wcpay_request:ok"){
                alert("")
                // ,
                // res.err_msgok
                var params = {
                  "appUsersId":that.$store.state.userInfo.user_id,//id
                  "transferOutId":that.$route.query.transferOutId,//id
                }
                that.httpRequest("updTranState.do",params,function(res){

                  that.$router.push({
                    name:'QuerySalesOrder',
                  })
                })
              }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                alert("")
              }else if(res.err_msg == "get_brand_wcpay_request:fail"){
                alert("")
              }
            });
          }

          if(typeof WeixinJSBridge == "undefined"){
            if( document.addEventListener ){
              document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
            }else if (document.attachEvent){
              document.attachEvent('WeixinJSBridgeReady', onBridgeReady); 
              document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
            }
          }else{
            onBridgeReady();
          }
        })
      },
      // confirm
      selfconfirm_cancel (){
        this.selfconfirm_show = false;
      },
      // confirm
      selfconfirm_confirm(){

      },
      // confirm
      selfconfirm_confirm_ (){

        this.selfconfirm_show = false;
        this.selfconfirm_confirm();
      },
      selfconfirm(text,selfconfirm_confirm){
        this.selfconfirm_show = true;
        this.confirmcontent = text;
        this.selfconfirm_confirm = selfconfirm_confirm;
      },
      //
      returnOnclick(){

        var that = this;

        this.selfconfirm('?',function(){
          var postData = {
            "transferOutId":that.$route.query.transferOutId,//id
            "businessId":that.$store.state.businessInfo.businessId,//ID
            "userId":that.$store.state.userInfo.user_id,//id
            "cellphone":that.$store.state.userInfo.cellphone,///
          }

          // 
          that.httpRequest("SalesReturnAll.do",postData,function(res){

            that.ifTips = true;
            that.tips_Msg = res.Msg;

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      // 
      PartReturn(){
        var that = this;
        // 
        var ReceiptInfo = {
          CUSTOMER_NAME:that.orderInfo.consignorName,
          CONTACT_NAME:that.orderInfo.consignorContact,
          CONTACT_TEL:that.orderInfo.consignorTelephone,
          CUSTOMER_ADDRESS:that.orderInfo.consignorAddress,
        }

        // 
        var DeliveryInfo = {
          CUSTOMER_NAME:that.orderInfo.consigneeName,
          CONTACT_NAME:that.orderInfo.consigneeContact,
          CUSTOMER_TEL:that.orderInfo.consigneeTelephone,
          CUSTOMER_ADDRESS:that.orderInfo.consigneeAddress,
        }
        this.$router.push({
          name:"ChooseGoods",
          query:{
            ReceiptInfo:ReceiptInfo,
            DeliveryInfo:DeliveryInfo,
            whoPush:"OutboundDetail",
            orderType:"SALE_RETURN_IN_PART",
            transferOutId:that.orderInfo.TRANSFER_ID,//id
          }
        })
      },
  	  //
  	  confirmOutbound () {
  	    var that = this;

        if(this.orderInfo.transferType == 'SALE_OUT'){
          // var objectName;

          // if(that.OutboundObject == 'user'){
            // objectName = that.orderInfo.createdUser;
          // }else{
          //   objectName = that.orderInfo.consignorName;
          // }

          this.selfconfirm('?',function(){
          // this.selfconfirm(' '+objectName+' ?',function(){

            var ConfirmData = {
              "transferOutId":that.$route.query.transferOutId,//id
              // "types":that.OutboundObject,//  type
              "types":'user',//  type
            }
            console.log(ConfirmData)

            // 
            that.httpRequest("saveConfirmTransferOut.do",ConfirmData,function(ConfirmRes){
              console.log(ConfirmRes)

              that.ifTips = true;
              that.tips_Msg = "";

              setTimeout(function(){
                that.$router.go(-1);
              },2000)
            })
          })
        }else{

          var msg = "?"
          if(this.$store.state.G_AppOrWX == 'WX'){
            msg = ""

          }
          this.selfconfirm(msg,function(){

            var ConfirmData = {
              "transferOutId":that.$route.query.transferOutId,//id
              "types":'osp',//  type
            }
            console.log(ConfirmData)

            // 
            that.httpRequest("saveConfirmTransferOut.do",ConfirmData,function(ConfirmRes){
              console.log(ConfirmRes)

              that.ifTips = true;
              that.tips_Msg = "";

              setTimeout(function(){
                that.$router.go(-1);
              },2000)
            })
          })
        }
  	  },
  	  //
  	  cancelOutbound (){
  	    var that = this;

        this.selfconfirm('?',function(){

          var CancelData = {
            "cellphone":that.$store.state.userInfo.cellphone,///
            "transferOutId":that.$route.query.transferOutId,//id
          }
          console.log(CancelData)

          // 
          that.httpRequest("saveCancelTransferOut.do",CancelData,function(CancelRes){
            console.log(CancelRes)

            that.ifTips = true;
            that.tips_Msg = "";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
  	  },
      // 
      confirmTransferOut(){

        var that = this;
        this.selfconfirm('?',function(){
          var ConfirmData = {
            "cellphone":that.$store.state.userInfo.cellphone,///
            "transferId":that.$route.query.transferOutId, //id
            "appUsersId":that.$store.state.userInfo.user_id, //id
            "type":'OUT', // 
          }
          that.httpRequest("saveTransferConfirm.do",ConfirmData,function(res){

            that.ifTips = true;
            that.tips_Msg = "";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
      // 
      cancelTransferIn() {

        var that = this;
        this.selfconfirm('?',function(){
          var ConfirmData = {
            "transferId":that.$route.query.transferOutId, //id
            "appUsersId":that.$store.state.userInfo.user_id, //id
            "type":"OUT", // 
          }

          // 
          that.httpRequest("saveTransferCancel.do",ConfirmData,function(ConfirmRes){

            that.ifTips = true;
            that.tips_Msg = "";

            setTimeout(function(){
              that.$router.go(-1);
            },2000)
          })
        })
      },
  	}
  }
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .OutboundDetail{
    width:100%;
    height:100%;
    padding:16/50rem;
    box-sizing: border-box;
    background-color: @GrayBackground;
    overflow-x: hidden;
    padding-bottom: 100/50rem;
    // .radio{
    //   .infoContainer{
    //     font-size: 33/50rem;
    //     padding:15/50rem 15/50rem;
    //     border-radius:15/50rem;
    //     box-sizing: border-box;
    //     background-color:#fff;
    //     margin-bottom: 20/50rem;
    //     &>div>div{
    //       float: left;
    //       width:344/50rem;
    //     }
    //   }
    //   .infoContainer>div{
    //     margin-bottom: 10/50rem;
    //     overflow: hidden;
    //   }
    // }
  }
  /*   */
  .entryBtn{
    font-size: 26/50rem;
    width:100%;
    position: fixed;
    bottom: 30/50rem;
    left: 0;
    text-align: center;
    overflow: hidden;
    display: flex;
    padding:0 20/50rem;
    box-sizing:border-box;
    text-align: center;
    &>div{
      width: 50%;
      margin: 0 auto;
      height: 68/50rem;
      background-color: @PublicColor;
      color: #fff;
      float: left;
      line-height: 68/50rem;
      text-align: center;
      border-radius:34/50rem;
      &:nth-child(2){
        margin-left: 20/50rem;
      }
    }
  }
  .returnGoods{
    float: right;
    margin-right: 10/50rem;
    margin-bottom: 30/50rem;
    padding-right: 35/50rem;
    padding-left: 35/50rem;
    padding-top: 10/50rem;
    padding-bottom: 10/50rem;
    border-radius: 10/50rem;
    color: #fff;
    font-size: 26/50rem;
    background-color: #999; 
  }
  .orderInfo{
    .infoContainer>div.flexDiv{
      // display: flex;
      // &>div{
      //   flex-grow: 1;
      // }
    }
    span{
      color: #666;
    }
    span.ASD{
      letter-spacing: 59.8/50rem;
      color: #000;
      margin-left: 0;
    }
    span.ASD_TWO{
      letter-spacing: 15/50rem;
    }
    .customerInfo>div{
      display: flex;
      &>div{
        float:none;
        width: auto; 
      }
      &>div:first-child{
        min-width: 130/50rem;
        flex-shrink:0;
        span{
          color: #000;
        }
      }
      &>div:nth-child(2){
        flex-grow: 1;
      }
      span{
        margin:0;
      }
    }
  }
</style>


