<template>
  <div class="InventoryRegister">
	 <componentHeader :headerText="headerText" :IfFanhuiShow='true' @f_Fanhui='f_Fanhui' :IfAddShow='true' @f_Add='f_Add'></componentHeader>

  	<div class='container haveHeadcontainer'>
      <div class="headFixed">
        <div class="CustomerSearch">
          <div>
            <input  v-model='searchValue'  placeholder="">
            <i class="iconfont icon-sousuo"></i>
          </div>
          <div class="MonthDisplay" @click="SelectMonthShow">{{MonthDisplay}}</div>
        </div>
        <div class="tabHead">
          <div><span class="tabHead_active" @click="SwitchBtnStore"></span></div>
          <div><span @click="SwitchBtnCustomer"></span></div>
        </div>
      </div>

      <div class="ListBox">
        <mt-loadmore  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >
          <div  class='inventoryList' @click='inventoryDetail(index)' v-for='(itemName,index) in listData'  :id="index"  :key='index'>
            <div><span></span><span>{{itemName.NAME}}</span></div>
            <div><span></span><span>{{itemName.CODE}}</span></div>
            <div><span></span><span>{{itemName.INVENTORY_TIME}}</span></div>
            <div><span></span><span>{{itemName.ADDDATE}}</span></div>
          </div>
        </mt-loadmore>
        <div class="kong"></div>
      </div>
      
      <div class="noData" v-if="noDataShow">
        <i class="iconfont icon-meiyoushuju-copy"></i>
        <span></span>
      </div>
  	</div>
    <div class="BtnComponent bottomBtn">
      <div @click="toSelectStore"></div>
    </div>
    <div class="SelectMeng" v-if="selectMonthHide">
      <div class="SelectWarehouses SelectMengContent">
        <div class="SelectTitle SelectMengTitle"></div>
        <div class="SelectItem">
          <div>
            <span></span>
            <el-date-picker v-model="StartMonth" type="month" :editable='false' placeholder="" format="yyyy  MM " value-format="yyyy-MM"></el-date-picker>
          </div>
          <div>
            <span></span>
            <el-date-picker v-model="EndMonth" type="month" :editable='false' placeholder="" format="yyyy  MM " value-format="yyyy-MM"></el-date-picker>
          </div>
        </div>
        <div class="selectBtn">
          <div @click="selectMonthCancel"></div>
          <div @click="selectMonthConfirm"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  import $ from 'jquery'
  import componentHeader from '@/components/componentHeader'
  export default{
    data(){
      return{
      	headerText:"",
      	listData:[],//
        MonthDisplay:"",//
        selectMonthHide:false,// 
        StartMonth:'',// 
        EndMonth:'',// 
        searchValue:'',//
        ObjectType:'store',//
        noDataShow:false,//
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date(new Date().getFullYear(), 0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setMonth(start.getMonth() - 6);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        numberPerPage:15,//
        loadPage: 1 ,//
        allLoaded:true,//
        autoFill:false,//
      }
    },
    components:{
	    componentHeader,
	  },
    created(){

      this.SwitchBtnStore();
    },
    watch:{

      // input
      searchValue(val, oldVal){
        var that = this;
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects(that.ObjectType);
      }
    },
    methods:{
      // 
      f_Fanhui(){

        this.$router.push({
          name:"InventoryManage",
        })
      },
      // /
      SwitchRegObjects(ObjectType,load){

        var that = this;

        this.ObjectType = ObjectType;

        if(ObjectType == 'store'){
          $('.tabHead>div:eq(0)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(1)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','block');

        }else if(ObjectType == 'customer'){
          $('.tabHead>div:eq(1)>span').addClass('tabHead_active');
          $('.tabHead>div:eq(0)>span').removeClass('tabHead_active');
          $('.BtnComponent').css('display','none');
        }
        
        var histData = {
          "strCustomerName":that.searchValue,//
          "strRegStartTime":that.StartMonth,//
          "strRegEndTime":that.EndMonth,//
          "currentPage":that.loadPage,//
          "numberPerPage":that.numberPerPage,//
          "types":ObjectType,///
          'ospId':that.$store.state.InventoryOspInfo.CUSTOMER_ID,//ospID
          'businessId':that.$store.state.businessInfo.businessId,//ID
          "orgnizationId":'',//ID
          "userId":that.$store.state.userInfo.user_id,//id
          "cellphone":that.$store.state.userInfo.cellphone,//
        }

        console.log(histData)
        // 
        that.httpRequest("GetRegisterList.do",histData,function(histRes){
          console.log(histRes)

          if(load){
            if(load == "TopLoad"){

              that.$refs.loadmore.onTopLoaded();

            }else if(load == "BottomLoad"){

              that.$refs.loadmore.onBottomLoaded();
            }
          }
          // 
          if (that.loadPage > 1) {
            if(histRes.data.length){
              that.listData = that.listData.concat(histRes.data);
            }
          } else {
            that.listData = histRes.data;
          }

          // 
          if (histRes.data.length < that.numberPerPage) {
            that.allLoaded = true;
    
          } else {
            that.allLoaded = false;
          }
          
          if(that.listData.length){
            that.noDataShow = false;
            
          }else{
            // 
            that.noDataShow = true;
          }
        })
      },
      //  
      SwitchBtnStore(){
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('store');
      },
      //  
      SwitchBtnCustomer(){
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects('customer');
      },
      //   
      toSelectStore(){

        this.$router.push({
          name:"SelectStores",
          query:{
            whoPush : 'InventoryRegister'
          }
        })
      },
      //     
      f_Add(){

        this.$router.push({
          name:"AddInventory",
          query:{
            inventoryReg : 'inventoryReg'
          }
        })
      },
      //  
      SelectMonthShow(){
        this.selectMonthHide = true;
      },
      //   
      selectMonthConfirm(){

        var that = this;

        // 
        if(!this.StartMonth && !this.EndMonth){

          this.EndMonth = '';
          this.StartMonth = '';
          this.MonthDisplay = '';
          this.selectMonthHide = false;
          $('.MonthDisplay').css({'color':'#666','border-color':'#666'})
          return;
        }

            // color: @PublicColor;
            // border:1/50rem solid @PublicColor;
        //   
        if(this.StartMonth && !this.EndMonth){
          this.EndMonth = this.StartMonth;
        }
        if(!this.StartMonth && this.EndMonth){
          this.StartMonth = this.EndMonth;
        }

        let newArr = [this.StartMonth,this.EndMonth].sort(function sequence(a,b){
          return  Date.parse(a) - Date.parse(b);
        })

        this.StartMonth = newArr[0];
        this.EndMonth = newArr[1];

        this.MonthDisplay = this.StartMonth + '' + this.EndMonth;

        //  
        if(this.StartMonth == this.EndMonth){

          this.MonthDisplay = this.StartMonth;
        }

        $('.MonthDisplay').css({'color':'#1e8cf8','border-color':'#1e8cf8'})
        this.selectMonthHide = false;
        this.loadPage = 1;
        this.allLoaded = false;
        this.SwitchRegObjects(that.ObjectType);
      },
      //   
      selectMonthCancel(){
        this.selectMonthHide = false;
      },
      // 
      inventoryDetail (idx){

        var that = this;
        this.$router.push({
          name:"InventoryRegDetail",
          query:{
            inventoryRegisterId : that.listData[idx].INVENTORY_REGISTER_ID,
            ObjectType:that.ObjectType
          }
        })
      },
      // 
      loadTop() {

        var that = this;
        this.loadPage = 1;

        this.allLoaded = false;

        this.SwitchRegObjects(that.ObjectType,"TopLoad");
      },
      // 
      loadBottom(){

        var that = this;

        if (that.allLoaded){
          that.$refs.loadmore.onBottomLoaded();
            return;
        }else{
         
            that.loadPage += 1;
        }

        this.SwitchRegObjects(that.ObjectType,"BottomLoad");
      }
    }
  }
</script>
<style lang="less" scoped>
  @import "../../../components/publicLess.less";
  .InventoryRegister{
    overflow-x: hidden;
    height: 100%;
    .container{
      height: 100%;
      margin-top: 0;
      .headFixed{
        width: 100%;
        background-color:#fff;
        z-index: 50;
        position:fixed;
        top: 87/50rem;
        left: 0;
        .CustomerSearch{
          .TopSearchBox;
          display: flex;
          &>div{
            flex-grow: 1;
          }
          &>div:last-child{
            font-size: 25/50rem;
            text-align: center;
            margin-left: 20/50rem;
            line-height: 60/50rem;
            // background-color:#999;
            padding:0 5/50rem;
            color: #666;
            border:1/50rem solid #666;
            border-radius: 30/50rem;
          }
        }
        .tabHead{
          background-color: #fff;
          height: 70/50rem;
          line-height: 70/50rem;
          border-bottom:1/50rem solid @PublicBorderColor;
          &>div{
            // width: 50%;
            padding:0 60/50rem;
            text-align: center;
            float: left;
            span.tabHead_active{
              padding-bottom: 10/50rem;
              color: @PublicColor;
              border-bottom:3/50rem solid @PublicColor;
            }
          }
        }
      }
      .ListBox{
        overflow: scroll;
        padding-top: 263/50rem;
        height: 100%;
        box-sizing:border-box;
      }
    }
  }
  .iconfont {
    font-size:37/50rem;
    color: @PublicColor;
  }
  .inventoryList{
    width: 100%;
    padding: 10/50rem 25/50rem;
    box-sizing: border-box;
    font-size: 28/50rem;
    border-bottom: 20/50rem solid #EFEFF2;
  }
  .inventoryList div{
    line-height: 60/50rem;  
  }
  .BtnComponent{
    padding:0 50/50rem;
  }
  .SelectMeng{
    .SelectMengContent{
      .SelectItem{
        padding:20/50rem 0;
        color: #000;
        &>div{
          border-top:none;
          display: flex;
          &>span{
            width: 150/50rem;
          }
          &>div{
            flex-grow: 1;
            margin-right: 20/50rem;
          }
        }
      }
    }
  }
  .kong{
    // height: 300/50rem;
  }
</style>


